import Vue from 'vue'
import Router from 'vue-router'
// import HelloWorld from '@/components/HelloWorld'
import err404 from '../page/four.vue'
import err403 from '../page/three.vue'
import err500 from '../page/five.vue'
import application from '../page/application/index.vue'
// import safety from '../page/safety/index.vue'
// import owner from '../page/ownerMessage/index.vue'
import owner from '../page/ownerMessage/index.vue'
import message from '../page/companyMessage/index.vue'
import app from '../page/app/index.vue'
import index from '../page/index.vue'
import orderingInfor from '../page/orderingInfor/index.vue'
// import receiver from '../page/receiver.vue'
import receiver from '../page/receiver/receiver.vue'
import agentReceiver from '../page/agentReceiver/agentReceiver.vue'

// import shippingAddress from '../page/shippingAddress.vue'
import authentication from '../page/authentication/index.vue'
// import enterprise from '../page/authentication/enterprise.vue'
import approvalCenter from '../page/approvalCenter/settleApproval.vue'     //入驻审批...........................
import shippingAddress from '../page/shippingAddress/index.vue'
// import orderingInfor from '../page/orderingInfor/index.vue' /
import remoteController from '../page/remoteController/index.vue'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path:'/orderingInfor',
      name:'申请1',
      component:orderingInfor
    },
    {
      path:'/receiver',
      name:'设备分布',
      component:receiver
    },
    // {
    //   path:'/shippingAddress',
    //   name:'申请3',
    //   component:shippingAddress
    // },
    // {
    //   path:'/index',
    //   name:'申请',
    //   component:index
    // },
    {
      path:'/authentication',
      name:'认证中心',
      component:authentication
    },
    {
      path:'/application',
      name:'申请中心',
      component:application
    },
    {                    //  入驻审批..........................
      path:'/approvalCenter',
      name:'入驻审批',
      component:approvalCenter
    },
    {                    //  入驻审批..........................
      path:'/agentReceiver',
      name:'设备分布',
      component:agentReceiver
    },    
    {
      path:'/app',
      name:'我的应用',
      component:app
    },
    {
      path:'/message',
      name:'企业信息',
      component:message
    },
    {
      path:'/owner',
      alias:'/index',
      name:'个人信息',
      component:owner
    },
    {
      path:'/shippingAddress',
      name:'远程运维',
      component:shippingAddress
    },
    {
      path:'/remoteController',
      name:'远程控制',
      component:remoteController
    },
    // {
    //   path:'/safety',
    //   name:'安全设置',
    //   component:safety
    // },
    {
      path: '/404',
      name: '404',
      component: err404
    },
    {
      path: '/403',
      name: '403',
      component: err403
    },
    {
      path: '/500',
      name: '500',
      component: err500
    },
    {
      path:'/',
      redirect:'/owner'
    }
  ]
})



