<template>
    <div class="companyMessage">
        <h2>企业信息</h2>
        <el-form :model="formData"  ref="ruleForm" label-width="200px" class="demo-ruleForm">
            <el-form-item label="企业名称" >
                <span>{{formData.name}}</span>
            </el-form-item>
            <el-form-item label="所在地区" >
                <span>
                    {{formData.province}} {{formData.city}} {{formData.district}}
                     <!-- <area-select :level="2" type="text" v-model="formData.adress" :data="pcaa" ></area-select> -->
                </span>
            </el-form-item>
            <el-form-item label="详细地址" >
                <span>{{formData.detailedAddress}}</span>
            </el-form-item>
            <el-form-item label="企业电话" >
                <span>{{formData.contactPhone}}</span>
            </el-form-item>
            <el-form-item label="企业法人姓名" >
                <span>{{formData.legalPerson}}</span>
            </el-form-item>
        </el-form>
        <h2 style="margin-top:30px;">账号安全</h2>
        <safety ></safety>
    </div>
</template>
<script>
import safety from '../safety/index.vue'
import {mapState} from 'vuex'
export default {
    data(){
        return{
            formData:{
                name:'',
                province:'',
                city:'',
                district:'',
                detailedAddress:'',
                contactPhone:'',
                legalPerson:''
            }
        }
    },
    computed:{
        ...mapState(['ports','userId'])
    },
    mounted(){
        this.$axios.post_noqs(
            this.ports + '/enterpriseauth/page',
            {userId:this.userId},
            this
        ).then(res=>{
            if(res.success){
                this.formData=res.data
                // this.formData2=res.data
            }
        })
    },
    components: {
        safety
    }
}
</script>
<style lang="less">
.companyMessage{
    margin-top: 30px;
    width: 100%;
    .el-form-item__label{
        padding: 0 40px 0 0;
    }
    .demo-ruleForm{
        .el-form-item{
            margin-bottom:0;
        }
    }
    
    h2{
        border-bottom: 2px solid #E5E5E5;
        font-size:16px;
        font-family:MicrosoftYaHei-Bold;
        height: 32px;
        line-height: 32px;
        padding-left: 10px;
        margin-bottom: 40px;
    }
    // .area-select{
    //     line-height: 32px;
    // }
    // .area-selected-trigger{
    //     display: inline;
    // }
}
  
</style>



