<template>
   <div class="settleApproval">
        <div class="tab-title">
            <table></table>
            <p class="title-text">入驻审批</p>
        </div>
        <div class="content">
             <cen-search
                :data="searchData.data"
                :config="searchData.config"
                @search="getPage(tableConfig.pageNo=1)">
            </cen-search>
            <cen-table
                :tableData="tableData"
                :tableConfig="tableConfig"
                @buttonFunction="showDialog"
                @showBatchDialog="showBatchDialog"
                @sizeChange="sizeChange"
                @pageChange="pageChange">
                <time-search @timeClick="timeClick"></time-search>
            </cen-table>
        </div>
        <!-- 审批弹出框 -->
        <cen-toast :config="approvalData"
                @cancel="cancel"
                @sure="sure">
            <div class="toast-">
                <el-form :model="approvalData.form" :rules="rules" ref="form">
                    <el-form-item>
                        <el-button :class="{'approval-btn':true,'active':passActive}" @click="passActive=true;refuseActive=false"><span class="circle"></span>审批通过</el-button>
                        <el-button :class="{'approval-btn':true,'active':refuseActive}" @click="refuseActive=true;passActive=false"><span class="circle"></span>审批驳回</el-button>
                    </el-form-item>
                    <el-form-item prop="opinion">
                        <el-input type="textarea" placeholder="驳回原因输入" v-model="approvalData.form.opinion"
                            :disabled="passActive"
                            @blur="approvalData.form.opinion = approvalData.form.opinion.replace(/\s+/gi,'')"></el-input>
                    </el-form-item>
                </el-form>
            </div>
        </cen-toast>
        <!-- 审批弹出框 -->


        <!-- 查看弹出框 -->
        <cen-toast :config="lookDetailData"
                @close="close"
                @sure="sure">
            <div class="toast-content">
                <el-form :model="lookDetailData.perForm" :rules="rules" ref="form" label-width="80px">
                    <el-form-item label="企业名称">
                        <el-input size="mini" v-model="lookDetailData.perForm.companyName" disabled></el-input>
                    </el-form-item>
                    <el-form-item label="法人代表">
                        <el-input size="mini" v-model="lookDetailData.perForm.legalPerson" disabled></el-input>
                    </el-form-item>
                    <el-form-item label="企业性质">
                        <el-select size="mini" v-model="lookDetailData.perForm.propertyDesc" disabled></el-select>
                    </el-form-item>
                    <el-form-item label="所属行业">
                        <el-select size="mini" v-model="lookDetailData.perForm.typeDesc" disabled></el-select>
                    </el-form-item>
                    <el-form-item label="企业规模">
                        <el-select size="mini" v-model="lookDetailData.perForm.enterpriseScaleDesc" disabled></el-select>
                    </el-form-item>
                    <el-form-item label="设备规模">
                      <el-input size="mini" v-model="lookDetailData.perForm.zhengjingjiNum" disabled style="width: 173px"></el-input>
                      <el-input size="mini" v-model="lookDetailData.perForm.jingbianjiNum" disabled style="width: 173px"></el-input>
                      <el-input size="mini" v-model="lookDetailData.perForm.qitaNum" disabled style="width: 173px"></el-input>
                    </el-form-item>
                    <el-form-item label="企业场所">
                        <img :src="lookDetailData.perForm.imageUrl" alt="">
                    </el-form-item>
                </el-form>
            </div>
        </cen-toast>
        <!-- 查看弹出框 -->
   </div>
</template>
<script>
import rule from '../../assets/js/rule.js'
import {mapState} from 'vuex'
    export default {
         data() {
            return {
            passActive: true,
            refuseActive: false,
            isBatch: false,
            checktype: null,
            /**搜索配置数据 */
            searchData: {
                data: {},
                config: [
                [
                    { label: "省份", field: "inProvince", type: "input" },
                    { label: "所在市", field: "inCity", type: "input" },
                    // { label: "用户性质", field: "userType", type: "input" },
                    { label: "企业性质", field: "companyNature", type: "input" },
                    {
                    label: "人员规模",
                    field: "companyModel",
                    type: "select",
                    childrens: [{name:''}]
                    },
                    {
                    label: "整经机规模",
                    field: "zhengjingjiNum",
                    type: "input"
                    },
                    {
                    label: "经编机规模",
                    field: "jingbianjiNum",
                    type: "input"
                    },
                    {
                    label: "其他设备规模",
                    field: "qitaNum",
                    type: "input"
                    },
                    {
                    label: "审批状态",
                    field: "authType",
                    type: "select",
                    childrens: [
                        { name: "审批通过", value: "2" },
                        { name: "审批中", value: "1" },
                        { name: "驳回", value: "3" }
                    ]
                    }
                ]
                ]
            },
            /**表格数据 */
            tableData: [],
            tableConfig: {
                toolbarConfig: [
                {
                    icon: "tickets",
                    disabled: true,
                    name: "批量审批",
                    method: "showBatchDialog"
                }
                ],
                height: 400,
                isHigh: false,
                isSelection: true,
                align: "center",
                isPage: true,
                pageNo: 1,
                pageSize: 20,
                total: 0,
                isOrder: true,
                currentSelectArr: [],
                currentClickRow: {},
                colConfig: [
                {
                    fixed: false,
                    field: "inProvince",
                    width: "",
                    isSort: false,
                    isResize: true,
                    label: "省份",
                    align: "center",
                    type: "text"
                },
                {
                    fixed: false,
                    field: "inCity",
                    width: "",
                    isSort: false,
                    isResize: true,
                    label: "所在市",
                    align: "center",
                    type: "text"
                },
                // {
                //   fixed: false,
                //   field: "userType",
                //   width: "",
                //   isSort: false,
                //   isResize: true,
                //   label: "用户性质",
                //   align: "center",
                //   type: "text"
                // },
                {
                    fixed: false,
                    field: "companyName",
                    width: "",
                    minWidth: "100px",
                    isSort: false,
                    isResize: true,
                    label: "用户名",
                    align: "center",
                    type: "text"
                },
                {
                    fixed: false,
                    field: "propertyDesc",
                    width: "",
                    minWidth: "100px",
                    isSort: false,
                    isResize: true,
                    label: "企业性质",
                    align: "center",
                    type: "text"
                },
                // {
                //   fixed: false,
                //   field: "yearSale",
                //   width: "",
                //   minWidth: "100px",
                //   isSort: false,
                //   isResize: true,
                //   label: "年销售额",
                //   align: "center",
                //   type: "text"
                // },
                // {
                //     fixed: false,
                //     field: "enterpriseScaleDesc",
                //     width: "",
                //     minWidth: "100px",
                //     isSort: false,
                //     isResize: true,
                //     label: "人员规模",
                //     align: "center",
                //     type: "text"
                // },
                // {
                //   fixed: false,
                //   field: "equipmentModel",
                //   width: "",
                //   minWidth: "100px",
                //   isSort: false,
                //   isResize: true,
                //   label: "设备规模",
                //   align: "center",
                //   type: "text"
                // },
                {
                    fixed: false,
                    field: "zhengjingjiNum",
                    width: "",
                    minWidth: "100px",
                    isSort: false,
                    isResize: true,
                    label: "整经机规模",
                    align: "center",
                    type: "text"
                },
                {
                    fixed: false,
                    field: "jingbianjiNum",
                    width: "",
                    minWidth: "100px",
                    isSort: false,
                    isResize: true,
                    label: "经编机规模",
                    align: "center",
                    type: "text"
                },
                {
                    fixed: false,
                    field: "qitaNum",
                    width: "",
                    minWidth: "100px",
                    isSort: false,
                    isResize: true,
                    label: "其他设备规模",
                    align: "center",
                    type: "text"
                },
                {
                    fixed: false,
                    field: "authDate",
                    width: "",
                    minWidth: "300px",
                    isSort: false,
                    isResize: true,
                    label: "送审时间",
                    align: "center",
                    type: "text"
                },
                {
                    fixed: false,
                    field: "statusDesc",
                    width: "",
                    minWidth: "100px",
                    isSort: false,
                    isResize: true,
                    label: "审批状态",
                    align: "center",
                    type: "text"
                },
                {
                    fixed: false,
                    fields: "",
                    width: "",
                    isSort: false,
                    isResize: true,
                    label: "操作",
                    align: "center",
                    type: "btnsApprove",
                    minWidth: "160px"
                }
                ]
            },
            /**审批弹出框 */
            approvalData: {
                title: "审批", //弹窗标题
                dialogVisible: false, //弹窗显示
                width: "500px", //弹窗宽
                btnData: [
                {
                    isShow: true,
                    name: "取消",
                    bgColor: "dialog-normal",
                    method: "cancel"
                },
                {
                    isShow: true,
                    name: "提交",
                    bgColor: "dialog-deep-blue",
                    method: "sure"
                }
                ],
                form: {
                opinion: ""
                }
            },
            /**校验规则 */
            rules: {opinion: [{ max: 200, message: "长度在不超过200个字符", trigger: "blur" }]},
            /**查看弹出框 */
            lookDetailData: {
                title: "查看详情", //弹窗标题
                dialogVisible: false, //弹窗显示
                width: "650px", //弹窗宽
                btnData: [
                {
                    isShow: true,
                    name: "关闭",
                    bgColor: "dialog-normal",
                    method: "close"
                }
                ],
                perForm: {
                    companyName: "",
                    legalPerson: "",  //法人代表
                    propertyDesc:"", //企业性质 
                    typeDesc: "", //所属行业
                    enterpriseScaleDesc:"", //人员规模
                    zhengjingjiNum: "",
                    jingbianjiNum: "",
                    qitaNum: "",
                    imageUrl:"", //企业场所
                }
            }
            };
        },

        computed: {
            ...mapState({ ports: "ports"})
        },

        mounted() {
            this.getPage();
            this.setOptions(3);
        },

        methods: {
            setOptions(index) {   //人员规模下拉查询
                  this.$axios.get(  
                    this.ports+'/agencyUserCheck/getEnterpriseScale', 
                    {userId:this.userId}, 
                    this
                ).then(res=>{
                     this.searchData.config[0][index].childrens = res.data;
              })
            },
            timeClick(value) {
            this.checktype = value;
            this.getPage();
            },
            /**查询分页 */
            getPage() {
            if (this.checktype && this.checktype.length == 2) {
                   this.$axios.get(  //获取信息
                    this.ports+'/agencyUserCheck/get/1/2', 
                    {       userId:this.userId,
                            pageNo: this.tableConfig.pageNo,
                            pageSize: this.tableConfig.pageSize,
                            inProvince: this.searchData.data.inProvince,  //省
                            inCity: this.searchData.data.inCity,   //市
                            companyNature: this.searchData.data.companyNature, //企业性质
                            companyModel: this.searchData.data.companyModel, //企业
                            zhengjingjiNum: this.searchData.data.zhengjingjiNum, //整经机规模
                            jingbianjiNum: this.searchData.data.jingbianjiNum,//经编机规模
                            qitaNum: this.searchData.data.qitaNum,  //其他设备规模
                            authType: this.searchData.data.authType,  // 审批状态
                            authDateL: this.checktype[0],  //起始时间
                            authDateH: this.checktype[1]   //结束时间
                    }, 
                    this
                ).then(res=>{
                    if(res.success){
                        this.tableData = res.data.data
                        this.tableConfig.total = res.data.total;
                    }
                })
            } else {
                    this.$axios.get(  //获取信息
                    this.ports+'/agencyUserCheck/get/1/2', 
                    {       userId:this.userId,
                            pageNo: this.tableConfig.pageNo,
                            pageSize: this.tableConfig.pageSize,
                            inProvince: this.searchData.data.inProvince,  //省
                            inCity: this.searchData.data.inCity,   //市
                            companyNature: this.searchData.data.companyNature, //企业性质
                            companyModel: this.searchData.data.companyModel, //企业
                            zhengjingjiNum: this.searchData.data.zhengjingjiNum, //整经机规模
                            jingbianjiNum: this.searchData.data.jingbianjiNum,//经编机规模
                            qitaNum: this.searchData.data.qitaNum,  //其他设备规模
                            authType: this.searchData.data.authType,  // 审批状态
                            checktype: this.checktype
                    }, 
                    this
                ).then(res=>{
                    if(res.success){
                        this.tableData = res.data.data
                        this.tableConfig.total = res.data.total;
                    }
                })
                }
            },

            /**显示条数变化 */
            sizeChange() {
            this.getPage();
            },

            /**当前页变化 */
            pageChange() {
            this.getPage();
            },

            showDialog(row, index, type) {
            if (type == "查看") {
                this.lookDetailData.dialogVisible = true;
                for (var key in this.lookDetailData.perForm) {
                this.lookDetailData.perForm[key] = row[key];
                }
            } else {
                this.isBatch = false;
                if (this.$refs.form) {
                this.$refs.form.resetFields();
                }
                this.passActive = true;
                this.refuseActive = false;
                this.approvalData.dialogVisible = true;
                this.tableConfig.currentClickRow = row;
            }
            },

            showBatchDialog() {
            var flag = true;
            this.tableConfig.currentSelectArr.forEach(v => {
                if (v.statusDesc != "审批中") {
                flag = false;
                return;
                }
            });
            if (flag) {
                this.approvalData.dialogVisible = true;
                this.isBatch = true;
                if (this.$refs.form) {
                this.$refs.form.resetFields();
                }
                this.passActive = true;
                this.refuseActive = false;
            } else {
                this.$message({
                type: "warning",
                message: "数据已审批，请重新勾选"
                });
            }
            },

            cancel() {  //取消按钮
            this.approvalData.dialogVisible = false;
            },
            close() {   //关闭按钮
            this.lookDetailData.dialogVisible = false;
            },

            sure() {    //审批
                    var id = null;
                    if (this.isBatch) {
                        id = [];
                        this.tableConfig.currentSelectArr.forEach(v => {
                        id.push(v.id);
                        });
                        id = ids.join(",");
                    } else {
                        id = this.tableConfig.currentClickRow.id;
                    }
                  this.$axios.get(    
                    this.ports+'/agencyUserCheck/check', 
                    {
                        id:id,
                        opinion: this.approvalData.form.opinion,
                        checkResult: this.passActive ? "2" : "3"
                    }, 
                    this
                ).then(res=>{
                      this.getPage();
                      this.approvalData.dialogVisible = false;
                })
            }
        },
        watch: {
            "tableConfig.currentSelectArr": function(val) {
            if (val.length == 0) {
                this.tableConfig.toolbarConfig[0].disabled = true;
            } else {
                this.tableConfig.toolbarConfig[0].disabled = false;
            }
            }
        }

    }
</script>
<style lang="less">
    #app .my-container[data-v-04c2046b] {
        position: relative;
    }
    .el-table td, .el-table th {
    padding: 2px 0;
}
    .settleApproval {
        box-sizing:border-box;
        padding-bottom:20px;
        width:965px;
        border: 1px solid #F5F5F5;
        border-radius: 4px;        top: 0px;
        left: 234px;
        right: 20px;
        bottom: 20px;
        .tab-title {
            height: 50px;
            background: #FFF;
            border-bottom: 1px solid #E5E5E5;

            .title-text {
            width: 100px;
            height: 41px;
            background: #fff;
            border-radius: 4px 4px 0 0;
            line-height: 42px;
            text-align: center;
            font-size: 14px;
            color: #333;
            margin-top:9px;
            margin-left:56px;
            /* border: 1px solid #d0d0d0;
            border-bottom: 0;*/
            
            border: none;            
            border-bottom: 2px solid #0D6FF4;
            }
        }

        .toolleft {
            span {
            display: block;
            }
        }

        .toast-content {
            margin: 10px 20px;
            padding-bottom: 10px;

            .el-form-item {
            margin-bottom: 8px;

            img {
                width: 100px;
                height: 100px;
            }
            }

            .approval-btn {
            width: 90px;
            height: 30px;
            padding: 0;
            border-radius: 2px;
            margin-right: 10px;
            border-color: #a6a6a6;
            }

            .active {
            border: 1px solid #0c68f1;
            color: #0c68f1;

            span.circle {
                border-color: #0c68f1;
            }
            }

            span.circle {
            display: inline-block;
            width: 4px;
            height: 4px;
            border-radius: 50%;
            border: 4px solid #a6a6a6;
            margin-right: 6px;
            position: relative;
            top: 1px;
            }

            .el-textarea__inner {
            border-radius: 2px;
            border-color: #a6a6a6;
            }
        }
    }
</style>



