<template>
  <div class="receiver-detail">
    <div class="receiver-detail-title">
      <el-input placeholder="" style="width:200px;" suffix-icon="el-icon-search" size="small" v-model="searchData" @change="searchList">
      </el-input>
        <el-checkbox v-model="checked" style="margin:20px" @change="mapSync">地图同步过滤</el-checkbox>
    </div>
    <div class="receiver-detail-radio">
     <el-radio-group v-model="type" size="small" @change="searchList">
      <el-radio-button label="按公司统计"></el-radio-button>
      <el-radio-button label="设备列表"></el-radio-button>
    </el-radio-group>
    </div>
    <div class="receiver-detail-content">
        <cen-table v-if="type=='按公司统计'"  :tableConfig="tableConfig.byCompany" :tableData="tableConfig.byCompany.tableData" @pageChange="pageChange"
      @sizeChange="sizeChange"></cen-table>
          <cen-table v-if="type=='设备列表'"  :tableConfig="tableConfig.byEquip" :tableData="tableConfig.byEquip.tableData" @pageChange="pageChange"
      @sizeChange="sizeChange"></cen-table>
    </div>
  </div>
</template>
<script>
import {
    listEnterpriseMcatRunDev,
    companyDo
} from "../../api/receiver.js";
export default {
  data() {
    return {
      searchData: "",
      checked: false,
      type: "按公司统计",
      tableConfig: {
        byCompany: {
          tableData: this.tableData || [],
          colConfig: [
            {
              field: "companyName",
              label: "企业名称",
              type: "text",
              align: "left"
            },
            {
              field: "onLineDevs",
              label: "在线设备",
              type: "text",
              align: "center"
            },
            {
              field: "offLineDevs",
              label: "离线设备",
              type: "text",
              align: "center"
            }
          ],
          currentSelectArr: [],
          isSelection: false, //是否可选
          isPage: true, //是否分页
          currentPage: 1,
          pageSize: 20,
          isFormatter: false,
          total: 0, //列表数量
          height: 400, //高度
          loading: false, //加载状态
          // 是否高亮
          isHigh: true
        },
        byEquip: {
          tableData: this.tableData || [],
          colConfig: [
            {
              field: "modelName",
              label: "设备名称",
              type: "text",
              align: "center"
            },
            {
              field: "manufactory",
              label: "品牌",
              type: "text",
              align: "center"
            },
            {
              field: "spec",
              label: "规格型号",
              type: "text",
              align: "center"
            },
            {
              field: "commCode",
              label: "机联网编码",
              type: "text",
              align: "center"
            },
            {
              field: "onlineStatus",
              label: "实时状态",
              type: "text",
              align: "center"
            }
          ],
          currentSelectArr: [],
          isSelection: false, //是否可选
          isPage: false, //是否分页
          currentPage: 1,
          pageSize: 20,
          isFormatter: false,
          total: 0, //列表数量
          height: 400, //高度
          loading: false, //加载状态
          // 是否高亮
          isHigh: true
        }
      }
    };
  },
  mounted() {
    this.type = "按公司统计";
    this.searchData = "";
    this.checked = false;
    this.searchList();
  },
  methods: {
    // 地图同步
    mapSync() {
      this.$parent.$parent.$parent.searchData(
        this.tableConfig.byCompany.tableData,
        this.checked
      );
    },
    // 根据条件查询
    searchList() {
      if (this.type == "按公司统计") {
        this.searchCompany();
      } else {
        this.searchEquip();
      }
    },
    // 查询公司
    searchCompany() {
      console.log('companyDo',companyDo)
      companyDo({
        Vue: this,
        params: { companyName: this.searchData }
      }).then(data => {
        this.tableConfig.byCompany.tableData = data.model;
        if(    this.checked){
          this.mapSync();
        }
      });
    },
    // 查询设备
    searchEquip() {
      listEnterpriseMcatRunDev({
        Vue: this,
        params: { modelName: this.searchData }
      }).then(data => {
        this.tableConfig.byEquip.tableData = data.model;
      });
    },
    // 分页部分
    pageChange(pageSize, currentPage) {},
    sizeChange(pageSize, currentPage) {}
  }
};
</script>
<style lang="less">
.receiver-detail {
  width: 620px;
  height: 610px;
  padding: 15px 30px;
  background: #fff;
  box-shadow: -4px 3px 10px 0px gainsboro;
  .el-checkbox__input.is-checked + .el-checkbox__label {
    color: #606266;
  }
  .el-table__empty-block {
    height: 400px;
  }
  .el-radio-button,
  .el-radio-button__inner {
    width: 120px;
  }
  .el-radio-button__orig-radio:checked + .el-radio-button__inner {
    color: #fff;
    background-color: #0d6ff4;
    border-color: #0d6ff4;
    -webkit-box-shadow: -1px 0 0 0 #0d6ff4;
    box-shadow: -1px 0 0 0 #0d6ff4;
  }
  .receiver-detail-radio {
    margin: 20px 0;
  }
  .receiver-detail-title {
    text-align: center;
    padding-bottom: 15px;
    border-bottom: 1px solid #e5e4e2;
  }
}
</style>



