<template>
<div class="rzToastBody">
	<div class="syx_step_box">		
		<el-steps :space="400" :active="config.step-1"  align-center finish-status="success">
			<el-step title="认证选择"></el-step>
			<el-step title="认证申请"></el-step>
			<el-step title="平台审核"></el-step>
		</el-steps>
	</div>
	<div class="content_box" v-show="config.step==1">
		<div class="cert_sel_item" @click="changeCertMod(0)" :class="config.ce_mod==0? 'active' : ''">
			<img class="cer_img" src="../../assets/img/personal.png">
			<div class="r">
				<h3 class="ce_title">个人认证</h3>
				<p class="ce_con">成功认证后，您可以在云市</p>
			</div>
		</div>
		<div class="cert_sel_item" @click="changeCertMod(1)" :class="config.ce_mod==1? 'active' : ''">
			<img class="cer_img" src="../../assets/img/enterprise.png">
			<div class="r">
				<h3 class="ce_title">企业认证</h3>
				<p class="ce_con">成功认证后，您可以购买</p>
			</div>
		</div>		
		<p class="step_box"><a href="javascript:void(0);" @click="nextStep" class="btn_next">下一步</a></p>
	</div>
	<!-- 个人认证step2 step3-->
	<template v-show="config.ce_mod==0">
			<div class="content_box" v-show="config.ce_mod==0 && (config.step==2)">
				<div style="width:450px;margin:0 auto;">
					<el-form ref="formPersonal" :model="formDataPersonal" :rules="rulePersonal" label-width="100px">
						<el-form-item label="姓名" prop="name">
						 	<el-input style="width:320px; height:32px;" maxlength="70" v-model="formDataPersonal.name" placeholder="请输入姓名"></el-input>				 	
						</el-form-item>			
						<el-form-item label="性别" prop="sex">
							<el-radio-group v-model="formDataPersonal.sex">
								<el-radio :label="1">男</el-radio>
								<el-radio :label="0">女</el-radio>
							</el-radio-group>
						</el-form-item>
					
						<el-form-item label="证件类型" prop="certificateType">
							<el-select v-model="formDataPersonal.certificateType"  placeholder="请选择" style="width:120px">
							    <el-option
							      v-for="item in dropDown.zjlx"
							      :key="item.value"
							      :label="item.label"
							      :value="item.value">
							    </el-option>
							</el-select>
						</el-form-item>
						<el-form-item label="证件号码" prop="certificate">
							<el-input placeholder="请输入证件号码" maxlength="30" v-model="formDataPersonal.certificate" style="width:320px;"></el-input>
						</el-form-item>
						<el-form-item label="所在地区" prop="region">
			                <area-select :level="2" type="text" v-model="formDataPersonal.region" :data="pcaa" ></area-select>
			            </el-form-item>	            
						<el-form-item label="详细地址">
							<el-input placeholder="请输入详细地址"  maxlength="70" v-model="formDataPersonal.detailedAddress" style="width:320px;"></el-input>
						</el-form-item>
						<!-- prop="imgs" -->
						<el-form-item label="证件图片" prop="imgs" class="zjtp">
		                <div style="margin-top:5px;">
		                    <el-upload
		                        class="avatar-uploader"
		                        action="http://61.153.187.80:8081/fileSystem/uploadFile"
		                        :show-file-list="false"
		                        :on-success="uploadSuccess1"
		                        :before-upload="beforeAvatarUpload">
		                        <img v-if="formDataPersonal.certificatePositiveShareUrl" :src="formDataPersonal.certificatePositiveShareUrl" class="avatar">
		                        <i v-else class="el-icon-plus avatar-uploader-icon"></i>
		                        <div slot="tip" class="el-upload__tip">身份证正面</div>
		                        <div slot="tip" class="el-upload__tipmain" style="font-size:10px;color:#666;">注：仅支持JPG、PNG格式图片</div>
		                    </el-upload>
		                    <el-upload
		                        class="avatar-uploader uploader-right"
		                        action="http://61.153.187.80:8081/fileSystem/uploadFile"
		                        :show-file-list="false"
		                        :on-success="uploadSuccess2"
		                        :before-upload="beforeAvatarUpload">
		                        <img v-if="formDataPersonal.certificateNegativeShareUrl" :src="formDataPersonal.certificateNegativeShareUrl" class="avatar">
		                        <i v-else class="el-icon-plus avatar-uploader-icon"></i>
		                        <div slot="tip" class="el-upload__tip">身份证反面</div>
		                    </el-upload>
		                    <div style="clear:both;"></div>
		                </div>
		                
		            </el-form-item>
					</el-form>			
				</div>
				<p class="step_box">			
				<a href="javascript:void(0);" @click="nextStep" class="btn_next">下一步</a>
				<a href="javascript:void(0);" @click="nextLast" class="btn_last">上一步</a>
				</p>
			</div>
			<div class="content_box" v-show="config.ce_mod==0 && (config.step==3)">
				<div class="status_box">
					<h3><span><i class="el-icon-circle-check"></i>提交成功！</span></h3>
					<p class="status_con">我们会尽快在第一时间审核</p>
				</div>
				<div class="uinfo_box">
					<p class="info_l"><label class="title">姓名：</label><span class="after">{{formDataPersonal.name}}</span></p>
					<p class="info_l"><label class="title">性别：</label><span class="after">{{sex_2[formDataPersonal.sex]}}</span></p>
					<p class="info_l"><label class="title">证件类型：</label><span class="after">{{zjlx_[formDataPersonal.certificateType]}}</span></p>
					<p class="info_l"><label class="title">证件号码：</label><span class="after">{{formDataPersonal.certificate}}</span></p>
					<p class="info_l"><label class="title">地址：</label><span class="after">{{formDataPersonal.province}}{{formDataPersonal.city}}{{formDataPersonal.district}}{{formDataPersonal.detailedAddress}}</span></p>
					<p class="info_l"><label class="title">证件图片：</label><span class="after">
						<img class="zjpic" :src="formDataPersonal.certificatePositiveShareUrl" ></img>
						<img class="zjpic" :src="formDataPersonal.certificateNegativeShareUrl" ></img>
					</span></p>
				</div>
			</div>
	</template>
	<!-- 企业认证step2 step3 -->
	<template v-show="config.ce_mod==1">
		<div class="content_box"  v-show="config.ce_mod==1 && (config.step==2)">
			<h1 class="form_title">企业信息</h1>
			<el-form ref="formCompany" :model="formDataCompany" :rules="rulesCompany" label-width="216px" >
				<el-form-item label="企业名称" style="width:320px;" prop='name'>
					<el-input style="width:320px;" maxlength="70" v-model="formDataCompany.name"  placeholder="请输入企业名称(最长70个字符)"></el-input>
				</el-form-item>	
				<el-form-item label="行业类型">
					<el-select v-model="formDataCompany.trade" placeholder="请选择" style="width:210px">
					    <el-option
					      v-for="item in dropDown.hylx"
					      :key="item.value"
					      :label="item.label"
					      :value="item.value">
					    </el-option>
					</el-select>
				</el-form-item>
				<el-form-item label="统一社会信用代码" style="width:320px;" prop="socialCreditCode">
					<el-input v-model="formDataCompany.socialCreditCode" maxlength="18" style="width:320px;" placeholder="请输入信用代码(18为字符加数字)"></el-input>
				</el-form-item>
				<el-form-item label="所在地区" prop="region">
			        <area-select :level="2" type="text" v-model="formDataCompany.region" :data="pcaa" ></area-select>
			    </el-form-item>
				<el-form-item label="详细地址" style="width:320px;" prop="detailedAddress">
					<el-input style="width:320px;" maxlength="70" v-model="formDataCompany.detailedAddress" placeholder="请输入详细地址"></el-input>
				</el-form-item>
				<el-form-item label="联系人" style="width:320px;" prop="contactPerson">
					<el-input style="width:320px;" maxlenth="40" v-model="formDataCompany.contactPerson" placeholder="请输入联系人"></el-input>
				</el-form-item>
				<el-form-item label="联系方式" maxlength="70" style="width:320px;" prop="contactPhone">
					<el-input style="width:320px;" v-model="formDataCompany.contactPhone" placeholder="请输入联系方式"></el-input>
				</el-form-item>
				<el-form-item class="zjtp fix" label="营业执照" style="width:320px;" prop="imgszz">
					<span><el-upload
	                    class="avatar-uploader"
	                    action="http://61.153.187.80:8081/fileSystem/uploadFile"
	                    :show-file-list="false"
	                    :on-success="uploadSuccesszhizhao"
	                    :before-upload="beforeAvatarUpload">
	                    <img v-if="formDataCompany.businessLicenceShareUrl" :src="formDataCompany.businessLicenceShareUrl" class="avatar">
	                    <i v-else class="el-icon-plus avatar-uploader-icon"></i>
	                </el-upload>
	                <label style="display:block;font-size:12px;color:#999;min-width:200px;float:left;line-height:12px;margin-top:12px;">注：仅支持 JPG、PNG 图片</label></span>
				</el-form-item>
				<i class="form_line" style="margin-top:50px;"></i>
				<h1 class="form_title" style="margin-top:32px;">法人信息</h1>
				<el-form-item label="姓名" style="width:320px;" prop="legalPerson">
					<el-input style="width:320px;" v-model="formDataCompany.legalPerson" maxlength="40" placeholder="请输入姓名"></el-input>
				</el-form-item>
				<el-form-item label="证件类型" prop='legalPersonIdentityType'>
					<el-select v-model="formDataCompany.legalPersonIdentityType" placeholder="请选择" style="width:120px">
					    <el-option
					      v-for="item in dropDown.zjlx"
					      :key="item.value"
					      :label="item.label"
					      :value="item.value">
					    </el-option>
					</el-select>
				</el-form-item>
				<el-form-item label="证件号码"  style="width:320px;" prop="legalPersonIdentityCard">
					<el-input style="width:320px;" maxlength="30" v-model="formDataCompany.legalPersonIdentityCard" placeholder="请输入证件号码"></el-input>
				</el-form-item>
				<el-form-item  class="zjtp" label="证件图片" prop="imgs">
	                <div style="margin-top:5px;">
	                    <el-upload
	                        class="avatar-uploader"
	                        action="http://61.153.187.80:8081/fileSystem/uploadFile"
	                        :show-file-list="false"
	                        :on-success="uploadSuccess3"
	                        :before-upload="beforeAvatarUpload">
	                        <img v-if="formDataCompany.certificatePositiveShareUrl" :src="formDataCompany.certificatePositiveShareUrl" class="avatar">
	                        <i v-else class="el-icon-plus avatar-uploader-icon"></i>
	                        <div slot="tip" class="el-upload__tip">身份证正面</div>
	                        <div slot="tip" class="el-upload__tipmain" style="font-size:10px;color:#666;">注：仅支持JPG、PNG格式图片</div>
	                    </el-upload>
	                    <el-upload
	                        class="avatar-uploader uploader-right"
	                        action="http://61.153.187.80:8081/fileSystem/uploadFile"
	                        :show-file-list="false"
	                        :on-success="uploadSuccess4"
	                        :before-upload="beforeAvatarUpload">
	                        <img v-if="formDataCompany.certificateNegativeShareUrl" :src="formDataCompany.certificateNegativeShareUrl" class="avatar">
	                        <i v-else class="el-icon-plus avatar-uploader-icon"></i>
	                        <div slot="tip" class="el-upload__tip">身份证反面</div>
	                    </el-upload>
	                    <div style="clear:both;"></div>
	                </div>
	                
	            </el-form-item>
			</el-form>

				<p class="step_box">			
				<a href="javascript:void(0);" @click="nextStep" class="btn_next">下一步</a>
				<a href="javascript:void(0);" @click="nextLast" class="btn_last">上一步</a>
				</p>
			
		</div>
		<div class="content_box" v-show="config.ce_mod==1 && (config.step==3)">
			<div class="status_box">
					<h3><span><i class="el-icon-circle-check"></i>提交成功！</span></h3>
					<p class="status_con">我们会尽快在第一时间审核</p>
				</div>
				<div class="uinfo_box">
				<h1 class="form_title">企业信息</h1>	
					<p class="info_l"><label class="title">企业名称：</label><span class="after">{{formDataCompany.name}}</span></p>
					<p class="info_l"><label class="title">行业类型：</label><span class="after">{{hylx_[formDataCompany.trade]}}</span></p>
					<p class="info_l"><label class="title">统一社会信用代码：</label><span class="after">{{formDataCompany.socialCreditCode}}</span></p>
					<p class="info_l"><label class="title">联系地址：</label><span class="after">{{formDataCompany.province}}{{formDataCompany.city}}{{formDataCompany.district}}{{formDataCompany.detailedAddress}}</span></p>
					<p class="info_l"><label class="title">联系人：</label><span class="after">{{formDataCompany.contactPerson}}</span></p>
					<p class="info_l"><label class="title">联系电话：</label><span class="after">{{formDataCompany.contactPhone}}</span></p>
					<p class="info_l"><label class="title">营业执照：</label><span class="after">
						<img class="zjpic" :src="formDataCompany.businessLicenceShareUrl" ></img>
					</span></p>
					<!-- <p class="info_l"><label class="title">地址：</label><span class="after"></span></p> -->
				<h1 class="form_title" style="margin-top:32px">法人信息</h1>	
					<p class="info_l"><label class="title">姓名：</label><span class="after">{{formDataCompany.legalPerson}}</span></p>
					<p class="info_l"><label class="title">证件号码：</label><span class="after">{{formDataCompany.legalPersonIdentityCard}}</span></p>
					<p class="info_l"><label class="title">证件图片：</label><span class="after">
						<img class="zjpic" :src="formDataCompany.certificatePositiveShareUrl" ></img>
						<img class="zjpic" :src="formDataCompany.certificateNegativeShareUrl" ></img>
					</span></p>
				</div>
		</div>
	</template>

</div>	
</template>
<script>
import { pca, pcaa } from "area-data";
import {mapState,mapAction} from 'vuex'
import {
	personalAuthAdd,
	personalAuthPage,
	personalAuthUpdate,
	enterpriseauthAdd,
	enterpriseauthPage,
	enterpriseauthUpdate
} from "../../api/rz.js"
var vueObj={
	props:{
		config:{
			type:[Object,Array],
			default:()=>{
				return {
					step:1,
					ce_mod:0,//[0:个人认证模式，1：企业认证模式]
				}
			}
		}
	},
	computed:{
		...mapState(['ports','userId','authStatus','authType'])
	},
	data(){
		return {
			sex_2:['女','男'],
			zjlx_:['身份证','护照','港澳台通行证'],
			hylx_:['精编行业','加工类行业'],
			isUpdate:false,
			sex_:[0,1],
			pcaa: pcaa,
			dropDown:{
				//证件类型
				zjlx:[
					{label:"身份证",value:0},
					{label:"护照",value:1},
					{label:"港澳台通行证",value:2},
				],
				hylx:[
					{label:"经编行业",value:0},
					{label:"加工类行业",value:1}
				]
			},
			// 个人认证表单
			formDataPersonal:{
				"userId":null,
				"name":"",
				"certificateType":"",
				"certificate":"",
				"sex":'',
				"region":[],//["浙江省","嘉兴市","海宁市"],
				"province":"",
				"city":"",
				"district":"",
				"detailedAddress":"",
				"certificatePositiveShareUrl":"",
				"certificateNegativeShareUrl":""
			},
			rulePersonal:{
				"userId":[],
				"name":[{required: true, message:'该项为必填项',trigger: ['input','change','blur']}],
				"certificateType": [{required: true, message:'请选择证件类型',trigger: ['change']}],
				"certificate": [{required: true, trigger: ['input','change','blur'],validator:(rule, value, callback)=>{
			    	if(this.formDataPersonal.certificateType==0){
			    		if(!/^\d{6}(18|19|20)?\d{2}(0[1-9]|1[012])(0[1-9]|[12]\d|3[01])\d{3}(\d|X)$/i.test(value)){
			    			callback(new Error("证件号码有误！"))
			    			return;
			    		}else{
			    			callback()
			    			return;
			    		}
			    	}else{
			    		if(value)callback();
			    		else callback(new Error("请输入证件号码！"))
			    	}
			    }}],
				"sex": [{required: true, message:'请选择性别',trigger: ['input','change','blur']}],
				"region": [{required: true, message:'请选择所在地区',trigger: ['change','blur'],validator:(rule, value, callback) => {
					if(value[0]&&value[1]&&value[2])callback()
					else callback(new Error("请选择所在地区"))}}],
				"province": [],
				"city": [],
				"district": [],
				"detailedAddress": [],
				"imgs":[{required: true, message:'请上传证件图片',trigger: ['input','change','blur'],validator:(rule, value, callback) =>{
					if(this.formDataPersonal.certificatePositiveShareUrl && this.formDataPersonal.certificateNegativeShareUrl){
						callback();
					}else{
						callback(new Error("请上传指定证件图片"))
					}
				}}],
				"certificatePositiveShareUrl": [],
				"certificateNegativeShareUrl": []
			},
			// 企业认证表单
			formDataCompany:{
				"userId": '',//999,
			    "deleted": '',//"F",
			    "createBy": '',//"丽经编海",
			    "createTime": '',//1532137220000,
			    "modifyBy":  '',//"丽经编海",
			    "modifyTime": '',// 1532137220000,
			    "companyId": '',// null,
			    "name": '',// "海宁海立经编有限公司",
			    "region": [],//['浙江省','嘉兴市','海宁市'],
			    "province":  '',//"浙江省",
			    "city":  '',//"嘉兴市",
			    "district":  '',//"海宁市",
			    "detailedAddress":  '',//"浙江嘉兴",
			    "contactPerson":  '',//"张三",
			    "contactPhone":  '',//"15533333333",
			    "legalPerson":  '',//"张三",
			    "legalPersonPhone":  '',//"15566666666",
			    "legalPersonIdentityCard":  '',//"465133199602056633",
			    "certificatePositiveShareUrl":  '',//"http://61.153.187.80:9000/group1/M00/00/27/wKgBDFtSkHqASHWIAAIxQFcz6hg164.jpg",
			    "certificateNegativeShareUrl":  '',//"http://61.153.187.80:9000/group1/M00/00/27/wKgBDFtSkHyAMEFwAAFYqTUU9xI569.png",
			    "socialCreditCode":  '',//"123456789123456789",
			    "businessLicenceShareUrl":  '',//"http://61.153.187.80:9000/group1/M00/00/27/wKgBDFtSkIWAcjsoAABvt_iaink609.jpg",
			    "status":  '',//2,
			    "sended":  '',//"T",
			    "legalPersonIdentityType": '',//"0",
			    "updated":  '',//"F",
			    "approvalOpinion": "",
			    "trade": '',//0
			},
			rulesCompany:{
				"userId": [],
			    // "deleted": [],
			    // "createBy": [],
			    // "createTime":[],
			    // "modifyBy": "丽经编海",
			    // "modifyTime": 1532137220000,
			    "companyId": [],
			    "name": [{required: true, message:'该项为必填项',trigger: ['input','change','blur']}],
			    "region": [{required: true, message:'请选择所在地区',trigger: ['change','blur'],validator:(rule, value, callback) => {
					if(value[0]&&value[1]&&value[2])callback()
					else callback(new Error("请选择所在地区"))}}],
			    "province": [],
			    "city": [],
			    "district": [],
			    "detailedAddress": [],
			    "contactPerson": [{required: true, message:'该项为必填项',trigger: ['input','change','blur']}],
			    "contactPhone": [{required: true, message:'该项为必填项',trigger: ['input','change','blur']}],
			    "legalPerson": [{required: true, message:'该项为必填项',trigger: ['input','change','blur']}],
			    "legalPersonPhone": [{required: true, message:'该项为必填项',trigger: ['input','change','blur']}],
			    "legalPersonIdentityCard": [{required: true,  trigger: ['input','change','blur'],validator:(rule, value, callback)=>{
			    	if(this.formDataCompany.legalPersonIdentityType==0){
			    		if(!/^\d{6}(18|19|20)?\d{2}(0[1-9]|1[012])(0[1-9]|[12]\d|3[01])\d{3}(\d|X)$/i.test(value)){
			    			callback(new Error("证件号码有误！"))
			    			return;
			    		}else{
			    			callback()
			    			return;
			    		}
			    	}else{
			    		if(value)callback();
			    		else callback(new Error("请输入证件号码！"))
			    	}
			    }}],
			    "certificatePositiveShareUrl": [],
			    "certificateNegativeShareUrl": [],
			    "socialCreditCode": [{required: true, message:'统一社会信用代码有误',trigger: ['input','change','blur'],validator:(rule, value, callback) =>{
					if(/^[a-z|A-Z|0-9]{18}$/.test(this.formDataCompany.socialCreditCode)){
						callback();
					}else{
						callback(new Error("请上传指定证件图片"))
					}
				}}],
			    "businessLicenceShareUrl":[],
			    'imgszz':[{required: true, message:'请上传营业执照',trigger: ['input','change','blur'],validator:(rule, value, callback) =>{
					if(this.formDataCompany.businessLicenceShareUrl){
						callback();
					}else{
						callback(new Error("请上传指定证件图片"))
					}
				}}],
			    "imgs":[{required: true, message:'请上传证件图片',trigger: ['input','change','blur'],validator:(rule, value, callback) =>{
					if(this.formDataCompany.certificatePositiveShareUrl && this.formDataCompany.certificateNegativeShareUrl){
						callback();
					}else{
						callback(new Error("请上传指定证件图片"))
					}
				}}],
			    // "status": 2,
			    // "sended": "T",
			    "legalPersonIdentityType":[{required: true, message:'该项为必填项',trigger: ['input','change','blur']}],
			    // "updated": "F",
			    // "approvalOpinion": "",
			    "trade":[{required: true, message:'该项为必填项',trigger: ['input','change','blur']}],
			},
			// 申请审核表单数据
			formDataSq:{
				sex:null,
				region:"",
				imgs:[],
				certificateNegativeShareUrl:"",
				certificatePositiveShareUrl:"",
			},
			ruleSq:{
				region:[],
				imgs:[],
				certificateNegativeShareUrl:[],
				certificatePositiveShareUrl:[],
			},
			formDataSq2:{
				imgs:[],
			},
			ruleSq2:{

			}

		}
	},
	watch:{
		'formDataPersonal.region':{
　　　　　　　　　　handler(curVal,oldVal){
				this.$refs['formPersonal'].validateField('region');
　　　　　　　　　　　　console.log(curVal,oldVal)
　　　　　　　　　　},
　　　　　　　　　　deep:true
　　　　　　　　}
	},
	methods:{
		checkCert(formData){
				console.log("checkCert.......")
			if(formData){
				if(formData.account.authType==1){					
					this.formDataPersonal = formData.personalAuth;
					this.config.ce_mod=0;
				}
				if(formData.account.authType==2){
					this.formDataCompany  = {...formData.enterpriseAuth,...formData.legalPerson}
					this.config.ce_mod=1;
				}
				this.config.step=3;
			}
		},
		setRzData(authType,authStatus,personalAuth){
			if(authType==1){
				this.isUpdate=true;
				personalAuth.region=[personalAuth.province,personalAuth.city,personalAuth.district];
				delete personalAuth.certificateTypeDesc;
				setTimeout(()=>{
					this.formDataPersonal=personalAuth;
					setTimeout(()=>{
						this.$refs['formPersonal'].clearValidate();
					},30)
				}
				,100)
			}else this.isUpdate=false;
		},
		setRzCpData(authType,authStatus,legalPerson,enterpriseAuth){
			if(authType==2){
				this.isUpdate=true;
				enterpriseAuth.region=[enterpriseAuth.province,enterpriseAuth.city,enterpriseAuth.district];
				setTimeout(()=>{
					this.formDataCompany={...enterpriseAuth,...legalPerson};
					this.$set(this.formDataCompany,'region',[enterpriseAuth.province,enterpriseAuth.city,enterpriseAuth.district])
					console.log('this.formDataCompany====>',this.formDataCompany,)
					setTimeout(()=>{
						this.$refs['formCompany'].clearValidate();
					},30)
				}
				,100)
			}
		},
		setUpMod(isUpdate=false){
			console.log('setUpMod===>',isUpdate);
			this.isUpdate=isUpdate;
		},
		resetPersonalForm(){			
			console.log('formPersonal reset...')
			setTimeout(()=>{
				this.formDataPersonal.region=["北京市","市辖区","东城区"];
				setTimeout(()=>{					
					this.$refs['formPersonal'].clearValidate();
				},30)
			},30)
			this.$refs['formPersonal'].resetFields();
		},
		resetCompanyForm(){
			this.formDataCompany.region=["北京市","市辖区","东城区"];
			console.log('formCompany reset...')
			this.$refs['formCompany'].resetFields();
		},
		uploadSuccesszhizhao(file){
                // console.log(file)
                // this.formDataSq2.imgs[0]=file.model
                this.formDataCompany.businessLicenceShareUrl=file.model
                // this.$refs.formCompany.validateField('imgs')
        },
        uploadSuccess3(file){
        	this.formDataCompany.certificatePositiveShareUrl=file.model;
        },
        uploadSuccess4(file){
        	this.formDataCompany.certificateNegativeShareUrl=file.model;
        },
		uploadSuccess1(file){
            // this.formDataSq.imgs[0]=file.model
            this.formDataPersonal.certificatePositiveShareUrl=file.model
            // this.$refs.ruleForm.validateField('imgs')
        },
        uploadSuccess2(file){
            // this.formDataSq.imgs[1]=file.model
            this.formDataPersonal.certificateNegativeShareUrl=file.model
            // this.$refs.ruleForm.validateField('imgs')
        },
        beforeAvatarUpload(file) {
            const isJPG = file.type === 'image/jpeg';
            const isPNG = file.type === 'image/png';
            const isLt2M = file.size / 1024 / 1024 < 2;
            console.log(file)
            if (!isJPG&&!isPNG) {
                this.$message.error('上传头像图片只能是 JPG 或者 PNG 格式!');
            }
            if (!isLt2M) {
                this.$message.error('上传头像图片大小不能超过 2MB!');
            }
            return (isJPG||isPNG) && isLt2M;
        },
		reset(){
			console.log('reset...............')
			this.config.step=1;
			this.config.ce_mod=0;
		},
		changeCertMod(mod){
			this.config.ce_mod=mod;
		},
		submitForm(formName,fun) {
	        let form=this.$refs[formName];
	        if(form){
	        	form.validate((valid) => {
		        	if (valid) {
		              	fun();
		          	} else {
		            	return false;
		          	}
	        	});
	        }
	        	
	    },
	    setAttr(data){
	    	for(let attr in data){
	    		if(undefined===data[attr]||(''===data[attr]))delete data[attr];
	    	}
	    	return data;
	    },
		nextStep(){
			if(this.config.step<3){			
				if(this.config.step==1){
					if(this.config.ce_mod==0){
						if(!this.isUpdate){
							this.resetPersonalForm();
						}
					}else{
						if(!this.isUpdate)this.resetCompanyForm();
					}
					this.config.step++;
				}else if(this.config.step==2){
					if(this.config.ce_mod==0){
						this.submitForm('formPersonal',()=>{
							// 保存个人认证信息
							let params = this.formDataPersonal;
							params.userId=this.userId;
							params.province=this.formDataPersonal.region[0]
							params.city=this.formDataPersonal.region[1]
							params.district=this.formDataPersonal.region[2]
							delete params.region;
							if(params.sexDesc)delete params.sexDesc;
							if(params.certificateTypeDesc)delete params.certificateTypeDesc;
							params = this.setAttr(params);
							delete params.userId;
							let action=this.isUpdate? personalAuthUpdate : personalAuthAdd;
							console.log('isUpdate=====>',this.isUpdate)
	    					action({Vue:this,params:params}).then(res=>{
	    						this.$message.success(res.message);
	    						this.$emit('rzChange',this.userId)
	    						this.config.step++;
	    					})
						})
						// personalAuthAdd()
						// this.resetPersonalForm();
					}else{
						this.submitForm('formCompany',()=>{
							let params=this.formDataCompany;
							params.userId=this.userId;
							params.province=this.formDataCompany.region[0]
							params.city=this.formDataCompany.region[1]
							params.district=this.formDataCompany.region[2]
							delete params.region;
							delete params.identityCard;
							console.log('isUpdate2=====>',this.isUpdate)
							let action=this.isUpdate? enterpriseauthUpdate : enterpriseauthAdd;
							action({Vue:this,params:params}).then(res=>{
								this.$message.success(res.message);
								this.$emit('rzChange',this.userId)
		    					this.config.step++;
							})
						})

						

						console.log("企业认证提交表单")
						// this.resetCompanyForm();
					}
				}
				console.log('this.userId',this.userId);
				
			}
		},
		nextLast(){
			if(this.config.step>0){
				this.config.step--;
				console.log(this.config.step);
			}
		},
	}
}
export default vueObj;	
</script>
<style lang="less">
.rzToastBody{
	.step_box{
		width: 400px;
		margin: 0 auto 80px;
		.el-step__title,.el-step__head{
			color:#0D6FF4;
			border-color:#0D6FF4;
		}
		.el-step__title.is-process{
			font-weight:400;
		}
		.is-success .el-step__icon.is-text{
			background:#0D6FF4;
			color:#FFF;
		}
		.el-step__head.is-success .el-step__line{
			background:#0D6FF4;
		}
		.el-step__title.is-success,.el-step__head.is-success{
			color:#0D6FF4;
			border-color:#0D6FF4;
		}
		.el-step__title.is-wait,.el-step__head.is-wait{
			color:#999;
			border-color:#999;
		}
	}
	.syx_step_box{
                width: 500px;
                margin: 0 auto 80px;
                .el-step__title,.el-step__head{
                    color:#0D6FF4;
                    border-color:#0D6FF4;
                }
                .el-step__title.is-process{
                    font-weight:400;
                }
                .el-step__icon.is-text{
                    width: 32px;
                    height: 32px;
                }
                .el-step.is-horizontal .el-step__line{
                    top: 14px;
                }
                .is-success .el-step__icon.is-text{
                    background:#0D6FF4;
                    color:#FFF;
                }
                .el-step__head.is-success .el-step__line{
                    background:#0D6FF4;
                }
                .el-step__title.is-success,.el-step__head.is-success{
                    color:#0D6FF4;
                    border-color:#0D6FF4;
                }
                .el-step__title.is-wait,.el-step__head.is-wait{
                    color:#999;
                    border-color:#999;
                }
            }
	.content_box{
		width:100%;
		height:auto;
		box-sizing:border-box;
		.cert_sel_item{
			cursor:pointer;
			box-sizing:border-box;
			margin:0 auto 35px;
			width:420px;
			height:140px;
			border:1px solid #E5E5E5;
			.cer_img{
				width:76px;
				height:54px;
				float:left;
				margin:43px 0 0 42px;
			}
			.r{
				width:420px;
				box-sizing:border-box;
				padding-left:134px;
				color:#333;
				font-size:18px;
				line-height:1;
				.ce_title{
					margin-top:50px;
				}
				.ce_con{
					font-size:14px;
					color:#999;
					margin-top:14px;
				}
			}

		}
		.cert_sel_item.active{
			background:url('../../assets/img/topright.png') no-repeat;
			background-position:380px 0;
			border-color:#0D6FF4;
		}
		.el-form-item__label{
			line-height:32px;
			height:32px;
		} 
		.el-form-item__content,.el-input__inner {
			height:32px;
			line-height:32px;
		}
		.area-select .area-selected-trigger{
			padding:0;
			padding-left:10px;
		}
		.area-select{
			width:100px;
			margin-left:4px;
		}
		.area-select:first-child{
			margin-left:0;
		}

		 .avatar-uploader{
            width: 150px;
            height: 100px;
            float: left;
        }
        .uploader-right{
            margin-left: 15px;
        }
        .avatar-uploader .el-upload {
            border: 1px dashed #d9d9d9;
            border-radius: 6px;
            cursor: pointer;
            position: relative;
            overflow: hidden;
        }
        .avatar-uploader .el-upload:hover {
            border-color: #409EFF;
        }
        .avatar-uploader-icon {
            font-size: 28px;
            color: #8c939d;
            width: 150px;
            height: 100px;
            line-height: 100px;
            text-align: center;
        }
        .avatar {
            width: 150px;
            height: 100px;
            display: block;
        }
        .el-upload__tip{
            margin-top: -10px;
            text-align: center;
        }
        .el-upload__tipmain{
        	position:absolute;
        	left:0;
        	top:140px;
            margin-top: -5px;
            font-size:14px;
            font-family:MicrosoftYaHei;
            color:rgba(153,153,153,1);
        }
        .step_box{
        	text-align:center;
        	margin-top:80px;
        }
		.btn_next{
			display:inline-block;
			color:#FFF;
			background:#0D6FF4;
			text-align:center;
			font-size:12px;
			width:88px;
			height:32px;
			line-height:32px;
			text-decoration:none;
		}
		.btn_last{
			margin-left:10px;
			display:inline-block;
			color:#666;
			background:#FFF;
			text-align:center;
			border:1px solid #E5E5E5;
			font-size:12px;
			width:88px;
			height:32px;
			line-height:32px;
			text-decoration:none;
		}
		.status_box{
			position:relative;
			padding:0 40px 50px;
			box-sizing:border-box;
		}	
		.el-select .el-input .el-select__caret{
			line-height:32px;
		}
		.status_box:after{
			content:"";
			display:block;
			position:absolute;
			bottom:0;
			left:40px;
			width:600px;
			height:1px;
			background:#E5E5E5;
		}
		.status_box h3{
			text-align:center;			
		}
		.status_box h3 span{
			display:inline-block;
			position:relative;
			font-size:24px;			
			line-height:34px;
