			color:#666;
			text-align:right;
		}
		.info_l .after{
			display:inline-block;
			color:#333;
			padding-left:20px;
		}
		.zjpic{
			display:inline-block;
			width:150px;
			height:100px;
			border:1px solid #E5E5E5;
			margin-right:20px;
		}
		.form_title{
			padding-left:128px;
			color:#333;
			font-size:18px;
			margin-bottom:22px;
		}
		.form_line{
			display:block;
			margin: 0 auto;
			width:620px;
			height:1px;
			background:#E5E5E5;
		}
		.zjtp .el-form-item__error{
			margin-top:130px;
		}
		.zjtp.fix .el-form-item__error{
			margin-top:100px;
		}
		.el-form-item__error{
			min-width:320px;
		}
	}
}
</style>


