<template>
    <div class="agent">
        <div class="left agent_item" v-for="(item,index) in appplicationList" :key="index" @click="lookDetail(item)">
            <div>
                <p>
                    <span><span class="item_title">代理行业</span><span class="item_detail">{{item.agencyTypeDesc}}</span></span>
                </p>
                <p>
                    <span><span class="item_title">代理省份</span><span class="item_detail">{{item.agencyArea}}</span></span>
                </p>
            </div>
            <div class="symbol" :class="{'is-success':item.status==2,'is-doing':item.status==1,'is-error':item.status==3}">
                {{setText(item.status)}}
            </div>
        </div>
        <div class="agent_item_plus">
            <el-button type="text" @click="application">代理申请</el-button>
        </div>
        <my-toast :config="toastConfig" @handleClose="handleClose">
            <el-form :model="formData" :rules="rules" ref="ruleForm" label-width="200px" class="demo-ruleForm" size="small">
                <el-form-item label="代理行业" prop="agencyType">
                    <el-select size="medium" v-model="formData.agencyType" placeholder="请选择代理行业" filterable @change="handlerAgencyTypeChange">
                        <el-option v-for="item in businessTypeList" :label="item.name" :value="item.id" :key="item.id"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="代理地区" prop="agencyArea" v-if="formData.agencyType">
                    <el-select size="medium" v-model="formData.agencyArea" placeholder="请选择代理地区" filterable>
                    <el-option v-for="item in marketClassList" 
                                :label="item.label" 
                                :value="item.name" 
                                :key="item.name"
                                :disabled="item.status==0?true:false">
                    </el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="相关资质" prop="qualityImageShareUrls">
                    <p class="msg-tip">具体提交资料内容请查询 <span>代理服务条件</span></p>
                    <!-- <el-form-item  v-for="img in formData.qualityImageShareUrl">
                        1231231
                        <img  class="avatar" :src="img[i]" v-if="img[i]" v-for="i in array" @click="shows" v-show="show" title="点击删除" style="display:inline-block;margin-right:10px;margin-top:17px;">
                    </el-form-item> -->
                    <div style="width:250px;">
                        <el-upload
                        action="http://61.153.187.80:8081/fileSystem/uploadFile"
                        list-type="picture-card"
                        :file-list="list"
                        :data="{appSrc:'icloud'}"
                        :on-success="uploadSuccess"
                        :on-remove="handlerMove"
                        :before-upload="beforeAvatarUpload">
                        <i class="el-icon-plus"></i>
                        <div slot="tip" class="el-upload__tip">请上传一张图片，仅支持JPG、PNG格式图片</div>
                        </el-upload>
                    </div>
                </el-form-item>
                <el-form-item>
                    <el-checkbox v-model="isRead"></el-checkbox><span style="margin-left:8px">我已阅读并同意</span><span class="protocol">代理规则</span>
                    <div @click="onSubmit" class="submit-btn" :class="{'isDisabled':!isRead}">立即创建</div>
                </el-form-item>
            </el-form>
        </my-toast>
        <my-toast :config="detailConfig" @handleClose="doClose"> 
            <h2 class="applicationResult">代理审核信息<span :class="{'is-success':formData.status==2,'is-doing':formData.status==1,'is-error':formData.status==3}">({{setText(formData.status)}})</span></h2>
            <el-form :model="formData"   label-width="200px" class="demo-ruleForm" size="small">
                <el-form-item label="代理行业" style="height:32px;">
                    <span class="demo-ruleForm-item">{{formData.agencyTypeDesc}}</span>
                </el-form-item>
                <el-form-item label="代理地区" style="height:32px;">
                    <span class="demo-ruleForm-item">{{formData.agencyArea}}</span>
                </el-form-item>
                <el-form-item label="相关资质" >
                   <div style="margin-top:10px;" class="avatar" v-for="it in formData.qualityImageShareUrls">
                        <img :src="it.qualityImageShareUrl" >
                    </div>
                </el-form-item>
                <el-form-item label="审批意见" style="height:32px;" v-if="formData.status==3">
                    <span class="demo-ruleForm-item">{{formData.approvalOpinion}}</span>
                </el-form-item>
                <el-form-item>
                    <div @click="reApplication" class="submit-btn" v-if="formData.status!=1">重新申请</div>
                </el-form-item>
            </el-form>
        </my-toast>
    </div>
</template>
<script>
import { pca, pcaa } from "area-data"; 
import rule from '../../assets/js/rule.js'
import {mapState} from 'vuex'
export default {
    data(){
        return{
            nextType:'',
            appplicationList:[],
            toastConfig:{
                dialogVisible:false,
                width:'560px'
            },
            detailConfig:{
                dialogVisible:false,
                width:'500px'
            },
            formData:{
                agencyType:'',
                agencyArea:'',
                qualityImageShareUrls:[]
            },
            rules:{ 
                agencyType:rule.require('string','请选择代理行业'),  //代理行业
                agencyArea:rule.require('string','请选择代理区域'),  //代理区域
                qualityImageShareUrls:rule.require('string','请至少上传一张图片'),   
            },
            // 是否已阅读协议
            isRead:false,
            businessTypeList:[],
            marketClassList:[]
        }
    },
    computed:{
        ...mapState(['userId','ports']),
        list:function(){
            var ary=[]
            for(let d of this.formData.qualityImageShareUrls){
                ary.push({
                    name:d.qualityImageShareUrl,
                    url:d.qualityImageShareUrl
                })
            }
            return ary
        }
    },
    methods:{
        doClose(){
            if(this.nextType=='todoAdd'){
                this.formData={
                    agencyType:'',
                    agencyArea:'',
                    qualityImageShareUrls:[]
                }
            }
        },
        setText(status){
            if(status==1){
                return '审核中'
            }else if(status==2){
                return '审核成功'
            }else{
                return '审核失败'
            }
        },
        handleClose(){
            this.$refs.ruleForm.resetFields()
        },
        handlerAgencyTypeChange(val){
            if(val){
                this.formData.agencyArea=''
                this.$axios.get(   //代理区域列表
                    this.ports+'/applicationAgency/getAgencyArea',
                    {agencyType:val,exceptId:this.formData.id},
                    this
                ).then(res=>{
                    if(res.success){
                        this.marketClassList=res.data
                    }
                })
            }else{
                this.marketClassList=[]
            }
        },
        reApplication(){
            this.nextType='todoEdit'
            this.$axios.get(   //代理区域列表
                this.ports+'/applicationAgency/getAgencyArea',
                {agencyType:this.formData.agencyType,exceptId:this.formData.id},
                this
            ).then(res=>{
                if(res.success){
                    this.detailConfig.dialogVisible=false;
                    this.toastConfig.dialogVisible=true;
                    this.marketClassList=res.data
                }
            })
        },
        lookDetail(data){
            console.log(data)
            this.nextType='todoAdd'
            this.formData={
                agencyType:data.agencyType,
                agencyArea:data.agencyArea,
                agencyTypeDesc:data.agencyTypeDesc,
                qualityImageShareUrls:data.qualityImageShareUrls,
                status:data.status,
                approvalOpinion:data.approvalOpinion,
                id:data.id
            }
            this.detailConfig.dialogVisible=true
        },
        application(){
            this.formData={
                agencyType:'',
                agencyArea:'',
                qualityImageShareUrls:[]
            }
            // this.$refs.ruleForm.resetFields()
            // setTimeout(()=>{
                this.toastConfig.dialogVisible=true;
                this.$nextTick(()=>{    
                    this.$refs.ruleForm.resetFields()
                    
                })
            // },1000)
        },
        uploadSuccess(file){
            this.formData.qualityImageShareUrls.push({qualityImageShareUrl:file.model})
            this.$refs.ruleForm.validateField('qualityImageShareUrls')
        },
        handlerMove(file){
            var index=null;
            if(file.url)
                index=this.formData.qualityImageShareUrls.findIndex(val=>val.qualityImageShareUrl==file.url)
            else
                index=this.formData.qualityImageShareUrls.findIndex(val=>val.qualityImageShareUrl==file.response.model)
            if(index>=0)
                this.formData.qualityImageShareUrls.splice(index,1)
        },
        beforeAvatarUpload(file) {
            const isJPG = file.type === 'image/jpeg';
            const isPNG = file.type === 'image/png';
            const isLt2M = file.size / 1024 / 1024 < 2;
            if (!isJPG&&!isPNG) {
                this.$message.error('上传头像图片只能是 JPG 或者 PNG 格式!');
            }
            if (!isLt2M) {
                this.$message.error('上传头像图片大小不能超过 2MB!');
            }
            return (isJPG||isPNG) && isLt2M;
        }, 
        onSubmit(){
            if(!this.isRead){
                return false
            }
            this.$refs['ruleForm'].validate((valid) => {
                if (valid) {
                    if(!this.formData.id){
                        this.$axios.post_noqs(  
                            this.ports+'/applicationAgency/add', 
                            {...this.formData}, 
                            this
                        ).then(res=>{
                            if(res.success){
                                this.toastConfig.dialogVisible=false;
                                this.$refs.ruleForm.resetFields();
                                this.getList();
                            }
                            
                        })
                    }else{
                        this.$axios.post_noqs(  
                            this.ports+'/applicationAgency/update', 
                            {...this.formData}, 
                            this
                        ).then(res=>{
                            if(res.success){
                                this.toastConfig.dialogVisible=false;
                                this.$refs.ruleForm.resetFields();
                                this.getList();
                            }
                        })
                    }
                } else {
                    console.log('error submit!!');
                    return false;
                }
            }); 
        },
        getList(){
            this.$axios.get(  
                this.ports+'/applicationAgency/list', 
                {}, 
                this
            ).then(res=>{
                if(res.success){
                    this.appplicationList=res.data
                }
            })
        }
    },
    mounted(){
        this.getList()
        this.$axios.get(  //请求代理行业
            this.ports+'/applicationAgency/getAgencyType', 
            {}, 
            this
        ).then(res=>{
            if(res.success){
                this.businessTypeList=res.data
            }
        })
    }
}
</script>

<style lang="less">
    .agent{
        .agent_item{
            width: 220px;
            float: left;
            height: 140px;
            overflow: hidden;
            box-shadow:0px 4px 9px 0px rgba(0,0,0,0.04);
            border:1px solid #E5E5E5;
            margin: 16px;
            position: relative;
            cursor: pointer;
            .symbol{
                width: 85px;
                height: 24px;
                line-height: 24px;
                text-align: center;
                position: absolute;
                transform: rotate(-45deg);
                    top: 10px;
                left: -21px;
                font-size:12px;
                font-family:PingFang-SC-Medium;
                color:rgba(255,255,255,1);
                &.is-success{
                     background: #21C93D;
                }
                &.is-doing{
                    background: #0D6FF4;
                }
                &.is-error{
                    background: #7E7E7E;
                }
            }
            >div{
                position: absolute;
                width: 160px;
                left: 50%;
                top: 50%;
                transform: translate(-50%,-50%);
                p{
                    text-align: center;
                    >span{
                        display: flex;
                        margin: 10px auto;
                        .item_title{
                            width: 70px;
                            text-align: left;
                            display: inline-block;
                            font-size:16px;
                            font-family:MicrosoftYaHei;
                            color:rgba(102,102,102,1);
                        }
                        .item_detail{
                            flex: 1;
                            text-align: left;
                            overflow: hidden;
                            text-overflow: ellipsis;
                            white-space: nowrap;
                            padding-left: 10px;
                            font-size:16px;
                            font-family:MicrosoftYaHei;
                            color:rgba(51,51,51,1);
                        }
                    }
                    
                }
            }
            
        }
        .agent_item_plus{
            width: 220px;
            float: left;
            height: 140px;
            margin: 16px;
            text-align: center;
            line-height: 140px;
            box-shadow:0px 4px 9px 0px rgba(0,0,0,0.04);
            border:1px solid #E5E5E5;
        }
        .msg-tip{
            font-size:14px;
            font-family:MicrosoftYaHei;
            color:rgba(153,153,153,1);
            span{
                cursor: pointer;
                color:#0D6FF4;
            }
        }
        .submit-btn.isDisabled{
            opacity: 1;
            cursor: not-allowed;
            background: #a0cfff;
        }
        .protocol{
            margin-left: 15px;
            cursor: pointer;
            font-size:14px;
            font-family:MicrosoftYaHei;
            color:#0D6FF4; 
        }
        // .el-form-item{
        //     height: 32px;
        // }
        .el-form-item__label{
            padding: 0 20px 0 0;
        }
        .submit-btn{
            width:120px;
            height:32px;
            background:rgba(13,111,244,1);
            font-size:14px;
            font-family:MicrosoftYaHei;
            color:rgba(255,255,255,1);
            line-height:32px;
            text-align: center;
            cursor: pointer;
            margin-top: 20px;
        }
        .el-upload--picture-card{
            font-size: 28px;
            color: #8c939d;
            width: 160px;
            height: 100px;
            line-height: 100px;
            text-align: center;
        }
        .el-upload-list__item-actions{
            width: 160px;
            height: 100px;
            line-height: 100px;
            text-align: center;
        }
        .el-upload-list--picture-card .el-upload-list__item{
            font-size: 28px;
            color: #8c939d;
            width: 160px;
            height: 100px;
            line-height: 100px;
            text-align: center;
        }
        .el-upload-list--picture-card .el-upload-list__item-thumbnail{
            position: absolute;
            left: 0;
            top: 0;
        }
        .el-upload-list__item-status-label{
            i{
            position: absolute;
                top: 0px;
                left: 13px;
            }
        }
        .avatar {
            width: 160px;
            height: 100px;
            overflow: hidden;
            position: relative;
            border:1px solid rgba(229,229,229,1);
            img{
                position: absolute;
                left: 50%;
                top: 50%;
                transform: translate(-50%,-50%);
                width: 100%;
            }
        }
        .applicationResult{
            font-size:24px;
            font-family:MicrosoftYaHei;
            color:rgba(51,51,51,1);
            text-align: center;
            margin: 0 0 40px 0;
            font-weight: 300;
            .is-success{
                color: #21C93D;
            }
            .is-doing{
                color: #0D6FF4;
            }
            .is-error{
                color: #FF0000;
            }
        }
        .demo-ruleForm-item{
            display: inline-block;
            width: 200px;
            overflow: hidden;
            text-overflow: ellipsis;
            white-space: nowrap;
        }
    }
</style>




