<template>
    <div class="main-authentication">
        <load :isShow='isShow'></load>
        <component
            :is="currentComponent"
            @change="handlerChange"
            @submit="submit"
            @submit1="submit1"
            @btn="btn"
            @reAuthentication="reAuthentication"
            @reAuthentication1="reAuthentication1"
            @getDataSuceess="getDataSuceess"
            @getDataSuceess2="getDataSuceess2"
            :formData="informationForm"
            :formData2="enterpriseForm"
        ></component>
        <div class="clear"></div>
    </div>
</template>

<script>
import selection from './selection.vue'
import authentication from './authentication.vue'
import information from './information.vue'
import certification from './certification.vue'
import enterprise from './enterprise.vue'
import business from './business.vue'
import enterpriseInformation from './enterpriseInformation.vue'
import {mapState} from 'vuex'
    export default {
    data(){
        return{
            isShow:false,
            type:'add',
            currentComponent:"selection",
            enterpriseForm:{
                name:'',
                region:[],
                detailedAddress:'',
                contactPerson:'',
                contactPhone:'',
                legalPerson:'',
                legalPersonPhone:'',
                legalPersonIdentityCard:'',
                socialCreditCode:'',
                imgs:['',''],
                certificatePositiveShareUrl:'',
                certificateNegativeShareUrl:'',
                businessLicenceShareUrl:''
            },
            informationForm:{
                name:'',
                certificateType:'',
                certificate:'',
                // passport:'',
                // PassCheck:'',
                sex:'',
                region:[],
                imgs:['',''],
                detailedAddress:'',
                certificateNegativeShareUrl:'',
                certificatePositiveShareUrl:''
            },
            
        }
    },
    computed:{
        ...mapState(['userId','ports','authStatus','authType'])
    },
    methods:{
        getDataSuceess(data){
            this.informationForm=data
             this.informationForm.imgs=[data.certificatePositiveShareUrl,data.certificateNegativeShareUrl]
            this.informationForm.region=[data.province,data.city,data.district]
        }, 
        getDataSuceess2(data){
            this.enterpriseForm=data
            this.enterpriseForm.imgs=[data.certificatePositiveShareUrl,data.certificateNegativeShareUrl]
            this.enterpriseForm.region=[data.province,data.city,data.district]
        },
        btn(data){
            console.log(this.authType)
            if(data=='self'){
                if(this.authType!=1){
                    this.type='add'
                    this.informationForm={
                        name:'',
                        certificateType:'',
                        certificate:'',
                        // passport:'',
                        // PassCheck:'',
                        sex:'',
                        region:[],
                        imgs:['',''],
                        detailedAddress:'',
                        certificateNegativeShareUrl:'',
                        certificatePositiveShareUrl:''
                    }
                }else{
                    this.type='edit'
                }
                this.currentComponent='authentication'
            }else{
                if(this.authType!=2){
                    this.type='add'
                        this.enterpriseForm={
                        name:'',
                        region:[],
                        detailedAddress:'',
                        contactPerson:'',
                        contactPhone:'',
                        legalPerson:'',
                        legalPersonPhone:'',
                        legalPersonIdentityCard:'',
                        socialCreditCode:'',
                        imgs:['',''],
                        certificatePositiveShareUrl:'',
                        certificateNegativeShareUrl:'',
                        businessLicenceShareUrl:''
                    }
                }else{
                    this.type='edit'
                }
                
                this.currentComponent='enterprise'
            }
        },
        handlerChange(){
            this.currentComponent='selection'
        },
        submit(data){
            this.isShow=true
            var param=Object.assign({},this.informationForm)
            param.province=param.region[0]
            param.city=param.region[1]
            param.district=param.region[2]
            delete param.region
            delete param.imgs
            if(this.type=='add'){
                this.$axios.post_noqs(
                    this.ports + '/personalAuth/add',
                    {...param,userId:this.userId},
                    this
                ).then(res=>{
                    this.isShow=false
                    if(res.success){
                        this.$store.dispatch('set_navList',this)
                        this.$store.dispatch('get_status',this)
                    }
                })
            }else{
                delete param.certificateTypeDesc
                delete param.sexDesc
                this.$axios.post_noqs(
                    this.ports + '/personalAuth/update',
                    {...param},
                    this
                ).then(res=>{
                    this.isShow=false
                    if(res.success){
                        this.$store.dispatch('set_navList',this)
                        this.$store.dispatch('get_status',this)
                        // this.currentComponent='information'
                    }
                })
            }
            // this.currentComponent='information'
        },
        submit1(data){
            this.isShow=true
            var param=Object.assign({},this.enterpriseForm)
            param.province=param.region[0]
            param.city=param.region[1]
            param.district=param.region[2]
            delete param.imgs
            delete param.region
            if(this.type=='add'){
                this.$axios.post_noqs(
                    this.ports + '/enterpriseauth/add',
                    {userId:this.userId,...param},
                    this
                ).then(res=>{
                    this.isShow=false
                    if(res.success){
                        this.$store.dispatch('set_navList',this)
                        this.$store.dispatch('get_status',this)
                    }
                })
            }else{
                this.$axios.post_noqs(
                    this.ports + '/enterpriseauth/update',
                    {userId:this.userId,...param},
                    this
                ).then(res=>{
                    this.isShow=false
                    if(res.success){
                        //  this.currentComponent='enterpriseInformation'
                        this.$store.dispatch('set_navList',this)
                        this.$store.dispatch('get_status',this)
                    }
                })
            }
            
        },
        reAuthentication(data){
            this.currentComponent='selection'
            this.type='edit'
        },
        reAuthentication1(data){
            this.currentComponent='selection'
            this.type='edit'
        }
    },
    components:{
        selection,
        authentication,
        information,
        certification,
        enterprise,
        business,
        enterpriseInformation
    },
    watch:{
        authStatus:function(val){
            if(val==0&&this.authType==0){
                this.currentComponent='selection'
            }else if(val==1&&this.authType==1){
                this.currentComponent='certification'
            }else if(val==1&&this.authType==2){
                this.currentComponent='business'
            }else if(val==2&&this.authType==1){
                this.currentComponent='information'
            }else if(val==2&&this.authType==2){
                this.currentComponent='enterpriseInformation'
            }else if(val==3&&this.authType==1){
                this.currentComponent='certification'
            }else if(val==3&&this.authType==2){
                this.currentComponent='business'
            }
        }
    },
    mounted(){

        if(this.authStatus==0&&this.authType==0){
            this.currentComponent='selection'
        }else if(this.authStatus==1&&this.authType==1){
            this.currentComponent='certification'
        }else if(this.authStatus==1&&this.authType==2){
            this.currentComponent='business'
        }else if(this.authStatus==2&&this.authType==1){
            this.currentComponent='information'
        }else if(this.authStatus==2&&this.authType==2){
            this.currentComponent='enterpriseInformation'
        }else if(this.authStatus==3&&this.authType==1){
                this.currentComponent='certification'
            }else if(this.authStatus==3&&this.authType==2){
                this.currentComponent='business'
            }
        // this.$axios.post_noqs(
        //     this.ports + '/personalAuth/page',
        //     {userId:this.userId},
        //     this
        // ).then(res=>{
        //     if(res.success){
        //         if(res.data){
        //             this.certificationForm=res.data
        //         }else{
        //             this.currentComponent='information'
        //             this.hasEnter=false
        //         }
        //     }
        // }),
        // this.$axios.post_noqs(
        //     this.ports + '/enterpriseauth/page',
        //     {userId:this.userId},
        //     this
        // ).then(res=>{
        //     if(res.success){
        //         if(res.data){
        //             this.certificationForm=res.data
        //         }else{
        //             this.currentComponent='enterpriseInformation'
        //             this.hasEnter1=false
        //         }
        //     }
        // })
    }
}
</script>

<style lang="less">
    .main-authentication{
        position: relative;
        box-sizing:border-box;
        padding:20px 0;
    }
    .clear{
    	clear:both;
    }
</style>



