// 设备分布--地图部分
<template>
  <div class="receiver-content">
    <el-tabs v-model="activeName">
      <el-tab-pane label="设备分布" name="first">
        <div id="mapBox"></div>
         <transition name="slide-fade">
           <receiver-detail v-show="isToggle" class="toggle-menu" ref="receiverDetail"></receiver-detail>
         </transition>
        <div class="toggle">
          <i class="iconfont icon-zhankaicaidan" v-if="isToggle" @click="isToggle=!isToggle"></i>
          <div style="transform:rotate(180deg);transform-origin:50% 50%;" v-if="!isToggle">
            <i class="iconfont icon-zhankaicaidan" @click="isToggle=!isToggle"></i>
          </div>
        </div>
      </el-tab-pane>
    </el-tabs>
  </div>
</template>
<script>

import ReceiverDetail from "../../components/common/receiver_detail.vue";
import {
listEnterpriseCompany,
listEnterpriseMcatRunDevDistribution
} from "../../api/receiver.js";
export default {
  data() {
    return {
      isToggle: false,
      activeName: "first",
      markerArr: [], //标注json数组
      map: null,
      overlays: []
    };
  },
  components: {
    ReceiverDetail
  },
  mounted() {
    this.initMap();
    this.searchData();
  },
  methods: {
    // 查询数据
    searchData(filterArr, checked) {
      let arr = [];
      // 后台请求到的数据

      listEnterpriseMcatRunDevDistribution({
        Vue: this,
        params: {}
      }).then(res => {
        let result = [];
        // 根据是否同步筛选出公司名称一致的数据
        if (checked) {
          result = res.model.filter(item => {
            let i = filterArr.findIndex(value => {
              return value.companyName === item.companyName;
            });
            if (i != -1) {
              return true;
            }
          });
        } else {
          result = res.model;
        }
        result.forEach(obj => {
          // obj.longitude = 119.417854;
          // obj.latitude = 39.921988;
          arr.push(this.initData(obj));
        });
        // 清除所有覆盖物
        this.map.clearOverlays();
        this.setMarker(arr);
      });
      // 以下为模拟数据
      // let data = [
      //   {
      //     companyId: 1,
      //     companyName: "XXXXXXX公司",
      //     longitude: 119.417854,
      //     latitude: 39.921988,
      //     count: 10,
      //     infos: [
      //       {
      //         modelId: 1,
      //         modelName: "设备1",
      //         sumDevs: 100,
      //         onLineDevs: 99,
      //         faultDevs: 0
      //       },
      //       {
      //         modelId: 2,
      //         modelName: "设备2",
      //         sumDevs: 100,
      //         onLineDevs: 99,
      //         faultDevs: 0
      //       }
      //     ]
      //   },
      //   {
      //     companyId: 1,
      //     companyName: "设备3",
      //     longitude: 116.406605,
      //     latitude: 39.921585,
      //     count: 10,
      //     infos: [
      //       {
      //         modelId: 1,
      //         modelName: "设备4",
      //         sumDevs: 100,
      //         onLineDevs: 9900,
      //         faultDevs: 0
      //       },
      //       {
      //         modelId: 2,
      //         modelName: "经编机",
      //         sumDevs: 10000,
      //         onLineDevs: 99,
      //         faultDevs: 0
      //       }
      //     ]
      //   }
      // ];
      // data.forEach(obj => {
      //   arr.push(this.initData(obj));
      // });
      // // 清除所有覆盖物
      // this.map.clearOverlays();
      // this.setMarker(arr);
    },
    // 初始化数据格式
    initData(data) {
      let obj = new Object();
      let content = "";
      if (data.infos.length > 0) {
        data.infos.forEach(value => {
          let str1 =
            "<div style='width:120px;display: inline-block;'>" +
            value.modelName +
            " : " +
            value.sumDevs +
            "</div>";
          let str2 =
            "<div style='width:90px;display: inline-block;'>在线 : " +
            value.onLineDevs +
            "</div>";
          let str3 =
            "<div style='display: inline-block;'>故障 : " +
            value.faultDevs +
            "</div>";
          content +=
            "<div style='line-height:10px;height:10px'>" +
            str1 +
            str2 +
            str3 +
            "</div>,";
        });
        obj = {
          lng: data.longitude,
          lat: data.latitude,
          total: data.count,
          type: "red",
          content:
            '<div style="text-align:center">' +
            data.companyName +
            "</div>," +
            '<div style="text-align:left;font-size: 14px;margin-left: 15px;">' +
            content +
            "</div>"
        };
        return obj;
      }
    },
    // 初始化地图
    initMap() {
      this.map = new BMap.Map("mapBox"); // 创建Map实例
      this.map.centerAndZoom(new BMap.Point(108.95, 37.38), 5); // 初始化地图,设置中心点坐标和地图级别
      this.map.setCurrentCity("北京"); // 设置地图显示的城市 此项是必须设置的
      this.map.enableScrollWheelZoom(true); //开启鼠标滚轮缩放
    },
    // 初始化标注
    initMarker(data) {
      let myIcon = null;
      let pt = new BMap.Point(data.lng, data.lat);
      if (data.type == "red") {
        myIcon = new BMap.Icon(
          require("../../assets/img/red_marker.png"),
          new BMap.Size(47, 52)
        );
      } else {
        myIcon = new BMap.Icon(
          require("../../assets/img/bule_marker.png"),
          new BMap.Size(47, 52)
        );
      }
      return new BMap.Marker(pt, { icon: myIcon });
      //  return new BMap.Marker(pt);
    },
    // 初始化文字标签
    initLabel(data) {
      let opts = {
        position: new BMap.Point(data.lng, data.lat), // 指定文本标注所在的地理位置
        offset: new BMap.Size(-8, -25) //设置文本偏移量
      };
      if (data.total < 10) {
        opts.offset = new BMap.Size(-4, -18);
      } else if (data.total >= 10 && data.total < 100) {
        opts.offset = new BMap.Size(-8, -18);
      } else if (data.total >= 100 && data.total < 1000) {
        opts.offset = new BMap.Size(-12, -18);
      }
      let label = new BMap.Label(data.total, opts); // 创建文本标注对象
      label.setStyle({
        color: "#fff",
        border: "none",
        fontSize: "12px",
        height: "20px",
        lineHeight: "20px",
        fontFamily: "微软雅黑",
        background: "rgba(0,0,0,0)"
      });
      return label;
    },
    //   初始化信息窗口
    initInfoWindow(data) {
      let str = data.content.replace(/,/g, "<br/>"); //自定义窗口内容
      return new BMap.InfoWindow(str, {
        offset: {
          //偏移量
          width: 5,
          height: -20
        },
        width: 300,
        enableCloseOnClick: true
      });
    },
    // 添加标注点、文字标签、信息窗口
    setMarker(arr) {
      this.markerArr = [];
      let markers = [];
      arr.forEach(data => {
        this.markerArr.push({
          data: data,
          point: new BMap.Point(data.lng, data.lat)
        });
      });
      this.setInfoWindow();
    },
    // 添加标注事件
    setInfoWindow() {
      // 清除地图上标注,文字标签
      this.overlays.forEach(value => {
        this.map.removeOverlay(value);
      });
      // 往地图添加标注点，与信息窗口
      this.markerArr.forEach(opt => {
        let infoWindow = this.initInfoWindow(opt.data); // 创建信息窗口对象
        let marker = this.initMarker(opt.data); //创建标注点
        let label = this.initLabel(opt.data); //创建文字标签
        this.map.addOverlay(marker);
        this.map.addOverlay(label);
        this.overlays.push(marker, label);
        // 监听打开信息窗口
        let this_ = this;
        marker.addEventListener("click", function() {
          this_.map.openInfoWindow(infoWindow, opt.point); //开启信息窗口
          this_.map.removeOverlay(marker);
          opt.data.type = "blue";
          this_.setInfoWindow();
          $(".BMap_pop>img").hide();
        });
        // 监听关闭信息窗口
        infoWindow.addEventListener("close", function() {
          opt.data.type = "red";
          this_.setInfoWindow();
        });
      });
    }
  }
};
</script>
<style lang="less">
@import url("../../assets/css/map/iconfont.css");
.receiver-content {
  overflow: hidden;
  width: 100%;
  min-height: 900px;
  position: relative;
  .slide-fade-enter-active {
    transition: all 0.8s ease;
  }
  .slide-fade-leave-active {
    transition: all 0.3s cubic-bezier(1, 0.5, 0.8, 1);
  }
  .slide-fade-enter, .slide-fade-leave-to
/* .slide-fade-leave-active for below version 2.1.8 */ {
    transform: translateX(600px);
    opacity: 0;
  }
  .el-tabs__active-bar {
    padding: 0 20px;
  }
  .el-tabs__nav {
    padding: 0 20px;
  }
  .el-tabs__content {
    overflow: hidden;
    position: relative;
    padding: 10px 20px 20px;
    box-sizing: border-box;
    height: 840px;
    width: 100%;
  }
  .el-tab-pane {
    height: 100%;
    width: 100%;
  }
  #mapBox {
    height: 100%;
    width: 100%;
  }
  .toggle-menu {
    position: absolute;
    right: 20px;
    top: 50%;
    transform: translateY(-50%);
  }
  .toggle {
    width: 30px;
    height: 80px;
    background: rgba(0, 0, 0, 0.64);
    position: absolute;
    right: 20px;
    top: 50%;
    transform: translateY(-50%);
  }
  > div {
    width: 100%;
    height: 100%;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    text-align: center;
  }
}
</style>



