<template>
    <div class="fillInForm">
        <el-form :model="formData" :rules="rules" ref="ruleForm" label-width="120px" class="demo-ruleForm" size="small">
            <el-form-item label="企业名称" prop="name">
                <el-input  v-model="formData.name"></el-input>
            </el-form-item>
            <el-form-item label="企业性质" prop="property">
                <el-select  v-model="formData.property" placeholder="请选择企业性质">
                  <el-option label="国有企业" :value="0"></el-option>
                  <el-option label="三资企业" :value="1"></el-option>
                  <el-option label="私营企业" :value="2"></el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="所属行业" prop="trade">
                <el-select  v-model="formData.trade" placeholder="请选择所属行业">
                  <el-option :label="item" :value="index+13" v-for="(item,index) in tradeList"></el-option>
                  <!-- <el-option label="加工类行业" :value="1"></el-option> -->
                </el-select>
            </el-form-item>
            <el-form-item label="所在地区" prop="adress">
                  <area-select @change="handlerAdressChange" :level="2" type="text" v-model="formData.adress" :data="pcaa" ></area-select>
            </el-form-item>
            <el-form-item label="详细地址" >
                <el-input  v-model="formData.detailedAddress"></el-input>
            </el-form-item>
            <el-form-item label="联系人" prop="contactPerson">
                <el-input  v-model="formData.contactPerson"></el-input>
            </el-form-item>
            <el-form-item label="联系方式" prop="contactPhone">
                <el-input  v-model="formData.contactPhone"></el-input>
            </el-form-item>
            <el-form-item label="员工规模" prop="employeeScale">
                <el-select  v-model="formData.employeeScale" placeholder="请选择员工规模">
                  <el-option label="0~20人" :value="0"></el-option>
                  <el-option label="20~100人" :value="1"></el-option>
                  <el-option label="大于100人" :value="2"></el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="设备总数" prop="equipmentScale">
                <el-input-number :min="0" :controls="false" style="width:100%;"  v-model="formData.equipmentScale"></el-input-number>
            </el-form-item>
            <el-form-item label="上云设备数" prop="onIcloudScale">
                <el-input-number :min="0" :controls="false" style="width:100%;" v-model="formData.onIcloudScale"></el-input-number>
            </el-form-item>
            <el-form-item label="企业场所" prop="siteImageShareUrl">
                <el-upload
                    class="avatar-uploader"
                    action="http://61.153.187.80:8081/fileSystem/uploadFile"
                    :show-file-list="false"
                    :on-success="uploadSuccess"
                    :before-upload="beforeAvatarUpload">
                    <!-- {{formData.siteImageShareUrl}} -->
                    <img v-if="formData.siteImageShareUrl" :src="formData.siteImageShareUrl" class="avatar">
                    <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                    <div slot="tip" class="el-upload__tip">请上传一张图片，仅支持JPG、PNG格式图片</div>
                </el-upload>
            </el-form-item>
            <el-form-item>
                <el-checkbox v-model="isRead"> </el-checkbox><span style="margin-left:10px;">我已阅读并同意</span><span class="protocol">隐私协议与诚信协议</span>
                <div @click="onSubmit" class="submit-btn" :class="{'isDisabled':!isRead}">立即创建</div>
            </el-form-item>
        </el-form>
    </div>
</template>
<script>
import rule from "../../assets/js/rule.js";
import {mapState} from 'vuex'
import { pca, pcaa } from "area-data";
export default {
  data() {
    return {
      tradeList:['农副食品加工业','食品制造业','酒、饮料和精制茶制造业','烟草制品业','纺织业','纺织服装、服饰业','皮革、毛皮、羽毛及其制品和制鞋业','木材加工和木、竹、藤、棕、草制品业','21家具制造业','造纸和纸制品业','印刷和记录媒介复制业','文教、工美、体育和娱乐用品制造业','石油、煤炭及其他燃料加工业','化学原料和化学制品制造业','医药制造业','化学纤维制造业','橡胶和塑料制品业',
'非金属矿物制品业','黑色金属冶炼和压延加工业','有色金属冶炼和压延加工业','金属制品业','通用设备制造业','专用设备制造业','汽车制造业','铁路、船舶、航空航天和其他运输设备制造业'
,'电气机械和器材制造业','计算机、通信和其他电子设备制造业','仪器仪表制造业','其他制造业','废弃资源综合利用业','金属制品、机械和设备修理业' ],
      pcaa: pcaa,
      rules: {
        name: rule.require("string", "请输入企业名称"),
        adress: rule.require("array","请选择所在地区"),
        property: rule.require("number", "请选择企业性质"),
        trade: rule.require("string","请选择所属行业"),
        contactPerson: rule.require("string", "请输入联系人"),
        contactPhone: [
          { required: true, message: '请输入联系方式', trigger: 'change' },
          { validator: rule.phone, trigger: 'blur' }
        ],
        employeeScale: rule.require("string","请选择员工规模"),
        siteImageShareUrl: rule.require("string", "请至少上传一张图片"),
        equipmentScale: rule.require("number", "请输入设备总数"),
        onIcloudScale: rule.require("number", "请输入上云设备数"),
      },
      // 是否已阅读协议
      isRead: false
    };
  },
  computed:{
    ...mapState(['ports'])
  },
  props: ["formData","operateType"],
  methods: {
    handlerAdressChange(data){
      this.$refs.ruleForm.validateField('adress')
    },
    uploadSuccess(file) {
      console.log(file);
      this.$set(this.formData, "siteImageShareUrl", file.model);
      this.$refs.ruleForm.validateField("siteImageShareUrl");
      console.log(this.formData);
      // this.formData.siteImageShareUrl=file.model
    },
    beforeAvatarUpload(file) {
      const isJPG = file.type === "image/jpeg";
      const isPNG = file.type === "image/png";
      const isLt2M = file.size / 1024 / 1024 < 2;
      console.log(file);
      if (!isJPG && !isPNG) {
        this.$message.error("上传头像图片只能是 JPG 或者 PNG 格式!");
      }
      if (!isLt2M) {
        this.$message.error("上传头像图片大小不能超过 2MB!");
      }
      return (isJPG || isPNG) && isLt2M;
    },
    onSubmit() {
      if (!this.isRead) {
        return false;
      }
      this.$refs["ruleForm"].validate(valid => {
        if (valid) {
          if(this.operateType=='add'){
            var obj={...this.formData}
            obj.province=obj.adress[0];
            obj.city=obj.adress[1];
            obj.district=obj.adress[2];
            delete obj.adress
            this.$axios.post_noqs(
                this.ports + '/onicloud/add',
                {...obj},
                this
            ).then(res=>{
                if(res.success){
                  console.log(res,'----')
                    this.$emit("onSubmit",res.data);
                }
            })
          }else{
            var obj={};
            obj={
              id:this.formData.id,
              name: this.formData.name,
              province:this.formData.province,
              city:this.formData.city,
              district:this.formData.district,
              property:this.formData.property,
              trade: this.formData.trade,
              contactPerson:this.formData.contactPerson,
              contactPhone:this.formData.contactPhone,
              employeeScale: this.formData.employeeScale,
              siteImageShareUrl: this.formData.siteImageShareUrl,
              equipmentScale:this.formData.equipmentScale,
              detailedAddress:this.formData.detailedAddress,
              onIcloudScale: this.formData.onIcloudScale,
            }
            this.$axios.post_noqs(
                this.ports + '/onicloud/update',
                {...obj},
                this
            ).then(res=>{
                if(res.success){
                  console.log(res,'----')
                    this.$emit("onSubmit",res.data);
                }
            })
          }
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    }
  }
};
</script>
<style lang="less">
.fillInForm {
  margin-top: 0px;
  width: 440px;
  .el-input-number .el-input input{
    text-align: left;
  }
  .submit-btn.isDisabled {
    opacity: 1;
    cursor: not-allowed;
    background: #a0cfff;
  }
  .protocol {
    cursor: pointer;
    font-size: 14px;
    font-family: MicrosoftYaHei;
    margin-left: 10px;
    color: #0d6ff4;
  }
  .el-form-item__label {
    padding: 0 20px 0 0;
  }
  .submit-btn {
    width: 220px;
    height: 44px;
    background: rgba(13, 111, 244, 1);
    font-size: 14px;
    font-family: MicrosoftYaHei;
    color: rgba(255, 255, 255, 1);
    line-height: 44px;
    text-align: center;
    cursor: pointer;
    margin-top: 30px;
  }
  .avatar-uploader {
    margin-top: 4px;
  }
  .avatar-uploader .el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
  }
  .avatar-uploader .el-upload:hover {
    border-color: #409eff;
  }
  .avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 240px;
    height: 140px;
    line-height: 140px;
    text-align: center;
  }
  .avatar {
    width: 240px;
    height: 140px;
    display: block;
  }
  .el-upload__tip {
    margin-top: -10px;
  }
}
</style>



