<template>
  <div id="app">
   <!--  <header>
        <div class="user-img left">
          <img src="./assets/logo.png" width="100%" height="100%">
        </div>
        <div class="user-msg left">
          <p>
            <span class="user-name">{{userName}}</span>
            <span class="user-status" :style="{'color':authType==0?'rgba(153,153,153,1)':'#EC6A1A','border-color':authType==0?'rgba(153,153,153,1)':'#EC6A1A'}">{{getAuthType(authType)}}</span>
          </p>
          <p>
            上次登陆时间：{{loginTime}}
          </p>
        </div>
    </header> -->
    <div class="my-container">
      <aside class="left qw_navbox">
          <dl v-for="item in navList">
            <dt>{{item.title}}</dt>
            <dd v-for="child in item.childrens" @click="$store.commit('set_select',child)" :class="{'active':select.link==child.link}">
              <router-link :to="child.link">{{child.name}}</router-link>
            </dd>
          </dl>
      </aside>
      <div class="my-content left">
          <div class="r" style="background:#FFF; box-shadow:1px 1px 10px rgba(0,0,0,.05);">
              <router-view></router-view>
          </div>
      </div>
    </div>
  </div>
</template>

<script>
import {mapState} from 'vuex'
export default {
  name: 'App',
  data(){
    return{
      userName:'',
      // authType:0,
      loginTime:'',
      // navList:[
      //   {
      //     title:'账号管理',
      //     childrens:[
      //       {
      //         name:'企业信息',
      //         link:'message'
      //       },
      //       {
      //         name:'个人信息',
      //         link:'owner'
      //       },
      //       {
      //         name:'认证中心',
      //         link:'authentication'
      //       },
      //       {
      //         name:'安全设置',
      //         link:'safety'
      //       }
      //     ]
      //   },
      //   {
      //     title:'应用管理',
      //     childrens:[
      //       {
      //         name:'我的应用',
      //         link:'app'
      //       },
      //       {
      //         name:'申请中心',
      //         link:'application'
      //       },
      //        {
      //         name:'入驻审批',
      //         link:'approvalCenter'
      //       }
      //     ]
      //   },
      //   {
      //     title:'订单管理',
      //     childrens:[
      //       {
      //         name:'订单信息',
      //         link:'orderingInfor'
      //       },
      //       {
      //         name:'收货人管理',
      //         link:'receiver'
      //       },
      //       {
      //         name:'收货地址管理',
      //         link:'shippingAddress'
      //       }
      //     ]
      //   }
      // ]
    }
  },
  methods:{
      getAuthType(type){
        switch(type)
        {
          case 0:
          return '未认证'
          break;
          case 1:
          return '个人认证'
          break;
          case 2:
          return '企业认证'
          break;
        } 
      }
  },
  computed:{
    ...mapState({
      select:'select',
      navList:'navList',
      authType:'authType'
    })
  },
  props:['ports','userId'],
  created(){
    // console.log('----------11111111111111111----------')
    window.localStorage.setItem('pocPort',this.ports)
    console.log('ports====>',this.ports,'userId=====>',this.userId);
    // this.$router.push('/authentication')
    console.log(this.$router.currentRoute.path.slice(1,this.$router.currentRoute.path.length-1))
    this.$store.commit('set_select',{ name:this.$router.currentRoute.name,link:this.$router.currentRoute.path.slice(1,this.$router.currentRoute.path.length)})
    this.$store.commit('set_ports',this.ports)
    this.$store.commit('set_userId',this.userId)
    this.$store.dispatch('set_navList',this)
    this.$store.dispatch('get_status',this)
  },
  watch:{
    $route(to,from){
      console.log(to)
      this.$store.commit('set_select',{ name:to.name,link:to.path.slice(1,to.path.length)})
    }
  }
}
</script>

<style lang="less" scoped>
     .BMap_cpyCtrl {
      display: none;
    }
  
    .anchorBL {
      display: none;
    }
  
  #app{
      width: 1200px;
      margin: 20px auto 0;
      header{
        height: 160px;
        line-height: 160px;
        background: #F5F5F5;
        .user-img{
          width: 100px;
          height: 100px;
          margin-left: 60px;
        }
        .user-msg{
          margin-top: 30px;
          margin-left: 36px;
          height: 100px;
          p{
            height: 50px;
            line-height: 50px;
            font-size:14px;
            font-family:MicrosoftYaHei;
            color:rgba(102,102,102,1);
            .user-name{
              font-size:24px;
              font-family:MicrosoftYaHei;
              color:rgba(51,51,51,1);
            }
            .user-status{
              width:60px;
              height:24px;
              margin-left: 17px;
              border:1px solid #999999;
              padding:5px 10px;
              border-radius:3px;
              font-size:12px;
              font-family:MicrosoftYaHei;
              color:rgba(153,153,153,1);
              line-height:24px; 
            }
          }
        }
      }
      .my-container{
        // height: 700px;
        margin-top: 20px;
        display: flex;
        aside{
          height: 700px;
          width: 220px;
          background: #FFF;
          box-shadow:1px 1px 10px rgba(0,0,0,.05);
          dt{
            margin-top: 20px;
            font-size:18px;
            font-family:MicrosoftYaHei-Bold;
            color:rgba(51,51,51,1);
            height: 50px;
            line-height: 50px;
            font-weight: 900;
            padding-left: 60px;
          }
          dd{
            a{
              width: 100%;
              display: inline-block;
              padding-left: 60px;
              font-size:14px;
              font-family:MicrosoftYaHei;
              color:rgba(51,51,51,1);
              height: 30px;
              line-height: 30px;
              text-decoration: none;
            }
            &.active a{
              color: #0D6FF4;
            }
          }
        }
        .my-content{
          flex:1;
          height: 100%;
          padding-left:40px;
          // background: red;
        }
      }
    }
</style>



