<template>
    <div class="time-search">
        <el-button v-for="(item,index) in buttons" size="mini" :class="{active: itemIndex==index}" @click="handleFun(item.value,index)">{{item.name}}</el-button>
        <div>
            <el-date-picker size="mini" type="daterange" v-model="timeData" value-format="yyyy-MM-dd hh:mm:ss"></el-date-picker>
        </div>
    </div>
</template>
<script>
export default {
    data() {
        return {
            itemIndex: -1,
            timeData: [],
            value : '',
            buttons: [{
                name: '今天',
                value: 'day'
            },{
                name: '本周',
                value: 'week'
            },{
                name: '本月',
                value: 'month'
            },{
                name: '本年',
                value: 'year'
            }]
        }
    },

    methods: {
        handleFun(value,index) {
            this.timeData = [];
            this.itemIndex = index;
            this.value = value;
        }
    },

    watch: {
        "timeData": function(val) {
            if(val && val.length == 2) {
                this.itemIndex = -1;
                this.$emit('timeClick',val);
            }else {
                this.$emit('timeClick',this.value);
            }
        }
    }
}
</script>

<style lang="less">
    .time-search {

        .el-button,
        .el-date-editor {
            width: 60px;
            height: 30px;
            border: 1px solid #BFC6CC;
            border-radius: 2px;
            color: #565656;
            margin-right: 8px;
        }

        .el-button {
            position: relative;
            .close {
                position: absolute;
                top: -8px;
                right: -8px;
                font-size: 14px;
                display: inline-block;
                border: 1px solid #0C68F1;
                border-radius: 50%;
                width: 15px;
                height: 15px;
                background: #ecf5ff;
                color: #0C68F1;
                display: none;
            }
        }

        .active {
            border-color: #0C68F1;
            color: #0C68F1;
        }

        .active:hover .close {
            display: block;
        }

        .el-date-editor {
            width: 300px;
            margin-left: 10px;
            top: 1px;
        }

        div {
            display: inline-block;
        }
    }
</style>



