<template>
    <div class="authentication">
        <el-form :model="formData" :rules="rules" ref="ruleForm" label-width="200px" class="demo-ruleForm">
            <el-form-item label="真实姓名" prop="name">
                <el-input size="medium" v-model="formData.name" placeholder="请输入真实姓名"></el-input>
            </el-form-item>
            <el-form-item label="证件类型" prop="certificateType">
                <el-select size="medium" v-model="formData.certificateType" placeholder="请选择证件类型" @change="formData.certificate=''" style="width:100%">
                  <el-option label="身份证" :value="0"></el-option>
                  <el-option label="护照" :value="1"></el-option>
                  <el-option label="港澳台通行证" :value="2"></el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="证件号码" prop="certificate" v-if="formData.certificateType==0">
                <el-input size="medium" v-model="formData.certificate" placeholder="请输入证件号码"></el-input>
            </el-form-item>
            <el-form-item label="护照" prop="certificate" v-if="formData.certificateType==1">
                <el-input size="medium" v-model="formData.certificate" placeholder="请输入护照"></el-input>
            </el-form-item>
            <el-form-item label="港澳台通行证" prop="certificate" v-if="formData.certificateType==2">
                <el-input size="medium" v-model="formData.certificate" placeholder="请输入港澳台通行证"></el-input>
            </el-form-item>
            <el-form-item label="性别" prop="sex">
                <el-radio-group v-model="formData.sex">
                    <el-radio :label="1">男</el-radio>
                    <el-radio :label="0">女</el-radio>
                </el-radio-group>
            </el-form-item>
            <el-form-item label="所在地区" prop="region">
                  <area-select :level="2" type="text" v-model="formData.region" :data="pcaa" ></area-select>
            </el-form-item>
            <el-form-item label="详细地址" prop="detailedAddress">
                <el-input size="medium" v-model="formData.detailedAddress" placeholder="请输入详细地址"></el-input>
            </el-form-item>
            <el-form-item label="证件图片" prop="imgs">
                <div style="margin-top:5px;">
                    <el-upload
                        class="avatar-uploader"
                        action="http://61.153.187.80:8081/fileSystem/uploadFile"
                        :show-file-list="false"
                        :on-success="uploadSuccess1"
                        :before-upload="beforeAvatarUpload">
                        <img v-if="formData.certificatePositiveShareUrl" :src="formData.certificatePositiveShareUrl" class="avatar">
                        <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                        <div slot="tip" class="el-upload__tip">身份证正面</div>
                        <div slot="tip" class="el-upload__tipmain">注：仅支持JPG、PNG格式图片</div>
                    </el-upload>
                    <el-upload
                        class="avatar-uploader uploader-right"
                        action="http://61.153.187.80:8081/fileSystem/uploadFile"
                        :show-file-list="false"
                        :on-success="uploadSuccess2"
                        :before-upload="beforeAvatarUpload">
                        <img v-if="formData.certificateNegativeShareUrl" :src="formData.certificateNegativeShareUrl" class="avatar">
                        <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                        <div slot="tip" class="el-upload__tip">身份证反面</div>
                    </el-upload>

                </div>
                
            </el-form-item>
            <el-form-item style="margin-top:50px;">
                <el-checkbox v-model="isRead"> </el-checkbox><span style="margin-left:10px;">我已阅读并同意</span><span class="protocol">隐私协议与诚信协议</span>
                <div @click="onSubmit" class="submit-btn" :class="{'isDisabled':!isRead}">提交</div>
            </el-form-item>
        </el-form>
    </div>
</template>

<script>
import rule from '../../assets/js/rule.js'
import { AreaSelect } from "vue-area-linkage";
import { pca, pcaa } from "area-data";
import {mapState} from 'vuex'
    export default {
        data(){
            var myValidator=(rule, value, callback) => {
                if(this.formData.certificateType==0){
                    if (
                    !/^(^[1-9]\d{7}((0\d)|(1[0-2]))(([0|1|2]\d)|3[0-1])\d{3}$)|(^[1-9]\d{5}[1-9]\d{3}((0\d)|(1[0-2]))(([0|1|2]\d)|3[0-1])((\d{4})|\d{3}[Xx])$)$/.test(
                        value
                    )
                    ) {
                        return callback(new Error("请输入正确的身份证号码"));
                    } else {
                        callback();
                    }
                }else if(this.formData.certificateType==1){
                    var re1 = /^[a-zA-Z]{5,17}$/;
                    var re2 = /^[a-zA-Z0-9]{5,17}$/
                    if(
                        !re1.test(value)&&!re2.test(value)

                    ) {
                        return callback(new Error("请输入正确的护照信息"));
                    } else {
                        callback();
                    }
                }else{
                    if(
                        !/^[a-zA-Z0-9]{5,21}$/.test(value)
                    ){
                        return callback(new Error("请输入正确的港澳台通行证信息"));
                    } else {
                        callback();
                    }
                }
                
            };
            var myValidator2=(rule, value, callback)=>{
                if(value[0]==''||value[1]==''){
                    return callback(new Error("请上传身份证正反面信息"));
                }else {
                    callback();
                }
            };
            return{
                pcaa: pcaa,
                radio:'',
                rules:{
                    name:[
                        { required: true, message: '此项为不能为空', trigger: 'blur' },
                        { validator: rule.name, trigger: 'blur' }
                    ],
                    certificateType:rule.require(),
                    sex:rule.require(),
                    certificate:[
                        { required: true, message: '此项为必填项', trigger: 'blur' },
                        { validator: myValidator, trigger: 'blur' }
                    ],
                    // passport:[
                    //     { validator: rule.passport, trigger: 'blur' }
                    // ],
                    // PassCheck:[
                    //     { validator: rule.PassCheck, trigger: 'blur' }
                    // ],
                    imgs:[
                        { required: true,type:'array', message: '此项为必填项', trigger: 'blur' },
                        { validator: myValidator2, trigger: 'blue' }
                    ],
                    region:rule.require('array'),
                    detailedAddress:rule.require()
                },
                region:['province','city','district'],
                isRead:false
            }
        },
        props:['formData'],
        computed:{
             ...mapState(['userId','ports']),
            // list:function(){
            //     var ary=[]
            //     for(let d of this.formData.dataImageShareUrl){
            //         ary.push({
            //             name:d,
            //             url:d
            //         })
            //     }
            //     return ary
            // }
        },
        components:{
            AreaSelect: AreaSelect
        },
        methods:{
            uploadSuccess1(file){
                console.log(file)
                this.formData.imgs[0]=file.model
                this.formData.certificatePositiveShareUrl=file.model
                this.$refs.ruleForm.validateField('imgs')
            },
            uploadSuccess2(file){
                this.formData.imgs[1]=file.model
                this.formData.certificateNegativeShareUrl=file.model
                this.$refs.ruleForm.validateField('imgs')
            },
            beforeAvatarUpload(file) {
                const isJPG = file.type === 'image/jpeg';
                const isPNG = file.type === 'image/png';
                const isLt2M = file.size / 1024 / 1024 < 2;
                console.log(file)
                if (!isJPG&&!isPNG) {
                    this.$message.error('上传头像图片只能是 JPG 或者 PNG 格式!');
                }
                if (!isLt2M) {
                    this.$message.error('上传头像图片大小不能超过 2MB!');
                }
                return (isJPG||isPNG) && isLt2M;
            },
            onSubmit(){
                if(!this.isRead){
                    return false
                }
                this.$refs['ruleForm'].validate((valid) => {
                    if (valid) {
                        this.$emit('submit')
                    } else {
                        console.log('error submit!!');
                        return false;
                    }
                }); 
            }
        },
        watch:{
            'formData.region':function(){
                this.$refs.ruleForm.validateField('region')
            }
        }
    }
</script>

<style lang="less">
    .el-upload__input{
        display: none!important;
    }
    .authentication{
        margin-top: 30px;
        width: 700px;
        .submit-btn.isDisabled{
            opacity: 1;
            cursor: not-allowed;
            background: #a0cfff;
        }
        .el-form-item__label{
            padding-right: 38px;
        }
        .submit-btn{
            width:220px;
            height:44px;
            background:rgba(13,111,244,1);
            font-size:14px;
            font-family:MicrosoftYaHei;
            color:rgba(255,255,255,1);
            line-height:44px;
            text-align: center;
            margin-top: 4px;
        cursor: pointer;
        }
        .avatar-uploader{
            width: 242px;
            height: 187px;
            float: left;
        }
        .uploader-right{
            margin-left: 15px;
        }
        .avatar-uploader .el-upload {
            border: 1px dashed #d9d9d9;
            border-radius: 6px;
            cursor: pointer;
            position: relative;
            overflow: hidden;
        }
        .avatar-uploader .el-upload:hover {
            border-color: #409EFF;
        }
        .avatar-uploader-icon {
            font-size: 28px;
            color: #8c939d;
            width: 240px;
            height: 140px;
            line-height: 140px;
            text-align: center;
        }
        .avatar {
            width: 240px;
            height: 140px;
            display: block;
        }
        .el-upload__tip{
            margin-top: -10px;
            text-align: center;
        }
        .el-upload__tipmain{
            margin-top: -5px;
            font-size:14px;
            font-family:MicrosoftYaHei;
            color:rgba(153,153,153,1);
        }
        .el-form{
            .area-selected-trigger{
                padding: 0px 20px 7px 12px;
            }
            .area-select-wrap{
                margin-top: 0px;
            }
            .area-select-wrap .area-select:nth-child(1){
                margin-left: 0px;
            }
            .area-select-wrap .area-select{
                margin-left: 2px;
                line-height: 32px;
                margin-top: 3px;
            }
        }
         .protocol{
             font-size:14px;
            font-family:MicrosoftYaHei;
            color:#0D6FF4;
         }
    }
</style>




