<template>
<div class="shippingAddress">
	<el-tabs v-model="activeName" @tab-click="handleClick">
	    <el-tab-pane label="远程控制" name="one">
	    	<div class="search">
	    	<div class="searchSim" v-show="searchMod==0">
	    		<el-form ref="searchSim" v-model="searchSim.searchData" :rules="searchSim.rule" label-width="76px">
	    			<el-form-item label="设备名称">
	    				<el-input v-model="searchSim.searchData.modelName" placeholder="请输入设备名称"></el-input>
	    			</el-form-item>
	    			<el-form-item label="品牌">
	    				<el-input v-model="searchSim.searchData.manufactory" placeholder="请输入设备品牌"></el-input>
	    			</el-form-item>
	    			<el-form-item label="型号">
	    				<el-input v-model="searchSim.searchData.spec" placeholder="请输入设备型号"></el-input>
	    			</el-form-item>
	    			<!-- <el-form-item label="出厂年月">
	    				<el-input v-model="searchSim.searchData.spec" placeholder="请输入出厂年月"></el-input>
	    			</el-form-item> -->
	    			<el-form-item label="实时状态" style="width:276px;">
	    				<el-radio-group v-model="searchSim.searchData.onlineStatus">
							<el-radio :label="0">全部</el-radio>
							<el-radio :label="1">在线</el-radio>
							<el-radio :label="2">离线</el-radio>
						</el-radio-group>
	    			</el-form-item>
	    			<span class="qw_btn blue" @click="doSearchSim">查询</span>
	    			<span class="qw_btn" @click="resetSearchSim">重置</span>
	    			<span class="qw_btn" @click="searchMod=1">高级查询</span>
	    		</el-form>
	    	</div>
	    	<div class="searchHigh" v-show="searchMod==1">
	    		<el-form ref="searchHigh" v-model="searchHigh.searchData" :rules="searchHigh.rule" label-width="76px">
	    			<el-form-item label="设备名称">
	    				<el-input v-model="searchHigh.searchData.modelName" placeholder="请输入设备名称"></el-input>
	    			</el-form-item>
	    			<el-form-item label="品牌">
	    				<el-input v-model="searchHigh.searchData.manufactory" placeholder="请输入设备品牌"></el-input>
	    			</el-form-item>
	    			<el-form-item label="型号">
	    				<el-input v-model="searchHigh.searchData.spec" placeholder="请输入设备型号"></el-input>
	    			</el-form-item>
	    		<!-- 	<el-form-item label="省份">
	    				<el-select v-model="searchHigh.searchData.devInfo">
	    					<el-option label="省份1" value="value1"></el-option>
	    					<el-option label="省份2" value="value2"></el-option>
	    				</el-select>	
	    			</el-form-item> -->
	    			<el-form-item label="企业名称">
	    				<el-input v-model="searchHigh.searchData.companyName" placeholder="请输入设备型号"></el-input>
	    			</el-form-item>
	    			<el-form-item label="网络模式">
	    				<el-select v-model="searchHigh.searchData.transferMethod">
	    					<el-option label="4G" value="4G"></el-option>
	    					<el-option label="NB" value="NB"></el-option>
	    				</el-select>	
	    			</el-form-item>
	    			<!-- <el-form-item label="出厂年月">
	    				<el-input v-model="searchSim.searchData.spec" placeholder="请输入出厂年月"></el-input>
	    			</el-form-item> -->
	    			<el-form-item label="实时状态" style="width:276px;">
	    				<el-radio-group v-model="searchHigh.searchData.onlineStatus">
							<el-radio :label="0">全部</el-radio>
							<el-radio :label="1">在线</el-radio>
							<el-radio :label="2">离线</el-radio>
							<!-- <el-radio :label="2">离线</el-radio> -->
						</el-radio-group>
	    			</el-form-item>
	    			<p style="text-align:left;"><span @click="doSearchHigh" class="qw_btn blue">查询</span>
	    			<span class="qw_btn" @click="resetSearchHigh">重置</span>
	    			<span class="qw_btn" @click="changeModLower">收起高级查询</span></p>
    			</el-form>
	    	</div>
	    	</div>
	    	<!-- TABLE -->
	    	<div class="tablebox">
	    		<TableEx :tableConfig="tableConfig" :border="true" :tableData="tableData" @sizeChange="sizeChange" @pageChange="pageChange" @add="add"
        @remove="remove" @edit="edit" @buttonFunction="buttonFunction">
        		<span @click="controlMul" class="qw_btn blue" style="margin-left:-10px;">批量控制</span>
        		</TableEx>
	    	</div>
	    	<my-toast :config="toastConfigCtrl">
	    		<div class="control">	    			
		    		<h2 class="title">光伏清扫车</h2>
		    		<span class="qw_btn_border_blue" @click="setGfParams">光伏清扫设置</span>
		    		<span class="qw_btn_border_blue"  @click="setDoneNow">强制清扫</span>
		    		<h2 class="title">M-CAT终端</h2>
		    		<span class="qw_btn_border_blue disabled">采集频率设置</span>
	    		</div>
	    		<my-toast :config="toastConfigParams" label-width="180px" append-to-body>
	    			<div class="root_params" style="padding:38px 0 0 65px;">
	    				<!-- {{gfParams.timeStart}} -->
		    			<el-form ref="formGfParm" :model="gfParams" :rules="rulesGf">
		    				<el-form-item label="每日开始时间" prop="timeStart">
		    					<el-time-picker style="width:184px;"
							      v-model="gfParams.timeStart"
							      value-format="HH"
							      placeholder="选择日期时间">
							    </el-time-picker>
		    				</el-form-item>
		    				<el-form-item label="每日结束时间" prop="timeEnd">
		    					<el-time-picker  style="width:184px;"
							      v-model="gfParams.timeEnd"
							      type="datetime"
							      value-format="HH"
							      placeholder="选择日期时间">
							    </el-time-picker>
		    				</el-form-item>
		    			</el-form>	 
		    			<p class="parm_btn_box" style="margin-top:43px">		    				
			    			<span class="qw_btn blue" @click="doItPlan" style="cursor:pointer;line-height:30px;height:32px;display:inline-block;box-sizing:border-box;width:88px;text-align:center;font-size:12px;background:#0D6FF4;border:1px solid #0D6FF4;color:#FFF;margin-left:57px;">下达指令</span>   				
			    			<span class="qw_btn" style="cursor:pointer;line-height:30px;height:32px;display:inline-block;box-sizing:border-box;width:88px;text-align:center;border:1px solid #E5E5E5;font-size:12px;color:#333;margin-left:16px;"  @click="toastConfigParams.dialogVisible=false">取消</span>   
		    			</p>				
	    			</div>
	    		</my-toast>
	    		<my-toast :config="toastConfigCirfim">
	    			<p style="font-size:16px;color:#666;text-align:center;padding:0 0 40px 0;">你确认下【强制清】指令？</p>
	    			<p style="text-align:center;"><span @click="doItNow" class="qw_btn blue" style="cursor:pointer;line-height:30px;height:32px;display:inline-block;box-sizing:border-box;width:88px;text-align:center;font-size:12px;background:#0D6FF4;border:1px solid #0D6FF4;color:#FFF;margin-left:0px;">确定</span>   				
			    			<span class="qw_btn" @click="toastConfigCirfim.dialogVisible=false" style="cursor:pointer;line-height:30px;height:32px;display:inline-block;box-sizing:border-box;width:88px;text-align:center;border:1px solid #E5E5E5;font-size:12px;color:#333;margin-left:16px;">取消</span> </p>
	    		</my-toast>
	    	</my-toast>	    	 
	    </el-tab-pane>
	</el-tabs>	
</div>
</template>
<script>
import {
	mcatRunDevSendInstruction,
	mcatRunDevListEnterprise,
} from '../../api/romoteController.js'
import TableEx from "../../components/common/TableEx.vue"
var vueObj={
	components:{
		TableEx
	},
	// watch:{
	// 	'tableConfig.currentSelectArr':(old,new_)=>{
	// 		if(new_.length<1){
	// 			// this.$set(this,'controlMul_',true);
	// 			this.controlMul_=false;
	// 		}else{
	// 			// this.$set(this,'controlMul_',true);
	// 			this.controlMul_=true;
	// 		}
	// 		console.log(new_.length);
	// 	}
	// },
	data(){
		return {
			controlMul_:false,
			dropDown:{
				modelName:[
					{label:'设备1',value:'131231'},
					{label:'设备2',value:'231231'},
				]
			},
			sendMod:0,
			buttonFunction_data:null,
			innerVisible:false,
			gfParams:{
				timeStart:"",
				timeEnd:""
			},
			rulesGf:{
				timeStart:[{required:true, message:"请选择开始时间",trigger:['change','blur']}],
				timeEnd:[{required:true, message:"请选择结束时间",trigger:['change','blur']}]
			},
			toastConfigCtrl:{
				title:'远程控制',
                dialogVisible:false,
                width:'440px',
                btnData:[
                   // {
                   //     type:'primary',
                   //     method:'submit',
                   //     name:'修改',

                   // }
               ]
			},			
			toastConfigParams:{
				title:'清扫周期设置',
                dialogVisible:false,
                appendToBody:true,
                width:'440px',
                btnData:[
                   // {
                   //     type:'primary',
                   //     method:'submit',
                   //     name:'修改',

                   // }
               ]
			},
			toastConfigCirfim:{
				title:'强制清扫',
                dialogVisible:false,
                appendToBody:true,
                width:'320px',
                btnData:[]
			},
			searchMod:0,
	        activeName: 'one',
	        searchSim:{
	        	searchData:{
	        		modelName:"",
	        		manufactory:"",
	        		spec:"",
	        		onlineStatus:"",
	        	},
	        	rule:{
	        		devInfo:[{required:false}],
	        		manufactory:[{required:false}],
	        		spec:[{required:false}],
	        		status:[{required:false}],
	        	}
	        },
	        searchHigh:{
	        	searchData:{
	        		devInfo:"",
	        		manufactory:"",
	        		spec:"",
	        		onlineStatus:"",
	        	},
	        	rule:{
	        		devInfo:[{required:false}],
	        		manufactory:[{required:false}],
	        		spec:[{required:false}],
	        		status:[{required:false}],
	        	}
	        },
	        tableConfig: {
        //操作按钮配置
        toolbarConfig: [
	        // {
	        //   icon: "",
	        //   disabled: false,
	        //   method: "add",
	        //   name: "批量控制",
	        //   bgColor:'dialog-deep-blue',
	        //   type:'primary'
	        // },
	        // {
	        //   icon: "edit",
	        //   disabled: true,
	        //   method: "edit",
	        //   name: "编辑"
	        // },
	        // {
	        //   icon: "remove",
	        //   disabled: true,
	        //   method: "remove",
	        //   name: "删除"
	        // }
	        ],
	        //表格字段配置
	        colConfig: [
	        // {
	        //   field: "",
	        //   label: "省份",
	        //   type: "text"
	        // },
	        {
	          field: "companyName",
	          label: "企业名称",
	          type: "text"
	        },
	        {
	          field: "modelName",
	          label: "设备名称",
	          type: "text"
	        },
	        {
	          field: "manufactory",
	          label: "品牌",
	          type: "text"
	        },
	        {
	          field: "spec",
	          label: "型号",
	          type: "text"
	        },
	        // {
	        //   field: "",
	        //   label: "出厂年月",
	        //   type: "text"
	        // },
	        {
	          field: "transferMethod",
	          label: "网络模式",
	          type: "text"
	        },
	        {
	          field: "commCode",
	          width:"100px",
	          label: "机联网编码",
	          type: "text"
	        },
	        {
	          field: "onlineStatus",
	          label: "实时状态",
	          type: "text"
	        },
	        { field: "远程运维", label: "操作", type: "btnText" }
	        ],
	        isSelection: true, //是否可选
	        isPage: true, //是否分页
	        currentSelectArr: [], //当前勾选的数据
	        align: "center", //文本对齐方式
	        pageNo: 1,
	        pageSize: 20,
	        total: 0,
	        isHigh: false,
	        isLoading: true, //是否开启loading
	        loadShow: false //loading控制
	      },
	     tableData: [],
	    }
	},
	created(){
		this.getData({});
	},
	methods:{
		changeModLower(){
			this.searchMod=0;
		},
		doItNow(){
			let params = this.getDoParams(1)
			this.do(params)
		},
		submitForm(formName,fun) {
	        let form=this.$refs[formName];
	        if(form){
	        	form.validate((valid) => {
		        	if (valid) {
		              	fun();
		          	} else {
		            	return false;
		          	}
	        	});
	        }
	        	
	    },
		doItPlan(){
			this.submitForm('formGfParm',()=>{
				let params = this.getDoParams(0)
				this.do(params)
			})
		},
		getDoParams(rightNow=0){
			let params ={...this.gfParams};
			if(rightNow)params.isClear=1;
			else params.isClear=0;
			if(this.sendMod==0){
				params.commcodes=this.buttonFunction_data.commCode;
			}else{
				params.commcodes="";
				for(let attr in this.tableConfig.currentSelectArr){
					if(attr< this.tableConfig.currentSelectArr.length-1)params.commcodes += (this.tableConfig.currentSelectArr[attr].commCode+',');
					else params.commcodes += (this.tableConfig.currentSelectArr[attr].commCode);
				}
			}
			return params;
		},
		do(params=null){
			if(!params)return;
			this.setAttr(params);
			mcatRunDevSendInstruction({Vue:this,params:params}).then(res=>{
				// console.log(res);
				this.$message.success(res.message);
				this.toastConfigParams.dialogVisible=false;
				this.toastConfigCirfim.dialogVisible=false;
				this.toastConfigCtrl.dialogVisible=false;
			})
			console.log("do========>",params)
		},
		resetSearchSim(){
			this.searchSim.searchData= { devInfo:"",manufactory:"",spec:"",status:"",};
			this.$refs['searchSim'].resetFields();
		},
		resetSearchHigh(){
			this.searchHigh.searchData= { devInfo:"",	manufactory:"", spec:"", status:"",}
			this.$refs['searchHigh'].resetFields();
		},
		doSearchHigh(){
			this.tableConfig.pageNo=1;
			this.getData({});
		},
		doSearchSim(){
			this.tableConfig.pageNo=1;
			this.getData({})
			// console.log("doSearchSim...")
		},
		setDoneNow(){
			this.toastConfigCirfim.dialogVisible=true;
		},
		setGfParams(){
			setTimeout(()=>{
				this.gfParams.timeStart="";
				this.gfParams.timeEnd="";
			},100)
			this.toastConfigParams.dialogVisible=true;
		},
		controlMul(){
			if(this.tableConfig.currentSelectArr.length>0){
				this.sendMod=1;			
				this.toastConfigCtrl.dialogVisible=true;	
			}else{
				this.$message.error("请勾选相应数据！")
			}
		},
		handleClick(tab, event) {
	        console.log(tab, event);
	    },
	    setAttr(data){
	    	for(let attr in data){
	    		if(undefined === data[attr] ||(''===data[attr]) ||(null === data[attr])){
	    			delete data[attr]
	    		}
	    	}
	    	return data;
	    },
	    getData(data={}){
	    	data.pageSize=this.tableConfig.pageSize;
	    	data.pageNo=this.tableConfig.pageNo;
	    	let params = this.searchMod==0? {...data, ...this.searchSim.searchData} : {...data, ...this.searchHigh.searchData}
	    	if(params.onlineStatus==0)params.onlineStatus="";
	    	if(params.onlineStatus==1)params.onlineStatus='在线';
	    	if(params.onlineStatus==2)params.onlineStatus='离线';
	    	this.setAttr(params);
	    	mcatRunDevListEnterprise({Vue:this,params:params}).then(res=>{
	    		this.tableData=res.model.items;
	    		this.tableConfig.total=res.model.numRows;
	    		this.tableConfig.loadShow=false;
	    	})
	    },
	    pageChange(){
	      this.getData() 
	    },
	    sizeChange(){
	      this.getData()
	    },
	    add(){

	    },
	    edit(){

	    },
	    remove(){

	    },
	    buttonFunction(...args){
	    	this.sendMod=0;
	    	this.buttonFunction_data=args[0];
	    	this.toastConfigCtrl.dialogVisible=true;
	      console.log(args)
	    }
	}
}		
export default vueObj;
</script>
<style lang="less">
.shippingAddress{
	min-height: 700px;
	box-sizing: border-box;
	.search{
		.el-form-item__label{
			color:#666;
			font-size:14px;
		}
		.el-radio+.el-radio{
			padding-left:10px;
			margin-left:10px;
		}
		.el-radio__label{
			padding-left:4px;
		}
		.el-input__inner{
			height:32px;
			line-height:32px;
			width:148px;
		}
		.el-form-item{
			width:240px;
			display:inline-block;
		}
	}
	.el-tabs__item{
		box-sizing: border-box;
		height:65px;
		line-height:65px;
		font-size:16px;
		color:#333;
		padding:0 20px;
	}
	.el-tabs__active-bar{
		background-color:#0D6FF4;
	}
	.el-tabs__nav-wrap::after {
		height:1px;
		background:#E5E5E5;
	}
	.el-tabs__nav-scroll{
		padding-left:30px;
	}
	.searchHigh,.searchSim{
		margin:0 auto;
		width:892px;
		height:128px;
		border-bottom:1px dashed #E5E5E5;
	}
	.searchHigh{
		height:190px;
	}
	.el-table th{
		padding:10px 0;
	}
	.TableColRender,.el-table tr td{
		padding:0;
		line-height:46px;
		font-size:12px;
		color: #333;
	}
	.el-table td{
		border-bottom-color: #F5F5F5;
	}
	.qw_btn{
		cursor:pointer;
		line-height:30px;
		height:32px;
		display:inline-block;
		box-sizing:border-box;
		padding:0 32px;
		border:1px solid #E5E5E5;
		font-size:12px;
		color:#333;
	}
	.blue.qw_btn{
		background:#0D6FF4;
		border:1px solid #0D6FF4;
		color:#FFF;
	}
	.qw_btn + .qw_btn{
		margin-left:16px;
	}
	.tablebox{
		box-sizing:border-box;
		width:100%;
		height:auto;
		padding: 0px 24px;
	}
	.root .el-table{
		min-height:250px;
	}
	.root{
		border: none;
	}
	.qw_btn_border_blue{
		display:block;
		margin:25px auto;
		width:160px;
		height:40px;
		font-size:14px;
		line-height:40px;
		color:#0D6FF4;
		border: 1px solid;
		text-align:center;
		cursor:pointer;
	}
	.qw_btn_border_blue.disabled{
		background:#F4F4F8;
		border:1px solid #F4F4F8;
		color:#CCC;
		cursor: not-allowed;
	}
	.control .title{
		margin-left:62px;
		font-size:16px;
	}
	.root_params{
		box-sizing:border-box;
		padding-left:65px;
	}
	.qw_btn.blue.disabled{
		cursor:not-allowed;
	}
}
</style>	


