<template>
        <div class="enterprise">
        <el-form :model="formData2" :rules="rules" ref="ruleForm" label-width="200px" class="demo-ruleForm">
            <el-form-item label="企业名称" prop="name">
                <el-input size="medium" v-model="formData2.name" placeholder="请输企业名称"></el-input>
            </el-form-item>
            <el-form-item label="所在地区" prop="region">
                  <area-select :level="2" type="text" v-model="formData2.region" :data="pcaa" ></area-select>
            </el-form-item>
            <el-form-item label="详细地址" prop="detailedAddress">
                <el-input size="medium" v-model="formData2.detailedAddress" placeholder="请输入详细地址"></el-input>
            </el-form-item>
            <el-form-item label="联系人" prop="contactPerson">
                <el-input size="medium" v-model="formData2.contactPerson" placeholder="请输联系人姓名"></el-input>
            </el-form-item>
            <el-form-item label="联系方式" prop="contactPhone">
                <el-input size="medium" v-model="formData2.contactPhone" placeholder="请输入联系方式"></el-input>
            </el-form-item>
            <el-form-item label="企业法人姓名" prop="legalPerson">
                <el-input size="medium" v-model="formData2.legalPerson" placeholder="请输入企业法人姓名"></el-input>
            </el-form-item>
            <el-form-item label="企业法人手机号" prop="legalPersonPhone">
                <el-input size="medium" v-model="formData2.legalPersonPhone" placeholder="请输入企业法人手机号"></el-input>
            </el-form-item>
            <el-form-item label="企业法人身份证号" prop="legalPersonIdentityCard">
                <el-input size="medium" v-model="formData2.legalPersonIdentityCard" placeholder="请输入企业法人身份证号"></el-input>
            </el-form-item>
            <el-form-item label="身份证图片" prop="imgs">
                <el-upload
                    class="avatar-uploader"
                    action="http://61.153.187.80:8081/fileSystem/uploadFile"
                    :show-file-list="false"
                    :on-success="uploadSuccess1"
                    :before-upload="beforeAvatarUpload">
                    <img v-if="formData2.certificatePositiveShareUrl" :src="formData2.certificatePositiveShareUrl" class="avatar">
                    <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                    <div slot="tip" class="el-upload__tip">身份证正面</div>
                    <div slot="tip" class="el-upload__tipmain">注：仅支持JPG、PNG格式图片</div>
                </el-upload>
                <el-upload
                    class="avatar-uploader uploader-right"
                    action="http://61.153.187.80:8081/fileSystem/uploadFile"
                    :show-file-list="false"
                    :on-success="uploadSuccess2"
                    :before-upload="beforeAvatarUpload">
                    <img v-if="formData2.certificateNegativeShareUrl" :src="formData2.certificateNegativeShareUrl" class="avatar">
                    <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                    <div slot="tip" class="el-upload__tip">身份证反面</div>
                </el-upload>
            </el-form-item>
            <el-form-item label="统一社会信用代码" prop="socialCreditCode" style="margin-top:10px;">
                <el-input size="medium" v-model="formData2.socialCreditCode" placeholder="请输入18位代码"></el-input>
            </el-form-item>
            <el-form-item label="营业执照图片" prop="businessLicenceShareUrl">
                <el-upload
                    class="avatar-uploader"
                    action="http://61.153.187.80:8081/fileSystem/uploadFile"
                    :show-file-list="false"
                    :on-success="uploadSuccess3"
                    :before-upload="beforeAvatarUpload">
                    <img v-if="formData2.businessLicenceShareUrl" :src="formData2.businessLicenceShareUrl" class="avatar">
                    <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                </el-upload>
            </el-form-item>
            <el-form-item>
                <el-checkbox v-model="isRead"> </el-checkbox><span style="margin-left:10px;">我已阅读并同意</span><span class="protocol">隐私协议与诚信协议</span>
                <div @click="onSubmit" class="submit-btn" :class="{'isDisabled':!isRead}">提交</div>
            </el-form-item>
        </el-form>
    </div>
</template>

<script>
import { AreaSelect } from "vue-area-linkage";
import { pca, pcaa } from "area-data";
import rule from '../../assets/js/rule.js'
    export default {
        data(){
            var myValidator=(rule, value, callback)=>{
                if(value[0]==''||value[1]==''){
                    return callback(new Error("请上传身份证正反面信息"));
                }else {
                    callback();
                }
            };
            return{
                pcaa: pcaa,
                radio:'',
                rules:{
                    name:rule.require(),
                    region:[
                        { required: true,type:'array', message: '请选择所在地区', trigger: 'change' },
                    ],
                    detailedAddress:rule.require(),
                    contactPerson:rule.require(),
                    contactPhone:[
                        { required: true, message: '此项为必填项', trigger: 'blur' },
                        { validator: rule.phone, trigger: 'blur' }
                    ],
                    legalPerson:rule.require(),
                    legalPersonPhone:[
                        { required: true, message: '此项为必填项', trigger: 'blur' },
                        { validator: rule.phone, trigger: 'blur' }
                    ],
                    legalPersonIdentityCard:[
                        { required: true, message: '此项为必填项', trigger: 'blur' },
                        { validator: rule.IDcard, trigger: 'blur' }
                    ],
                    socialCreditCode:[
                        { required: true, message: '此项为必填项', trigger: 'blur' },
                        { validator: rule.credit, trigger: 'blur' }
                    ],
                    businessLicenceShareUrl:rule.require(),
                    imgs:[
                        { required: true,type:'array', message: '请上传身份证正反面信息', trigger: 'change' },
                        { validator: myValidator, trigger: 'change' }
                    ],
                },
                selected:['110000'],
                isRead:false
            }
        },
        props:['formData2'],
        methods:{
            uploadSuccess1(file){
                console.log(this.formData2)
                this.formData2.imgs[0]=file.model
                this.formData2.certificatePositiveShareUrl=file.model
                this.$refs.ruleForm.validateField('imgs')
            },
            uploadSuccess2(file){
                this.formData2.imgs[1]=file.model
                this.formData2.certificateNegativeShareUrl=file.model
                this.$refs.ruleForm.validateField('imgs')
            },
            uploadSuccess3(file){
                this.formData2.businessLicenceShareUrl=file.model
                this.$refs.ruleForm.validateField('businessLicenceShareUrl')
            },
            beforeAvatarUpload(file) {
                const isJPG = file.type === 'image/jpeg';
                const isPNG = file.type === 'image/png';
                const isLt2M = file.size / 1024 / 1024 < 2;
                console.log(file)
                if (!isJPG&&!isPNG) {
                    this.$message.error('上传头像图片只能是 JPG 或者 PNG 格式!');
                }
                if (!isLt2M) {
                    this.$message.error('上传头像图片大小不能超过 2MB!');
                }
                return (isJPG||isPNG) && isLt2M;
            },
            onSubmit(){
                if(!this.isRead){
                    return false
                }
                this.$refs['ruleForm'].validate((valid) => {
                    if (valid) {
                        this.$emit('submit1')
                    } else {
                        console.log('error submit!!');
                        return false;
                    }
                });
            }
        },
        components:{
            AreaSelect: AreaSelect
        },
        watch:{
            'formData2.region':function(){
                this.$refs.ruleForm.validateField('region')
            }
        }
    }
</script>

<style lang="less">
    .enterprise{
        width: 700px;
        margin-top: 30px;
        .el-form-item__label{
            padding-right: 38px;
        }
        .submit-btn.isDisabled{
            opacity: 1;
            cursor: not-allowed;
            background: #a0cfff;
        }
        .license{
            margin-top: -30px;
        }
        .submit-btn{
            width:220px;
            height:44px;
            background:rgba(13,111,244,1);
            font-size:14px;
            font-family:MicrosoftYaHei;
            color:rgba(255,255,255,1);
            line-height:44px;
            text-align: center;
        cursor: pointer;
        }
        .avatar-uploader{
            width: 242px;
            float: left;
        }
        .uploader-right{
            margin-left: 15px;
        }
        .avatar-uploader .el-upload {
            border: 1px dashed #d9d9d9;
            border-radius: 6px;
            cursor: pointer;
            position: relative;
            overflow: hidden;
        }
        .avatar-uploader .el-upload:hover {
            border-color: #409EFF;
        }
        .avatar-uploader-icon {
            font-size: 28px;
            color: #8c939d;
            width: 240px;
            height: 140px;
            line-height: 140px;
            text-align: center;
        }
        .avatar {
            width: 240px;
            height: 140px;
            display: block;
        }
        .el-upload__tip{
            margin-top: -10px;
            text-align: center;
        }
        .el-upload__tipmain{
            // margin-top: 10;
            line-height: 14px;
            font-size:14px;
            font-family:MicrosoftYaHei;
            color:rgba(153,153,153,1);
        }
        .el-input__prefix, .el-input__suffix{
            margin-top: -20px;
        }
         .el-form{
            .area-selected-trigger{
                padding: 0px 20px 7px 12px;
            }
            .area-select-wrap{
                margin-top: 3px;
            }
            .area-select-wrap .area-select:nth-child(1){
                margin-left: 0px;
            }
            .area-select-wrap .area-select{
                margin-left: 2px;
                line-height: 32px;
                margin-top: 3px;
            }
        }
        .protocol{
             font-size:14px;
            font-family:MicrosoftYaHei;
            color:#0D6FF4;
         }
    }
</style>




