<template>
    <div class="application"> 
        <h2 class="tab_title_box" >
            <span :class="{'select':current=='企业入驻'}" @click="marketClick('企业入驻')">企业入驻</span>
            <span :class="{'select':current=='业务代理'}" @click="marketClick('业务代理')">业务代理</span>
        </h2>
        <div class="syx_content" v-if="current=='企业入驻'">
            <div class="syx_step_box">
                <el-steps :space="500" :active="step"  align-center finish-status="success" >
                    <el-step title="入驻申请"></el-step>
                    <el-step title="业务受理"></el-step>
                    <el-step :title="endResult" :status="endStatus"></el-step>
                </el-steps>
            </div>
            <div class="syx_form_box">
                <fillIn ref="fillForm" :formData="formData" @onSubmit="onSubmit" v-show="step==0" :operateType="operateType"></fillIn>
                <message :formData="formData" v-show="step==1"></message>
                <result :formData="formData" v-show="step==2" @resetApprove="resetApprove"></result>
            </div>
        </div>
        <div v-else class="agent_box">
            <agent></agent>
        </div>
    </div>
</template>
<script>
import {mapState} from 'vuex'
import fillIn from './fillIn.vue'
import message from './message.vue'
import result from './result.vue'
import agent from './agent.vue'
export default {
    data(){
        return{
            operateType:'add',
            current:'企业入驻',
            formData:{
                name: null,
                adress: [],
                property: null,
                trade: null,
                contactPerson: null,
                contactPhone:null,
                employeeScale: null,
                siteImageShareUrl: null,
                equipmentScale:0,
                detailedAddress:null,
                onIcloudScale: 0,
            },
            step:-1,
            result:0,
        }
    },
    computed:{
        ...mapState(['userId','ports','id']),
        endResult:function(){
            if(this.result==0){
                return '审核结果'
            }else if(this.result==1){
                return '审核成功'
            }else if(this.result==2){
                return '审核失败'
            }
        },
        endStatus:function(){
            if(this.result==0){
                return 'wait'
            }else if(this.result==1){
                return 'success'
            }else if(this.result==2){
                return 'error'
            }
        }
    },
    methods:{
        resetApprove(){
            this.operateType='edit'
            this.step=0;
            this.result=0;
        },
        marketClick(current){
            this.current=current
        },
        onSubmit(data){
             this.getDetail()
        },
        getDetail(){
             this.$axios.post_noqs(
                this.ports + '/onicloud/get',
                {},
                this
            ).then(res=>{
                if(res.success){
                    if(res.data.status==0){
                        this.step=0
                        this.result=0
                        this.formData={
                            name: res.data.name,
                            adress: [res.data.province,res.data.city,res.data.district],
                            property: res.data.property,
                            trade: res.data.trade,
                            contactPerson: res.data.contactPerson,
                            contactPhone:res.data.contactPhone,
                            employeeScale: res.data.employeeScale,
                            siteImageShareUrl: res.data.siteImageShareUrl,
                            equipmentScale:res.data.equipmentScale,
                            detailedAddress:res.data.detailedAddress,
                            onIcloudScale: res.data.onIcloudScale,
                        }
                        // this.formData.adress=[]
                    }else if(res.data.status==1){
                        this.step=1
                        this.result=0
                        this.formData=res.data
                        this.formData.adress=[res.data.province,res.data.city,res.data.district]
                    }else if(res.data.status==2){
                        this.step=2
                        this.result=1
                        this.formData=res.data
                        this.formData.adress=[res.data.province,res.data.city,res.data.district]
                    }else if(res.data.status==3){
                        this.step=2
                        this.result=2
                        this.formData=res.data
                        this.formData.adress=[res.data.province,res.data.city,res.data.district]
                    }
                    
                    
                }
            })
        }
    },
    components:{
        fillIn,
        message,
        result,
        agent
    },
    mounted(){
        this.getDetail()
    }
}
</script>

<style lang="less">
    .application{
        box-sizing:border-box;
        padding-bottom:40px;
        width: 100%;
        min-height: 700px;
        display: flex;
        flex-direction: column;
        .tab_title_box{
            font-size:16px;
            font-family:MicrosoftYaHei-Bold;
            color:rgba(51,51,51,1);
            border-bottom: 1px solid rgba(229,229,229,1);
            height: 60px;
            line-height: 60px;
            >span{
                width: 160px;
                text-align: center;
                line-height:60px;
                font-size:16px;
                font-family:MicrosoftYaHei;
                color:rgba(51,51,51,1);
                display: inline-block;
                height: 60px;
                color:#666;
                font-weight:400;
                cursor: pointer;
                &.select{
                    border-bottom: 2px solid rgba(13,111,244,1);
                }
            }
        }
        .syx_content{
            width: 500px;
            margin: 40px auto 0;
            .syx_step_box{
                width: 500px;
                margin: 0 auto 40px;
                .el-step__title,.el-step__head{
                    color:#0D6FF4;
                    border-color:#0D6FF4;
                }
                .el-step__title.is-process{
                    font-weight:400;
                }
                .el-step__icon.is-text{
                    width: 32px;
                    height: 32px;
                }
                .el-step.is-horizontal .el-step__line{
                    top: 14px;
                }
                .is-success .el-step__icon.is-text{
                    background:#0D6FF4;
                    color:#FFF;
                }
                .el-step__head.is-success .el-step__line{
                    background:#0D6FF4;
                }
                .el-step__title.is-success,.el-step__head.is-success{
                    color:#0D6FF4;
                    border-color:#0D6FF4;
                }
                .el-step__title.is-wait,.el-step__head.is-wait{
                    color:#999;
                    border-color:#999;
                }
            }
        }
        .agent_box{
            padding: 20px;
            overflow: auto;
        }
    }
</style>




