      /* toastData:{
          title:'啦啦啦',//弹窗标题
          dialogVisible:true,//弹窗显示
          width:'600px',//弹窗宽
          btnData:[{
            name:'确定',//按钮名
            method:'dialogSuer',//回调函数
            bgColor:'dialog-bule'//按钮背景
          },{
            name:'确定并继续',
            method:'savaAgain',
            bgColor:'dialog-deep-blue'
          }]
      }*/
<template>
    <el-dialog class="poc-toast" :title="config.title" :fullscreen="config.fullscreen" :visible.sync="config.dialogVisible" :close-on-click-modal="false" :append-to-body="config.appendToBody" :width="config.width" @close="callBack('handleClose')" @open="callBack('handleOpen')">
        <!-- 内容   主题内容自定义部分-->
        <slot></slot>
        <!-- 按钮组 -->
        <div class="my-btn-group">
            <el-button class="dialog-white" v-for="(item,index) in config.btnData" :type="item.type" @click="callBack(item.method)" :key="index">{{item.name}}</el-button>
        </div>
        <slot name="footer"></slot>
    </el-dialog>
</template>
<script>
export default {
  props: ["config"],
  methods: {
    callBack(method) {
      this.$emit(method);
    }
  }
};
</script>
<style lang="less">
// @headerbgImage: linear-gradient(to right, #4a8dd2, #2acaff);
@headerbgColor:#FFF;
@header-height:50px;
.poc-toast {
  .el-dialog {
    border-radius: 0px;
    overflow: hidden;
  }
  .el-dialog__header {
    background-color: @headerbgColor;  
    position: relative;
    height: @header-height;
    line-height: @header-height;
    padding: 0 10px;
    box-sizing:border-box;
    /*border-bottom:1px solid #EEE;*/

    .el-dialog__title {
      color: #333333;
    }
  }
  .el-dialog__body {
    padding: 25px 0 0 0 !important;
  }
  .my-btn-group {
    text-align: center;
    // background-color: #eef1f5;
    padding: 5px 10px;
    margin-top: 20px;
    button{
      padding: 8px 35px;
    }
    margin-bottom: 20px;
  }
  .el-dialog__headerbtn{
    color: #333333;
    position: absolute;
    top:50%;
    transform: translate(0,-50%)
  }
}
</style>



