import http from "./public"
//上传用户头像
export const accountCenterUpdateAvatar = (data) =>{
	return http.fetchPost('/accountCenter/updateAvatar',data)	
}
//获取用户信息
export const accountCenterGetAccountInfo = (data) =>{
	return http.fetchPost('/accountCenter/getAccountInfo',data)	
}
//新手机号码获取验证码
export const safeSetSendNewMess = (data)=>{
	return http.fetchPostJson('/safeSet/sendNewMess',data)
}

export const safeSetSubmitMess = (data)=>{
	return http.fetchPostJson('/safeSet/submitMess',data)
}
export const safeSetUpdatePw = (data)=>{
	return http.fetchPost('/safeSet/updatePw',data)
}

export const safeSetSendMess = (data)=>{
	return http.fetchPostJson('/safeSet/sendMess',data)
}


