<template>
    <div class="enterpriseInformation">
        <el-form :model="formData2"  ref="ruleForm" label-width="200px" class="demo-ruleForm">
            <el-form-item label-width="0" class="titlenp">
                <h1 style="text-align:center">企业认证申请信息<b style="color:#2BD954;font-weight:300;">(审核已通过)</b></h1>
            </el-form-item>
            <el-form-item label="企业名称" prop="name">
                <span>{{formData2.name}}</span>
            </el-form-item>
            <el-form-item label="所在地区" >
               <span>{{formData2.province}} {{formData2.city}} {{formData2.district}}</span>
            </el-form-item>
            <el-form-item label="详细地址">
                <span>{{formData2.detailedAddress}}</span>
            </el-form-item>
            <el-form-item label="联系人">
                <span>{{formData2.contactPerson}}</span>
            </el-form-item>
            <el-form-item label="联系方式">
                <span>{{formData2.contactPhone}}</span>
            </el-form-item>
            <el-form-item label="企业法人姓名">
                <span>{{formData2.legalPerson}}</span>
            </el-form-item>
            <el-form-item label="企业法人手机号">
                <span>{{formData2.legalPersonPhone}}</span>
            </el-form-item>
            <el-form-item label="企业法人身份证号" >
                <span>{{formData2.legalPersonIdentityCard}}</span>
            </el-form-item>
            <el-form-item label="证件图片" >
                <div  style="margin-top:15px;margin-left:28px;">
                    <img :src="formData2.certificatePositiveShareUrl" class="avatar">
                    <img :src="formData2.certificateNegativeShareUrl" class="avatar">
                </div>
            </el-form-item>
            <el-form-item label="统一社会信用代码">
                <span>{{formData2.socialCreditCode}}</span>
            </el-form-item>
            <el-form-item label="营业执照" >
                <div  style="margin-top:15px;margin-left:28px;">
                    <img :src="formData2.businessLicenceShareUrl" class="avatar">
                </div>
            </el-form-item>
            <el-form-item style="margin-top:50px;">
                <div style="margin-left:28px;" @click="reAuthentication1" class="submit-btn">重新认证</div>
            </el-form-item>
        </el-form>
    </div>    
</template>

<script>
    import { AreaSelect } from "vue-area-linkage";
    import { pca, pcaa } from "area-data";
    import rule from '../../assets/js/rule.js'
    import {mapState} from 'vuex'
    export default {
         data(){
            return{
                pcaa: pcaa,
                // 上传的图片url
                imageUrl:'',
                // 是否已阅读协议
                isRead:false
            }
        },
        props:['formData2'],
        methods:{
            reAuthentication1(){
                this.$refs['ruleForm'].validate((valid) => {
                    if (valid) {
                        this.$emit('reAuthentication1',this.formData2)
                    } else {
                        console.log('error submit!!');
                        return false;
                    }
                }); 
            }
        },
        components:{
            AreaSelect: AreaSelect
        },
        computed:{
            ...mapState(['userId','ports','authStatus','authType'])
        },
        mounted(){
            this.$axios.post_noqs(
                this.ports + '/enterpriseauth/page',
                {userId:this.userId},
                this
            ).then(res=>{
                if(res.success){
                     this.$emit('getDataSuceess2',res.data)
                    // this.formData2=res.data
                }
            })
        }
    }
</script>

<style lang="less">
    .enterpriseInformation{
        margin-top: 30px;
        // width: 500px;
        .submit-btn{
            width:220px;
            height:44px;
            background:rgba(13,111,244,1);
            font-size:14px;
            font-family:MicrosoftYaHei;
            color:rgba(255,255,255,1);
            line-height:44px;
            text-align: center;
            cursor: pointer;
        }
        .demo-ruleForm{
            margin-left: 40px;
        }
        .titlenp h1{
            font-size: 24px;
            font-weight: normal;
        }
        .area-select .el-select {
            width: 42px;
        }
        .el-input span{
            display: none;
        }
        // .el-input--suffix .el-input__inner{
        //     border: none;
        //     padding: 0;
        //     cursor: default;
        //     border-radius: 0px;
        //     height: 25px;
        //     line-height: 42px;
        //     font-size: 14px;
        //     color: #333;
        //     float: left;
        //     width: 42px;
        //     margin-top: 5px;
        // }
        .el-form-item{
            margin-bottom: 10px;
        }
        .el-form-item__content span{
            margin-left: 28px;
            font-size:14px;
            font-family:MicrosoftYaHei;
            color:rgba(51,51,51,1);
        }
        .avatar {
            width: 240px;
            height: 140px;
            display: inline-block;
        }
        .avatar:nth-child(2){
            margin-left: 18px;
        }
        // .el-form-item__content {
        //     // width: 524px;
        // }
    }
</style>



