
import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex)
const store = {
    cur_AplicationItem:null,//当前申请项目审核已通过
    ports:null,
    select:{
        link:null
    },
    userId:-1,
    navList:[],
    authStatus:null,
    authType:null,
    agencyType:0, //代理级别
    id:null,
}
export default new  Vuex.Store({
    state:store,
    actions: {
        set_navList({commit},self){
            self.$axios.post(
                self.ports+'/sidebar/get',
                {},
                self
            ).then(res=>{
                if(res.success){
                    // self.navList=res.data.navList
                    commit('set_navList',res.data.navList)
                }
            })
        },
        get_status({commit},self){
            self.$axios.post(
                self.ports+'/user/get',
                {},
                self
            ).then(res=>{
                if(res.success){
                    commit('get_status',res.data)
                    console.log(res.data,'+++++++')
                    self.loginTime=res.data.lastLoginDate
                    self.userName=res.data.name
                }
            })
        }
    },
    mutations:{
        set_curApplicationItem:(state,data)=>{
            state.cur_AplicationItem=data;
        },
        set_navList:(state,data)=>{
            console.log(data,'????')
            state.navList=data
        },
        set_ports:(state,ports)=>{
            state.ports=ports
        },
        set_setappid:(state,data)=>{  //
            state.id=data
        },
        set_select:(state,data)=>{
            state.select=data
        },
        set_userId:(state,userId)=>{
            state.userId=userId
        },
        get_status:(state,data)=>{
            //审核中 审核失败 审核成功 
            state.authStatus=data.authStatus;
            //个人认证 企业认证
            state.authType=data.authType
        }
    },
    getters:{

    }
})



