<template>
    <div>

        <div class="content-center">
            <div class="center-box" :class="{active:isActive}" style="margin-top: 78px;" @click="isActive=true,type='self'">
                <div :class="{sanjiao:isActive}"></div>
                <img src="../../assets/img/personal.png" alt="" style="margin: 41px 13px 39px 58px;float: left;">
                <div style="float: left;width: 238px;margin-top: 64px;">
                    <h1 style="font-size:24px;font-weight: bold;margin-bottom: 10px;">个人认证</h1>
                    <span style="font-size: 14px;color:rgba(153,153,153,1);">成功认证后，您可以在云市场自由交易</span>
                </div>
            </div>
            <div class="center-box" :class="{active:!isActive}" style="margin-top: 40px;" @click="isActive=false,type='company'">
                <div :class="{sanjiao:!isActive}"></div>
                <img src="../../assets/img/enterprise.png" alt="" style="margin: 41px 13px 39px 58px;float: left;">
                <div style="float: left;width: 295px;margin-top: 64px;">
                    <h1 style="font-size:24px;font-weight: bold;margin-bottom: 10px;">企业认证</h1>
                    <span style="font-size: 14px;color:rgba(153,153,153,1);">成功认证后，您可以购买我们的App Store应用</span>
                </div>
            </div>
            <div class="content-footer">
                <input type="button" value="马上认证" @click="btn">
            </div>
        </div>
        <div style="clear:both;"></div>       
    </div>
</template>

<script>
    export default{
        data(){
            return{
                isActive:true,
                type:'self'
            }
        },
        methods:{
            btn(){
                this.$emit('btn',this.type)
            }
        }
    }
</script>

<style lang="less">
    .content-center{
        float: left;
        margin-left: 200px;
    }
    .content-center .center-box{
        width: 543px;
        height: 174px;
        cursor: pointer;
        border: 1px solid #E5E5E5;
        background:rgba(255,255,255,1);
        margin: 0 auto;
        position: relative;
    }
    .content-center .active{
        border: 1px solid #0D6FF4;
    }
    .sanjiao{
        width: 40px;
        height: 40px;
        background: url('../../assets/img/topright.png') no-repeat;
        position: absolute;
        right: 0;
    }
    .content-footer{
        margin-top: 48px;
        text-align: center;
    }
    .content-footer input{
        width: 220px;
        background:rgba(13,111,244,1);
        font-size:14px;
        font-family:MicrosoftYaHei;
        color:rgba(255,255,255,1);
        outline: none;
        cursor: pointer;
        height: 44px;
        border:none;
    }
    .content-footer-item{
        margin-top: 24px;
        text-align: center;
    }
</style>




