// The Vue build version to load with the `import` command  
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import './assets/css/common.css'
import './assets/css/iconfont.css'
Vue.config.productionTip = false
import ElementUI from 'element-ui';
import 'element-ui/lib/theme-chalk/index.css';
import load from './components/common/load.vue';
import store from './vuex/store'
import myToast from './components/common/Toast.vue'
import VueAreaLinkage from 'vue-area-linkage';
import 'vue-area-linkage/dist/index.css'




import Loading from './components/common/Loading.vue' //Loading组件
// import pocHeader from './components/common/pocHeader.vue'
import page from './components/common/page.vue'
import Sidebar from './components/common/Sidebar.vue'
import cenSearch from './components/common/search.vue' //查询组件
import cenToast from './components/common/Toasts.vue' //弹框组件
import lookInfo from './components/common/lookInfo.vue' //
import cenTable from './components/common/Table.vue' //表格组件
import toolbar from './components/common/toolbar.vue' //按钮组件
import filterSearch from './components/common/filterSearch.vue' //过滤查询
import timeSearch from './components/common/timeSearch.vue' //时间查询





Vue.use(ElementUI);
Vue.use(VueAreaLinkage);
import axios from './api/base.js';
Vue.prototype.$axios = axios;  
Vue.component('load',load)
Vue.component('my-toast',myToast)




Vue.component('Loading',Loading)//Loading组件
// Vue.component('pocHeader',pocHeader)
Vue.component('page',page)
Vue.component('Sidebar',Sidebar)
Vue.component('cenSearch',cenSearch)//查询组件
Vue.component('cenToast',cenToast) //弹框组件
Vue.component('lookInfo',lookInfo)
Vue.component('cenTable',cenTable) //表格组件
Vue.component('toolbar',toolbar) //按钮组件
Vue.component('filterSearch',filterSearch) //过滤查询
Vue.component('timeSearch',timeSearch) //时间查询




/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,
  components: { App },
})
