<template>
    <div class="message">
        <p class="status"><i class="iconfont icon-shalou"></i>业务受理中</p>
        <div class="syx-liner"></div>
        <el-form :model="formData"  ref="ruleForm" label-width="120px" class="demo-ruleForm">
            <el-form-item label="企业名称" prop="name">
                <span>{{formData.name}}</span>
            </el-form-item>
            <el-form-item label="企业性质" >
                <span>{{formData.propertyDesc}}</span>
            </el-form-item>
            <el-form-item label="所属行业" >
                <span>{{formData.tradeDesc}}</span>
            </el-form-item>
            <el-form-item label="地址" >
                <span>{{formData.province}}{{formData.city}}{{formData.district}}</span>
            </el-form-item>
             <el-form-item label="详细地址" >
                <span>{{formData.detailedAddress}}</span>
            </el-form-item>
            <el-form-item label="联系人" >
                <span>{{formData.contactPerson}}</span>
            </el-form-item>
            <el-form-item label="联系方式" >
                <span>{{formData.contactPhone}}</span>
            </el-form-item>
             <el-form-item label="员工规模" >
                <span>{{formData.employeeScaleDesc}}</span>
            </el-form-item>
            <el-form-item label="设备总数" >
                <span>{{formData.equipmentScale}}</span>
            </el-form-item>
            <el-form-item label="上云设备数" >
                <span>{{formData.onIcloudScale}}</span>
            </el-form-item>
            <el-form-item label="企业场所" >
                <div style="margin-top:10px;" class="avatar">
                     <img :src="formData.siteImageShareUrl" >
                </div>
            </el-form-item>
        </el-form>
    </div>
</template>
<script>
import rule from '../../assets/js/rule.js'
export default {
    data(){
        return{
            // 上传的图片url
            imageUrl:'',
            // 是否已阅读协议
            isRead:false
        }
    },
    props:['formData'],
    methods:{
        reFillIn(){
            this.$emit('reFillIn')
        }
    }
}
</script>
<style lang="less">
.message{
    margin-top: 30px;
    .status{
        font-size:24px;
        font-family:MicrosoftYaHei;
        color:rgba(51,51,51,1);
        text-align: center;
        padding-bottom:40px;
        i{
            font-size:24px;
            color: #0D6FF4;
            margin-right: 15px;
        }
    }
    .syx-liner{
        width: 800px;
        border-bottom: 1px solid #E5E5E5;
        margin-left: -150px;
        margin-bottom: 45px;
    }
    .el-form-item__label{
        padding: 0 20px 0 0;
    }
    .submit-btn{
        width:220px;
        height:44px;
        background:rgba(13,111,244,1);
        font-size:14px;
        font-family:MicrosoftYaHei;
        color:rgba(255,255,255,1);
        line-height:44px;
        text-align: center;
        cursor: pointer;
    }
    .avatar {
        width: 150px;
        height: 100px;
        overflow: hidden;
        position: relative;
        border:1px solid rgba(229,229,229,1);
        img{
            position: absolute;
            left: 50%;
            top: 50%;
            transform: translate(-50%,-50%);
            width: 100%;
        }
    }
    .el-form-item{
        margin-bottom: 0px;
    }
}
  
</style>



