import axios from 'axios'
import qs from 'qs'
axios.defaults.withCredentials=true;
axios.interceptors.request.use(config => {    // 这里的config包含每次请求的内容
    // 判断localStorage中是否存在api_token
    if (localStorage.getItem('api_token')) {
        //  存在将api_token写入 request header
        config.headers.apiToken = `${localStorage.getItem('api_token')}`;
    }
    return config;
}, err => {
    return Promise.reject(err);
});

axios.interceptors.response.use(response => {
    return response
}, error => {
    return Promise.resolve(error.response)
});

function checkStatus (response) {
    // 如果http状态码正常，则直接返回数据
    if (response && (response.status === 200 || response.status === 304 ||
            response.status === 400)) {
        return response
    }
    // 异常状态下，把错误信息返回去
    return {
        status: -404,
        msg: '网络异常'
    }
}

function checkCode (res,self) {
    // 如果code异常(这里已经包括网络错误，服务器错误，后端抛出的错误)，可以弹出一个错误提示，告诉用户
    // if (res.status === -404) {
    //     self.$router.push('/404')
    //     return false
    // }
    console.log(res,'======')
    if (res.data) {
        if(res.data.result){
            return {success:true,data:res.data.model}
        }else{
            self.$alert(res.data.message, '错误提示', {
                confirmButtonText: '确定'
            });
            return {success:false,data:res.data.message}
        }
    }
    return res
}
// 请求方式的配置
export default {
    post (url, data,self) {  //  post
        return axios({
            method: 'post',
            baseURL: '/backapis',
            url,
            data: qs.stringify(data),
            timeout: 20000,
        }).then(
            (response) => {
                return checkStatus(response)
            }
        ).then(
            (res) => {
                return checkCode(res,self)
            }
        )
    },
    post_noqs (url, data,self) {  //  post
        return axios({
            method: 'post',
            baseURL: '/backapis',
            url,
            data: data,
            timeout: 20000,
        }).then(
            (response) => {
                return checkStatus(response)
            }
        ).then(
            (res) => {
                return checkCode(res,self)
            }
        )
    },
    get (url, params,self) {  // get
        return axios({
            method: 'get',
            baseURL: '/backapis',
            url,
            params, // get 请求时带的参数
            timeout: 20000,
        }).then(
            (response) => {
                return checkStatus(response)
            }
        ).then(
            (res) => {
                return checkCode(res,self)
            }
        )
    }
}


