<template>
    <div class="safety">
        <div class="level">
            <div>
                <span class="my-label">安全等级</span><span class="my-item"><span class="curent-level">中</span><i v-for="i in 6" :class="{'isCurrent':i<form.safeNum}"></i></span>
            </div>
            <div class="my-form">
                <div class="clearfix">
                     <span class="form-label">手机</span><span class="form-item">{{form.phone}}</span><span style="float: left;"><el-button type="text" @click="phoneEdit(form.phone)">修改</el-button></span>
                </div>
               <div class="clearfix">
                    <span class="form-label">邮箱</span>
                    <el-tooltip class="item" effect="dark" :content="form.emailaddress" placement="top-start"><span class="form-item">{{form.emailaddress}}</span></el-tooltip>
                    <span style="float: left;"><el-button type="text">未验证</el-button></span>
                </div>
                <div class="clearfix"> 
                     <span class="form-label">密码</span><span class="form-item"><i v-for="i in 6">*</i></span><span style="float: left;"><el-button type="text" @click="passwordEdit()">修改</el-button></span>
                </div>
            </div>
        </div>
        <my-toast :config="phoneEditConfig" @submit="phoneEditSubmit" @handleClose="handleClose('ruleForm1')">
            <div style="padding:0 20px">
                <el-form :model="phoneEditForm" :rules="rules"  ref="ruleForm1" label-width="130px" >
                    <el-form-item label="原手机号码" >
                        <span>{{phoneEditForm.oldPhone}}</span>
                    </el-form-item>
                    <el-form-item label="原手机验证" prop="code1">
                        <el-input style="width:150px;margin-right:20px" size="medium" v-model="phoneEditForm.code1"></el-input>
                        <el-button style="width:146px;" type="info" size="medium" plain @click="getCode1" :disabled="code1IsEdit!=0||(!/^1[34578]\d{9}$/.test(phoneEditForm.oldPhone))">获取验证码<span v-if="code1IsEdit!=0">({{code1IsEdit}})</span></el-button>
                    </el-form-item>
                    <el-form-item label="新手机号码" prop="phone1">
                        <el-input size="medium" style="width: 320px" v-model="phoneEditForm.phone1"></el-input>
                    </el-form-item>
                    <el-form-item label="新手机验证"  prop="code2">
                         <el-input style="width:150px;margin-right:20px" size="medium" v-model="phoneEditForm.code2"></el-input>
                         <el-button style="width:146px;" type="info" size="medium" plain @click="getCode2" :disabled="code2IsEdit!=0||(!/^1[34578]\d{9}$/.test(phoneEditForm.phone1))">获取验证码<span v-if="code2IsEdit!=0">({{code2IsEdit}})</span></el-button>
                    </el-form-item>
                </el-form>
            </div>
            <div class="footer" slot="footer">提示：若您的手机无法收到信息，请联系客服021-1111111，人工帮您找回！</div>
        </my-toast> 
        <my-toast :config="passwordEditConfig" @submit="passwordEditSubmit" @handleClose="handleClose('ruleForm')">
            <div style="padding:0 20px">
                <el-form :model="passwordEditForm" :rules="rules2"  ref="ruleForm" label-width="130px">
                    <el-form-item label="手机号码" prop="phone1">
                        <el-input size="medium" v-model="passwordEditForm.phone1"></el-input>
                    </el-form-item>
                    <el-form-item label="手机验证" prop="code1">
                        <el-input style="width:120px;margin-right:20px" size="medium" v-model="passwordEditForm.code1"></el-input>
                         <el-button type="info" size="medium" plain @click="getCode" :disabled="code3IsEdit!=0||(!/^1[34578]\d{9}$/.test(passwordEditForm.phone1))">获取验证码<span v-if="code3IsEdit!=0">({{code3IsEdit}})</span></el-button>
                    </el-form-item>
                    <el-form-item label="新密码" prop="password1">
                        <el-input size="medium" v-model="passwordEditForm.password1" type="password"></el-input>
                    </el-form-item>
                    <el-form-item label="确认密码" prop="password2">
                        <el-input size="medium" v-model="passwordEditForm.password2"  type="password"></el-input>
                    </el-form-item>
                </el-form>
            </div>
            <div class="footer" slot="footer">提示：若您的手机无法收到信息，请联系客服021-1111111，人工帮您找回！</div>
        </my-toast>
    </div>
</template>
<script>
import {mapState} from 'vuex'
import rule from '../../assets/js/rule.js'
export default {
    data(){
        var validatePass = (rule, value, callback) => {
            console.log(value.length)
            if (value === '') {
              callback(new Error('请输入密码'));
            }else if(value.length<6||value.length>30){
                callback(new Error('密码长度为6到30位'));
            } else {
              if (this.passwordEditForm.password2 !== '') {
                this.$refs.ruleForm.validateField('password2');
              }
              callback();
            }
        };
        var validatePass2 = (rule, value, callback) => {
            if (value === '') {
              callback(new Error('请再次输入密码'));
            }else if (value !== this.passwordEditForm.password1) {
              callback(new Error('两次输入密码不一致!'));
            } else {
              callback();
            }
        };
        return{
            level:4,
            rules:{
                phone1:[
                    rule.require(),
                    { validator: rule.phone, trigger: 'blur' },
                    
                ],
                code1:rule.require(),
                code2:rule.require(),
            },
            rules2:{
                phone1:[
                    rule.require(),
                    { validator: rule.phone, trigger: 'blur' },
                    
                ],
                code1:rule.require(),
                password1: [
                    { validator: validatePass, trigger: 'blur' }
                ],
                password2: [
                    { validator: validatePass2, trigger: 'blur' }
                ]
            },
            code1IsEdit:0,
            code2IsEdit:0,
            code3IsEdit:0,
            form:{
                phone:18355105163,
                password:'123456',
                emailaddress:'1083440519@qq.com',
                safeNum:0
            },
            phoneEditForm:{
                oldPhone:'',
                phone1:'',
                code1:'',
                code2:''
            },
            phoneEditConfig:{
               title:'修改手机号',
               dialogVisible:false,
               width:'500px',
               btnData:[
                   {
                       type:'primary',
                       method:'submit',
                       name:'修改',

                   }
               ]
            },
            passwordEditForm:{
                phone1:'',
                code1:'',
                password1:'',
                password2:''
            },
            passwordEditConfig:{
               title:'修改密码',
               dialogVisible:false,
               width:'500px',
               btnData:[
                   {
                       type:'primary',
                       method:'submit',
                       name:'修改',

                   }
               ]
            },
        }
    },
    computed:{
        ...mapState({
            ports:'ports'
        })
    },
    methods:{
        handleClose(name){
            this.$refs[name].resetFields()
        },
        phoneEdit(phone){
            this.phoneEditForm={
                oldPhone:phone,
                phone1:'',
                code1:'',
                code2:''
            }
            this.phoneEditConfig.dialogVisible=true
        },
        passwordEdit(){
            this.passwordEditConfig.dialogVisible=true
        },
        getCode1(){
            this.$axios.post(
                this.ports+'/safeSet/sendFormerMess',
                {},
                this
            ).then(res=>{
                if(res.success){
                    this.code1IsEdit=60
                    var time=setInterval(()=>{
                        this.code1IsEdit--
                        if(this.code1IsEdit==0){
                            clearInterval(time)
                        }
                    },1000)
                    this.$message.success('发送成功')
                }
            })
        },
        getCode2(){
            if(this.phoneEditForm.phone1){
                this.$axios.post(
                    this.ports+'/safeSet/sendNewMess',
                    {phone1:this.phoneEditForm.phone1},
                    this
                ).then(res=>{
                    if(res.success){
                        this.code2IsEdit=60
                        var time=setInterval(()=>{
                            this.code2IsEdit--
                            if(this.code2IsEdit==0){
                                clearInterval(time)
                            }
                        },1000)
                        this.$message.success('发送成功')
                    }
                })
            }else{
                this.$message.error('请输入新手机号码')
            }        
        },
        getCode(){
            if(this.passwordEditForm.phone1){
                this.$axios.post(
                    this.ports+'/safeSet/sendMess',
                    {phone1:this.passwordEditForm.phone1},
                    this
                ).then(res=>{
                    if(res.success){
                        this.code3IsEdit=60
                        var time=setInterval(()=>{
                            this.code3IsEdit--
                            if(this.code3IsEdit==0){
                                clearInterval(time)
                            }
                        },1000)
                        this.$message.success('发送成功')
                    }
                })
            }else{
                this.$message.error('请输入新手机号码')
            }
        },
        phoneEditSubmit(){
            this.$refs['ruleForm1'].validate((valid) => {
                if (valid) {
                    var data=Object.assign({},this.phoneEditForm)
                    delete data.oldPhone
                    this.$axios.post(
                        this.ports+'/safeSet/submitMess',
                        data,
                        this
                    ).then(res=>{
                        if(res.success){
                            this.$message.success('操作成功')
                            this.phoneEditConfig.dialogVisible=false
                            this.getMessage()
                        }
                    })
                } else {
                    console.log('error submit!!');
                    return false;
                }
            });
        },
        passwordEditSubmit(){
            this.$refs['ruleForm'].validate((valid) => {
                if (valid) {
                    var data=Object.assign({},this.passwordEditForm)
                    this.$axios.post(
                        this.ports+'/safeSet/updatePw',
                        data,
                        this
                    ).then(res=>{
                        if(res.success){
                            this.$message.success('操作成功')
                            this.passwordEditConfig.dialogVisible=false
                            this.getMessage()
                        }
                    })
                } else {
                    console.log('error submit!!');
                    return false;
                }
            });
        },
        getMessage(){
            this.$axios.post(
                this.ports+'/safeSet/safeMess',
                {},
                this
            ).then(res=>{
                if(res.success){
                    this.form=res.data
                }
            })
        }
    },
    mounted(){
        this.getMessage()
    }
}
</script>

<style lang="less">
    .safety{
        .clearfix:after{
            content: '';
            clear: both;
            display: block;
        }
        padding-left: 100px;
        padding-top: 0;
        .level{
            >div{
                margin-top: 30px;
            }
            .my-label{
                
                font-size:14px;
                font-family:MicrosoftYaHei;
                color: #666;
                padding-left:6px;
            }
            .my-item{
                margin-left: 50px;
                .curent-level{
                    font-size:14px;
                    font-family:MicrosoftYaHei;
                    color: #EC6A1A;
                }
                i{
                    display: inline-block;
                    width:40px;
                    height:8px;
                    background: #E8E6E6;
                    margin-left: 5px;
                    &:nth-of-type(1){
                        margin-left: 30px;
                    }
                    &.isCurrent{
                         background:rgba(236,106,26,1);
                    }
                }
            }
            .my-form{
                .form-label{
                    width: 100px;
                    text-align: center;
                    display: inline-block;
                    font-size:14px;
                    height: 40px;
                    line-height: 40px;
                    font-family:MicrosoftYaHei;
                    color:rgba(102,102,102,1);
                    float: left;
                }
                .form-item{
                    width: 210px;
                    height: 40px;
                    line-height: 40px;
                    display: inline-block;
                    font-size:14px;
                     overflow: hidden;
                    text-overflow: ellipsis;
                    font-family:MicrosoftYaHei;
                    color:rgba(51,51,51,1);
                    margin-left: 10px;
                    position: relative;
                    // top: 5px;
                    margin-right: 10px;
                    float: left;
                }
            }
        }
        .footer{
            height: 52px;
            line-height: 52px;
            text-align: center;
            font-size:12px;
            font-family:MicrosoftYaHei;
            color:rgba(153,153,153,1);
        }
    }
</style>



