<template>
    <div class="information">
        <el-form :model="formData"  ref="ruleForm" label-width="200px" class="demo-ruleForm">
            <el-form-item label-width="130px" class="titlenp">
                <h1 style="margin-left:100px;margin-top:10px">个人认证申请信息<b style="color:#2BD954;font-weight:300;">(审核已通过)</b></h1>
            </el-form-item>
            <el-form-item label="真实姓名" prop="name">
                <span>{{formData.name}}</span>
            </el-form-item>
            <el-form-item label="证件类型" >
                <span>{{formData.certificateTypeDesc}}</span>
            </el-form-item>
            <el-form-item label="证件号码" >
                <span>{{formData.certificate}}</span>
            </el-form-item>
            <el-form-item label="性别" >
                <span>{{formData.sex==1?'男':'女'}}</span>
            </el-form-item>
            <el-form-item label="所在地区" >
                <!-- <area-select :level="2" type="text" v-model="formData.region" :data="pcaa" ></area-select> -->
                <span>{{formData.province}} {{formData.city}} {{formData.district}}</span>
            </el-form-item>
            <el-form-item label="详细地址" >
                <span>{{formData.detailedAddress}}</span>
            </el-form-item>
            <el-form-item label="证件图片" >
                <div style="margin-top:10px;margin-left:28px;">
                    <img :src="formData.certificatePositiveShareUrl" class="avatar">
                    <img :src="formData.certificateNegativeShareUrl" class="avatar">
                </div>
            </el-form-item>
            <el-form-item style="margin-top:50px;" >
                <div style="margin-left:28px;" @click="reAuthentication" class="submit-btn">重新认证</div>
            </el-form-item>
        </el-form>
    </div>
</template>

<script>
import { AreaSelect } from "vue-area-linkage";
import { pca, pcaa } from "area-data";
import rule from '../../assets/js/rule.js'
import {mapState} from 'vuex'
    export default {
         data(){
            return{
                pcaa: pcaa,
                // 上传的图片url
                imageUrl:'',
                // 是否已阅读协议
                isRead:false
            }
        },
        props:['formData'],
        methods:{
            reAuthentication(){
                this.$refs['ruleForm'].validate((valid) => {
                    if (valid) {
                        this.$emit('reAuthentication',this.ssss)
                    } else {
                        console.log('error submit!!');
                        return false;
                    }
                }); 
            }
        },
        computed:{
            ...mapState(['userId','ports','authStatus','authType'])
        },
        mounted(){
            this.$axios.post_noqs(
                this.ports + '/personalAuth/page',
                {userId:this.userId},
                this
            ).then(res=>{
                if(res.success){
                    this.$emit('getDataSuceess',res.data)
                    // this.formData=res.data
                }
            })
        },
        components:{
            AreaSelect: AreaSelect
        }
    }
</script>

<style lang="less">
    .information{
        .submit-btn{
            width:220px;
            height:44px;
            background:rgba(13,111,244,1);
            margin-left: 28px;
            font-size:14px;
            font-family:MicrosoftYaHei;
            color:rgba(255,255,255,1);
            line-height:44px;
            text-align: center;
            cursor: pointer;
        }
        .demo-ruleForm{
            margin-left: 40px;
        }
        .titlenp h1{
            font-size: 24px;
            font-weight: normal;margin-bottom: 24px;
        }
        .area-select .el-select {
            width: 42px;
        }
        .el-input span{
            display: none;
        }
        .el-form-item{
            margin-bottom: 10px;
        }
        .el-form-item__content span{
            margin-left: 28px;
            font-size:14px;
            font-family:MicrosoftYaHei;
            color:rgba(51,51,51,1);
        }
        .el-input--suffix .el-input__inner{
            border: none;
            padding: 0;
            cursor: default;
            border-radius: 0px;
            height: 25px;
            line-height: 42px;
            font-size: 14px;
            color: #333;
            float: left;
            width: 42px;
            margin-top: 5px;
        }
        .avatar {
            width: 240px;
            height: 140px;
            display: inline-block;
        }
        .avatar:nth-child(2){
            margin-left: 18px;
        }
        // .el-form-item__content {
            // width: 524px;
        // }
    }
</style>




