import http from "./public"

// 企业中心
export const listEnterpriseCompany = (data) => {
  return http.fetchPost('/mcatRunDevDistribution/listEnterpriseCompany', data)
}

export const listEnterpriseMcatRunDevDistribution = (data) => {
  return http.fetchPost('/mcatRunDevDistribution/listEnterpriseMcatRunDevDistribution', data)
}
export const listEnterpriseMcatRunDev = (data) => {
  return http.fetchPost('/mcatRunDevDistribution/listEnterpriseMcatRunDev', data)
}
export const companyDo = (data) => {
  return http.fetchPost('/mcatRunDevDistribution/listEnterpriseMcatRundevStateDto', data)
}





