<template>
    <div class="customr-box">
        <div class="search-content clear">
            <div class="right">
                <el-input
                    size="small"
                    placeholder="请输入内容"
                    v-model="keyword">
                    <el-button slot="append" icon="el-icon-search"></el-button>
                </el-input>
            </div>
        </div>
        <el-table
            :data="tableData"
            header-row-class-name="customr-table"
            style="width: 100%">
            <el-table-column
            prop="trade"
            width="100px"
            align="center"
            show-overflow-tooltip
            label="行业">
            </el-table-column>
            <el-table-column
            prop="province"
            width="120px"
            show-overflow-tooltip
            label="省份">
            </el-table-column>
            <el-table-column
            prop="name"
            show-overflow-tooltip
            width="120px"
            label="企业名称">
            </el-table-column>
            <el-table-column
            prop="contactPerson"
            show-overflow-tooltip
            width="70px"
            label="联系人">
            </el-table-column>
            <el-table-column
            prop="contactPhone"
            show-overflow-tooltip
            width="100px"
            label="联系电话">
            </el-table-column>
            <el-table-column
            prop="equipmentScale"
            show-overflow-tooltip
            width="80"
            label="设备总数">
            </el-table-column>
            <el-table-column
            prop="onIcloudScale"
            show-overflow-tooltip
            width="80"
            label="上云设备">
            </el-table-column>
            <el-table-column
            label="操作"
            align="center">
                <template slot-scope="scope">
                    <el-button type="text" size="mini" @click="see(scope.row)">查看</el-button>
                    <el-button type="text" size="mini" @click="audit(scope.row)">通过</el-button>
                    <el-button type="text" size="mini" @click="reject(scope.row)">拒绝</el-button>
                </template>
            </el-table-column>
        </el-table>
        <my-toast :config="detailConfig">
            <el-form :model="formData"  ref="ruleForm" label-width="200px" class="demo-ruleForm" size="small">
                <el-form-item label="企业名称" prop="name">
                    <span>{{formData.name}}</span>
                </el-form-item>
                <el-form-item label="企业性质" >
                    <span>{{formData.propertyDesc}}</span>
                </el-form-item>
                <el-form-item label="所属行业" >
                    <span>{{formData.tradeDesc}}</span>
                </el-form-item>
                <el-form-item label="地址" >
                    <span>{{formData.province}}{{formData.city}}{{formData.district}}</span>
                </el-form-item>
                <el-form-item label="详细地址" >
                    <span>{{formData.detailedAddress}}</span>
                </el-form-item>
                <el-form-item label="联系人" >
                    <span>{{formData.contactPerson}}</span>
                </el-form-item>
                <el-form-item label="联系方式" >
                    <span>{{formData.contactPhone}}</span>
                </el-form-item>
                <el-form-item label="员工规模" >
                    <span>{{formData.employeeScaleDesc}}</span>
                </el-form-item>
                <el-form-item label="设备总数" >
                    <span>{{formData.equipmentScale}}</span>
                </el-form-item>
                <el-form-item label="上云设备数" >
                    <span>{{formData.onIcloudScale}}</span>
                </el-form-item>
                <el-form-item label="企业场所" >
                    <div style="margin-top:10px;" class="avatar">
                        <img :src="formData.siteImageShareUrl" >
                    </div>
                </el-form-item>
            </el-form>
        </my-toast>
    </div>
</template>

<script>
import {mapState} from 'vuex'
  export default {
    data() {
      return {
        keyword:'',
        tableData: [],
        detailConfig:{
            dialogVisible:false,
            width:'500px',
            title:'查看'
        },
        formData:{},
      }
    },
    computed:{
        ...mapState(['ports'])  
    },
    methods: {
        getList(){
            this.$axios.get(  
                this.ports+'/customerManagement/list', 
                {search:this.keyword,status:1,pageNo:1,pageSize:1000000}, 
                this
            ).then(res=>{
                if(res.success){
                    console.log(res)
                    this.tableData=res.data.items
                }
            })
        },
        see(data){
            this.formData=data;
            this.detailConfig.dialogVisible=true
        },
        audit(data){
            this.$confirm('确认受理该客户业务?', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
            type: 'warning'
            }).then(() => {
                this.$axios.get(  
                    this.ports+'/customerManagement/audit', 
                    {id:data.id,status:2}, 
                    this
                ).then(res=>{
                    this.detailConfig.dialogVisible=false;
                    this.getList()
                    this.$message({
                        type: 'success',
                        message: '已确认受理!'
                    });
                })
                
            }).catch(() => {
                    this.$message({
                    type: 'info',
                    message: '已取消操作'
                });            
            });
        },
        reject(data){
            this.$prompt('请输入驳回意见', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
            }).then(({ value }) => {
                this.$axios.get(  
                    this.ports+'/customerManagement/audit', 
                    {id:data.id,status:3,approvalOpinion:value}, 
                    this
                ).then(res=>{
                    this.detailConfig.dialogVisible=false;
                    this.getList()
                    this.$message({
                        type: 'success',
                        message: '已驳回受理!'
                    });
                })
            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: '已取消操作'
                });       
            });
        },
    },
    mounted(){
        this.getList()
    }
  }
</script>
<style lang="less">
    .customr-box{
        .search-content{
            padding:20px 0;
            >div{

            }
        }
        .el-table th, .el-table tr.customr-table{
            background: #F5F5F5;
            th{
                padding: 10px 0;
            }
        }
    }
    
</style>



