		 handleClose(name){
            this.$refs[name].resetFields()
        },
        phoneEdit(phone){
            this.phoneEditForm={
                oldPhone:phone,
                phone1:'',
                code1:'',
                code2:''
            }
            this.phoneEditConfig.dialogVisible=true
        },
        changePass(){
            this.passwordEditConfig.dialogVisible=true
        },
        getCode1(){
            this.$axios.post(
                this.ports+'/safeSet/sendFormerMess',
                {},
                this
            ).then(res=>{
                if(res.success){
                    this.code1IsEdit=60
                    var time=setInterval(()=>{
                        this.code1IsEdit--
                        if(this.code1IsEdit==0){
                            clearInterval(time)
                        }
                    },1000)
                    this.$message.success('发送成功')
                }
            })
        },
        getCode2(){
            if(this.phoneEditForm.phone1){
                let params={phone1: this.phoneEditForm.phone1}
                safeSetSendNewMess({Vue:this,params:params}).then(res=>{
                    // if(res.success){
                        this.code2IsEdit=60
                        var time=setInterval(()=>{
                            this.code2IsEdit--
                            if(this.code2IsEdit==0){
                                clearInterval(time)
                            }
                        },1000)
                        this.$message.success('发送成功')
                    // }
                })
                // console.log('this.phoneEditForm.phone1====>',this.phoneEditForm.phone1);              
                // return;
                // this.$axios.post(
                //     this.ports+'/safeSet/sendNewMess',
                //     {phone1:this.phoneEditForm.phone1},
                //     this
                // ).then(res=>{
                //     if(res.success){
                //         this.code2IsEdit=60
                //         var time=setInterval(()=>{
                //             this.code2IsEdit--
                //             if(this.code2IsEdit==0){
                //                 clearInterval(time)
                //             }
                //         },1000)
                //         this.$message.success('发送成功')
                //     }
                // })
            }else{
                this.$message.error('请输入新手机号码')
            }        
        },
        getCode(){
            if(this.passwordEditForm.phone1){
                safeSetSendMess({Vue:this,params:{phone1:this.passwordEditForm.phone1}}).then(res=>{
                    this.code3IsEdit=60
                    var time=setInterval(()=>{
                        this.code3IsEdit--
                        if(this.code3IsEdit==0){
                            clearInterval(time)
                        }
                    },1000)
                    this.$message.success('发送成功')
                })
                // this.$axios.post(
                //     this.ports+'/safeSet/sendMess',
                //     {phone1:this.passwordEditForm.phone1},
                //     this
                // ).then(res=>{
                //     if(res.success){
                //         this.code3IsEdit=60
                //         var time=setInterval(()=>{
                //             this.code3IsEdit--
                //             if(this.code3IsEdit==0){
                //                 clearInterval(time)
                //             }
                //         },1000)
                //         this.$message.success('发送成功')
                //     }
                // })
            }else{
                this.$message.error('请输入新手机号码')
            }
        },
        phoneEditSubmit(){
            this.$refs['ruleForm1'].validate((valid) => {
                if (valid) {
                    var data=Object.assign({},this.phoneEditForm)
                    delete data.oldPhone
                    safeSetSubmitMess({Vue:this,params:data}).then(res=>{
                        this.$message.success('操作成功')
                        this.phoneEditConfig.dialogVisible=false
                        this.getMessage()
                    })
                    // this.$axios.post(
                    //     this.ports+'/safeSet/submitMess',
                    //     data,
                    //     this
                    // ).then(res=>{
                    //     if(res.success){
                    //         this.$message.success('操作成功')
                    //         this.phoneEditConfig.dialogVisible=false
                    //         this.getMessage()
                    //     }
                    // })
                } else {
                    console.log('error submit!!');
                    return false;
                }
            });
        },
        passwordEditSubmit(){
            this.$refs['ruleForm'].validate((valid) => {
                if (valid) {
                    var data=Object.assign({},this.passwordEditForm)
                    safeSetUpdatePw({Vue:this,params:data}).then(res=>{
                          this.$message.success('操作成功')
                          this.passwordEditConfig.dialogVisible=false
                          this.getMessage()
                    })
                    // this.$axios.post(
                    //     this.ports+'/safeSet/updatePw',
                    //     data,
                    //     this
                    // ).then(res=>{
                    //     if(res.success){
                    //         this.$message.success('操作成功')
                    //         this.passwordEditConfig.dialogVisible=false
                    //         this.getMessage()
                    //     }
                    // })
                } else {
                    console.log('error submit!!');
                    return false;
                }
            });
        },
        getMessage(){
            this.getUserInfo();
            // this.$axios.post(
            //     this.ports+'/safeSet/safeMess',
            //     {},
            //     this
            // ).then(res=>{
            //     if(res.success){
            //         this.form=res.data
            //     }
            // })
        }
	}

}	
export default vueObj;
</script>
<style scoped>
.rz{
	margin-right: 8px;
}
.qw_root2{
	padding-bottom:50px;
	margin-bottom: 50px;
	min-height: 700px;
}
.qw_tab_title_box{
	width: 100%;
	height: 65px;
	border-bottom: 1px solid #E5E5E5;
	box-sizing: border-box;
}	
.qw_tab{
	font-size: 14px;
	line-height: 65px;
	display: inline-block;
	height: 65px;
	color: #333;
	box-sizing: border-box;
	padding: 0 20px;
	text-align: center;
	cursor: pointer;
}
.qw_tab.active{
	border-bottom: 2px solid #0D6FF4;
}
.qw_tab_body{
	padding-top: 40px;
}
.f_info{
	box-sizing: border-box;
	padding:0 10px;
}
.info_block{
	position: relative;
	padding-left: 47px;
}
.info_block .photo_box{
	position: absolute;
	width: 128px;
	height: 158px;
	right: 158px;
	top: 40px;
	background-color: #FFF; 
	text-align: center;
    overflow: hidden;
}
.info_block .zhizhaobox{
  position: absolute;
  box-sizing: border-box;
  width: 200px;
  height: 180px;
  right: 158px;
  top: 40px;
}
.info_block .zhizhaobox .zhizhao{
  display: inline-block;
  width: 200px;
  height: 150px;
  border: 1px solid #E5E5E5;
  box-sizing: border-box;
}
.info_block .zhizhaobox .title{
   color: #333;
   font-size: 14px;
   margin-top: 10px;
   line-height: 20px;
   text-align: center;
   font-weight: 400;
}
.info_block .photo_box .photo{
	width: 128px;
	height: 128px;
	background-color: #EEE;
}
.photo_box a{
	display: inline-block;
	font-size: 14px;
	line-height: 30px;
	color: #333;
	text-decoration: none;
}
.info_block .title{
	font-weight: normal;
	color: #333;
	font-size: 24px;
}
.info_item {
	margin-top: 10px;
}
.info_item label,.info_item span{
	display: inline-block;
	width: 110px;
	color: #666;
	box-sizing: border-box;
	padding-right: 20px;
	text-align: right;
	font-size: 14px;
	line-height: 30px;
}
.info_item  span{
	display: inline-block;
	width: 300px;
	padding-right: 0;
	color: #333;
	text-align: left;
}
.info_item a{
	color: #0D6FF4;
	text-decoration:none;
	font-size: 14px;
}
.info_item .lev{
	display: inline-block;
	width: 20px;
	height: 4px;
	margin-right: 4px;
	-o-transform: translateY(-3px);
	-moz-transform: translateY(-3px);
	-ms-transform: translateY(-3px);
	-webkit-transform: translateY(-3px);
	transform: translateY(-3px);
	background: #D6D6D6; 
}
.info_item .lev:last-child{
	margin-right: 10px;
}
.info_item .lev.blue{
	background:#0D6FF4;
}
.mt_60{
	margin-top: 40px;
}
.zjpic{
	display: inline-block;
  box-sizing: border-box;
  border:1px solid #E5E5E5;
	background: #EEE;
	width: 200px;
	height: 150px;
	margin-right: 10px;
	float: left;
}
.myfooter{
	text-align: left;
	box-sizing: border-box;
	font-size: 10px;
	color: #666;
	margin: 20px 0 40px;
	padding: 0 70px;
}
.user_head{
    display: block;
    box-sizing: border-box;
    width: 128px;
    height: 128px;
    border:1px solid #E5E5E5;
}
.roleItem {
  float:left;
  line-height:10px;
  color:333;
}
</style>


