<template>
    <div class="orderingInfor"> 
        <h2 class="tab_title_box" >
            <span :class="{'select':current=='已有客户'}" @click="marketClick('已有客户')">已有客户</span>
            <span :class="{'select':current=='潜在客户'}" @click="marketClick('潜在客户')">潜在客户</span>
        </h2>
        <div class="syx_content" v-if="current=='已有客户'">
            <has-customer></has-customer>
        </div>
        <div v-else class="agent_box">
            <customer-table></customer-table>
        </div>
    </div>
</template>
<script>
import {mapState} from 'vuex'
import customerTable from './customerTable.vue'
import hasCustomer from './hasCustomer.vue'
export default {
    data(){
        return{
            current:'已有客户'
        }
    },
    computed:{
        ...mapState(['userId','ports','id']),
    },
    methods:{
        marketClick(val){
            this.current=val
        }
    },
    components:{
        customerTable,
        hasCustomer
    },
    mounted(){
    }
}
</script>

<style lang="less">
    .orderingInfor{
        box-sizing:border-box;
        padding-bottom:40px;
        width: 100%;
        min-height: 700px;
        display: flex;
        flex-direction: column;
        .tab_title_box{
            font-size:16px;
            font-family:MicrosoftYaHei-Bold;
            color:rgba(51,51,51,1);
            border-bottom: 1px solid rgba(229,229,229,1);
            height: 60px;
            line-height: 60px;
            >span{
                width: 160px;
                text-align: center;
                line-height:60px;
                font-size:16px;
                font-family:MicrosoftYaHei;
                color:rgba(51,51,51,1);
                display: inline-block;
                height: 60px;
                color:#666;
                font-weight:400;
                cursor: pointer;
                &.select{
                    border-bottom: 2px solid rgba(13,111,244,1);
                }
            }
        }
        .syx_content{
             padding: 0 36px;
        }
        .agent_box{
            padding: 0 36px;
        }
    }
</style>




