<template>
    <div class="certification">
        <div class="right-top" :style="{'border-bottom':authStatus==1?'1px solid #ECECEC':'none','height':authStatus==1?'165px':'auto'}">
            <div class="top-title" v-if="authStatus==1">
                <span>
                    <img src="../../assets/img/green.jpg" alt="">
                    认证中...
                </span>
            </div>
            <div class="top-title" v-else>
                <span>
                    个人认证申请信息<span style="color:red">(审核未通过)</span>
                </span>
            </div>
            <div class="top-text">您的资料已经提交，我们会第一时间为您处理。</div>
        </div>
        <el-form :model="formData"  ref="ruleForm" label-width="300px" class="demo-ruleForm">
            <el-form-item label-width="0px" class="titlenp" v-if="authStatus==1">
                <h1 style="text-align:center">个人认证申请信息</h1>
            </el-form-item>
            <el-form-item label="真实姓名" prop="name">
                <span>{{formData.name}}</span>
            </el-form-item>
            <el-form-item label="证件类型" >
                <span>{{formData.certificateTypeDesc}}</span>
            </el-form-item>
            <el-form-item label="证件号码" >
                <span>{{formData.certificate}}</span>
            </el-form-item>
            <el-form-item label="性别" >
                <span>{{formData.sex==1?'男':'女'}}</span>
            </el-form-item>
            <el-form-item label="所在地区" >
                <!-- <area-select :level="2" type="text" v-model="formData.region" :data="pcaa" ></area-select> -->
                <span>{{formData.province}} {{formData.city}} {{formData.district}}</span>
            </el-form-item>
            <el-form-item label="详细地址" >
                <span>{{formData.detailedAddress}}</span>
            </el-form-item>
            <el-form-item label="证件图片" >
                <div style="margin-top:15px;margin-left:28px;">
                    <img :src="formData.certificatePositiveShareUrl" class="avatar">
                    <img :src="formData.certificateNegativeShareUrl" class="avatar">
                </div>
                
            </el-form-item>
            <el-form-item label="审批意见" v-if="authStatus!=1" style="margin-top:20px;">
                <span style="color:red">{{formData.approvalOpinion}}</span>
            </el-form-item>
            <el-form-item v-if="authStatus==3" style="margin-top:50px;">
                <div style="margin-left:28px;" @click="reAuthentication" class="submit-btn">重新认证</div>
            </el-form-item>
        </el-form>
    </div>
</template>

<script>
    import rule from '../../assets/js/rule.js'
    import {mapState} from 'vuex'
    export default {
         data(){
            return{
                // 上传的图片url
                imageUrl:'',
                // 是否已阅读协议
                isRead:false
            }
        },
        props:['formData'],
        computed:{
            ...mapState(['userId','ports','authStatus','authType'])
        },
        methods:{
            reAuthentication(){
                this.$refs['ruleForm'].validate((valid) => {
                    if (valid) {
                        this.$emit('reAuthentication',this.formData)
                    } else {
                        console.log('error submit!!');
                        return false;
                    }
                }); 
            }
        },
        mounted(){
            this.$axios.post_noqs(
                this.ports + '/personalAuth/page',
                {userId:this.userId},
                this
            ).then(res=>{
                if(res.success){
                    this.$emit('getDataSuceess',res.data)
                    // this.formData=res.data
                }
            })
        },
    }
</script>

<style lang="less">
    #app .my-container .content[data-v-04c2046b]{
        padding: 0 0px 0 20px;
    }
    .certification{
        .submit-btn{
            width:220px;
            height:44px;
            background:rgba(13,111,244,1);
            font-size:14px;
            font-family:MicrosoftYaHei;
            color:rgba(255,255,255,1);
            line-height:44px;
            text-align: center;
            cursor: pointer;
        }
        .el-form-item{
            margin-bottom: 5px;
        }
        .demo-ruleForm{
            margin-left: 40px;
            margin-top: 20px;
        }
        .titlenp h1{
            font-size: 18px;
            font-weight: normal;
            margin:30px 0 20px 0;
        }
        .el-form-item__content{
            span {
                margin-left: 28px;
                font-size:14px;
                font-family:MicrosoftYaHei;
                color:rgba(51,51,51,1);
            }
        }
        .right-top{
            height: 165px;
            // border-bottom: 1px solid #ECECEC;
            .top-title{
                margin: 0 auto;
                padding-top: 38px;
                font-size:24px;
                text-align: center;
                font-family:MicrosoftYaHei;
                position: relative;
                >span{
                    position: absolute;
                    left: 50%;
                    transform: translate(-50%,0);
                    font-weight: 600;
                    img{
                        margin-right:20px;
                        float: left;
                    }
                }
                
            }
            .top-text{
                text-align: center;
                color: #666666;
                margin-top: 60px;
            }
        }
        .avatar {
            width: 240px;
            height: 140px;
            display: inline-block;
        }
        .area-select .el-select {
            width: 42px;
        }
        .el-input span{
            display: none;
        }
        .el-input--suffix .el-input__inner{
            border: none;
            padding: 0;
            cursor: default;
            border-radius: 0px;
            height: 25px;
            line-height: 42px;
            font-size: 14px;
            color: #333;
            float: left;
            width: 42px;
            margin-top: 5px;
        }
    }
</style>



